import React, { ReactNode, useEffect, useState, useRef } from 'react';
import {HashLink} from 'react-router-hash-link';
import p5 from 'p5'
import './ContentCommon.css';
import hljs from 'highlight.js/lib/core';
hljs.registerLanguage('processing', require('highlight.js/lib/languages/processing'));

// === リンク・スクロール系 ===

interface LinkProps {
  // Propsの型定義
  to: string;
  children: ReactNode; // children プロパティを追加
}

/**
 * スクロールリンクのProgramming要ラッパー
 */
export const LinkP: React.FC<LinkProps> = (props) => {
  
  const scrollWithOffset = (el: HTMLElement | null) => {
    if (el) {
      window.scrollTo({
        top: el.getBoundingClientRect().top + window.scrollY,
        behavior: 'smooth',
      });
    }
  };

  return <HashLink className="scroll-link" to={`${props.to}`} smooth={true} scroll={el => scrollWithOffset(el)}>{props.children}</HashLink>;
};

/**
 * スクロールなしでジャンプ 
 */
 export const JumpToDefaultScroll = (afterH: string, location: any) => {

  const fragment = location.hash; // #以下を取得

  // スクロール実行
  if( fragment )
  {
    const [,anchorId] = fragment.split('#');
    console.log(`URL Scroll anchorId: ${anchorId}`);
    jumpToElement(anchorId);
  } else {
    const practiceHId = 'h' + afterH;
    console.log(`URL Scroll practiceId: ${practiceHId}`);
    jumpToElement(practiceHId);
  }
};

const jumpToElement = (elementId: string) => {
  const element = document.getElementById(elementId);

  if (element) {
    window.scrollTo({
      top: element.getBoundingClientRect().top + window.scrollY,
      behavior: 'auto',
    });
  }
};


/**
 * igm（キャプション付き） 
 */
interface ImgProps {
  src: string;
  caption?: string;
}
export const Img: React.FC<ImgProps> = ({ src, caption }) => {

  const fileNameWithExtension = src.split('/').pop(); // ファイル名と拡張子が含まれた文字列を取得
  const fileName = fileNameWithExtension?.split('.').slice(0, -1).join('.'); // 拡張子を除いたファイル名を取得

  return (
    <figure className="figure">
      <img src={src} alt={fileName} />
      {caption && <figcaption>{caption}</figcaption>}
    </figure>
  );
};

