/**
 * .jsからP5を実行 
 * 
 *  folder内のpdeファイルを読み込んでコードブロック表示
 */

const sketchPDEFolder: string = process.env.PUBLIC_URL + '/sketch/';

interface SketchCodeProps
{
  name:string;
}
//export const LinkP: React.FC<LinkProps> = (props) => {
export const SketchCode:  React.FC<SketchCodeProps> = ({name}) => {
  const [p5Instance, setP5Instance] = useState<any>(null);
  const wrapper = useRef<HTMLDivElement>(null);
  const [strPDEFolderPath, setPDEFolderPath] = useState<string>(`${sketchPDEFolder}${name}/`);
  //const [strPDEJSFullPath, setPDEullPath] = useState<string>(`${sketchPDEFolder}${folder}/${folder}.pde`);
  const [pdeFiles, setPdeFiles] = useState<string[]>([]);
  const [selectedFile, setSelectedFile] = useState<string | null>(null);

  // p5.jsのインスタンスを作成
  useEffect(() => {

    // コードブロック
    const fetchPdeFiles = async () => {
      try {
        const response = await fetch(`${strPDEFolderPath}${name}.pde`);
        if (!response.ok) {
          throw new Error('Failed to fetch PDE files');
        }
        const pdeFilesText = await response.text(); // テキストデータとして取得
        //const pdeFiles = pdeFilesText.split('\n'); // テキストを改行で分割して配列にする
        console.log( `pdeFilesText:${pdeFilesText}` );
        setSelectedFile(pdeFilesText);
      } catch (error) {
        console.error('Error fetching PDE files:', error);
      }
    };

    fetchPdeFiles();
    
    // スケッチJSをダイナミックに取得
    const sketch_func = require(`./programming/sketch/${name}`).default;
    if (wrapper.current && !p5Instance )
    {
      setP5Instance(new p5(sketch_func,wrapper.current));
    }
    
    return () => {
      if (p5Instance) {
        p5Instance.remove();
        setP5Instance(null);
      }
    };
  }, [name]);

  return (
    <div>
      <div className="sketch-canvas" ref={wrapper}></div>
      {selectedFile && <CodeBlock>{selectedFile}</CodeBlock>}
    </div>
  );
};


/**
 * CodeBlockタグに挟まれたコンテンツからコードブロックを表示する
 * 
 *  folder内のpdeファイルを読み込んでコードブロック表示
 */
interface CodeBlockProps {
  //code: string;
  children?: React.ReactNode; // childrenプロパティを含める
}
export const CodeBlock: React.FC<CodeBlockProps> = ({ children }) => {
  const [expanded, setExpanded] = useState(false);
  const [copyText, setCopyText] = useState('Copy');
  const [hasExpandText, setHasExpandText] = useState<boolean>(false);

  // コード隠し
  const toggleExpanded = () => {
    setExpanded(!expanded);
  };

  // コードコピー
  const copyToClipboard = async () => {
    try {
      if(typeof children === 'string')
      {
        await navigator.clipboard.writeText(children);
      }
      setCopyText('Copied!');
      setTimeout(() => {
        setCopyText('Copy');
      }, 2000); // 2秒後にテキストを元に戻す
    } catch (error) {
      console.error('Failed to copy:', error);
    }
  };

  function logCharCode(text: string) {
    for (let i = 0; i < text.length; i++) {
        console.log(`Character "${text[i]}" has Unicode code point ${text.charCodeAt(i)}`);
    }
  }

  // 改行コード（\n）のみをカウントする
  function countNewLines(text: string): number {
    const match = text.match(/\n/g);
    return match ? match.length : 0;
  }

  // 3回以上の改行があるかどうかを判定する
  function hasThreeOrMoreNewLines(text: string): boolean {
    return countNewLines(text) >= 3;
  }

  // Processing言語のハイライトを適用するためのスタイル
  const codeStyle = {
    fontFamily: 'Consolas, monospace', // 使用するフォント
    fontSize: '16px', // フォントサイズ
  };

  useEffect(() => {
    // コードブロックの中身をハイライトする
    if (typeof children === 'string') {
      // キャリッジリターン（\r）削除
      children = children.replace(/\r/g, '');
      setHasExpandText(hasThreeOrMoreNewLines(children.toString()));
    }
    hljs.highlightAll();
  }, [children]);

  return (
    <div className={`code-block ${expanded || !hasExpandText ? '' : 'collapsed'}`} style={{ maxHeight: expanded ? 'none' : '80px' }}>
      <div className="code-buttons">
        { hasExpandText && <button className="code-toggle-button" onClick={toggleExpanded}>{expanded ? 'Show less' : 'Show more'}</button>}
        { children && typeof children === 'string' && <button className="copy-button" onClick={copyToClipboard}>{copyText}</button>}
      </div>
      <pre>
        <code className="hljs" style={codeStyle}>
          {children}
        </code>
      </pre>
    </div>
  );
};

