// ContentFrame.tsx

import React, { useState, useEffect } from 'react';
import './ContentFrame.css';

interface ContentFrameProps {
  LessonPrefix: string;
  param?: string;
}

interface ContentProps {
  practiceId: string;
}


const ContentFrame: React.FC<ContentFrameProps> = ({ LessonPrefix, param }) => {
  const[lessonPrefixCur, setLessonPrefixCur] = useState<string>();
  const[paramCur, setparamCur] = useState<string>();
  const[sectionId, setSectionId] = useState<string>();
  const [DynamicComponentCur, setDynamicComponentCur] = useState<React.FC<ContentProps> | null>(null);

  let lessonId: string ='';
  let practiceId: string ='';

  if (param !== undefined) {

    [lessonId, practiceId] = param.split('-');

    console.log(`param in ContentFrame: ${param}`);
    //console.log(`practiceId, anchorId in ContentFrame: ${practiceId}, ${anchorId}`);
  }

  // LessonIndex を最大2桁の数字として生成
  const formattedLessonId = lessonId.padStart(2, '0');
  //const formattedPracticeId = practiceId.toString().padStart(2, '0');

  // コンポーネントの名前を生成
  const componentName = `${LessonPrefix}-${formattedLessonId}`;

  console.log(`componentName in ContentFrame: ${componentName}`);

  // 生成されたコンポーネントをダイナミックに取得
  const DynamicComponent = require(`./programming/${componentName}`).default;

  //setDynamicComponentCur(DynamicComponent);
  //setSectionId();
  //setparamCur();

  return (
    <div className="content-frame">
        {DynamicComponent && <DynamicComponent practiceId={practiceId} />}
    </div>
  );
};

export default ContentFrame;
