// Programming04.tsx

import React, { useEffect} from 'react';
import { useLocation } from 'react-router-dom';
import { LinkP, JumpToDefaultScroll, SketchCode, CodeBlock } from '../ContentCommon';
import { MathJaxProvider, MathJaxFormula } from 'mathjax3-react';
import '../ContentCommon.css';

const imageFolder: string = "/musabi-react/images/programming/";

interface ContentProps {
  practiceId: string;
}

const Programming04: React.FC<ContentProps> = ({ practiceId }) => {

  /* URLから初期スクロール位置 */
  const location = useLocation();
  useEffect(() => {
    JumpToDefaultScroll(practiceId, location as any );    
  }, [practiceId,location]);
  return (
    <div className="common-style">
      <h1 id='h0'>第４回：制御文：繰り返し</h1>
      <hr color='#8dacf9' />
        <h2 id='for文の基本１'>for文の基本１</h2>
        <h3 id='最初のサンプル-1'>最初のサンプル</h3>
        <p>スクリーン右端まで正方形を並べて描画する．</p>
        <img src={`${imageFolder}var_add_squares.png`} alt="var_add_squares" />
        <h4 id='前回までのプログラム'>前回までのプログラム</h4>
        <CodeBlock>{`size( 400, 100 );\n\nint iX = 0;\n\nsquare( iX, 25, 50 );\niX += 50;\n\nsquare( iX, 25, 50 );\niX += 50;\n\nsquare( iX, 25, 50 );\niX += 50;\n\nsquare( iX, 25, 50 );\niX += 50;\nsquare( iX, 25, 50 );\niX += 50;\n\nsquare( iX, 25, 50 );\niX += 50;\n\nsquare( iX, 25, 50 );\niX += 50;\n\nsquare( iX, 25, 50 );\niX += 50;`}</CodeBlock>
        <h4 id='for文を使ったプログラム'>for文を使ったプログラム</h4>
        <CodeBlock>{`size( 400, 100 );\n\nfor( int iX = 0; iX < 400; iX += 50 )\n{\n  square( iX, 25, 50 );\n}`}</CodeBlock>
        <h3 id='for文とは'>for文とは</h3>
        <ul>
        <li><p>繰り返しを制御する構文．</p></li>
        <li><p>for文は４つの部分で構成される．（下図の破線で囲まれた箇所）</p></li>

        </ul>
        <img src={`${imageFolder}for_koubun.jpg`} alt="for_koubun" />
        <h3 id='条件式とは-1'>条件式とは</h3>
        <img src={`${imageFolder}condition_exp.jpg`} alt="condition_exp" />
        <ul>
        <li><p>プログラムの実行の流れを決める式．</p></li>
        <li><p>二つの値や変数を比較し，計算結果として真(<strong>true</strong>)か偽(<strong>false</strong>)のどちらかを返す．</p></li>

        </ul>
        <h3 id='比較演算子'>比較演算子</h3>
        <ul>
        <li><p>条件式に用いる演算子を比較演算子という．</p></li>

        </ul>
        <h3 id='構文-56'>構文</h3>
        <ul>
        <li><p>下の表に比較演算子及び構文を記載する．</p></li>
        <li><p><code>a</code>または<code>b</code>の箇所に比較に用いる数値や変数を記述する．</p></li>

        </ul>
        <figure><table>
        <thead>
        <tr><th>条件式</th><th>比較演算子</th><th>真(true)を返す条件</th></tr></thead>
        <tbody><tr><td><code>{`a < b`}</code></td><td><code>{`<`}</code></td><td>aがbより小さい</td></tr><tr><td><code>{`a <= b`}</code></td><td><code>{`<=`}</code></td><td>aがb以下</td></tr><tr><td><code>{`a > b`}</code></td><td><code>{`>`}</code></td><td>aがbより大きい</td></tr><tr><td><code>{`a >= b`}</code></td><td><code>{`>=`}</code></td><td>aがbと等しいか大きい</td></tr><tr><td><code>a == b</code></td><td><code>==</code></td><td>aとbが等しい</td></tr><tr><td><code>a != b</code></td><td><code>!=</code></td><td>aがbが等しくない</td></tr></tbody>
        </table></figure>
        <h3 id='for文の繰り返し実行順序'>for文の繰り返し実行順序</h3>
        <ul>
        <li><p>for文の４つの部分は実行される順序がある．</p></li>
        <li><p>例えば，３つ目の正方形が描画されるまでの流れは下のGIFのようになる．</p></li>

        </ul>
        <img src={`${imageFolder}for_flow_true.gif`} alt="for_flow_true" />
        <h3 id='for文の変数を文に記述する-1'>for文の変数を文に記述する．</h3>
        <p>繰り返しを図形にも反映させるため，for文の変数は繰り返す文の中にも記述する．</p>
        <img src={`${imageFolder}for_variable_incommand.jpg`} alt="for_variable_incommand" />
        <p><a href='https://processing.org/reference/for.html' target='_blank' >https://processing.org/reference/for.html</a></p>
        <h3 id='例1-17'>例1</h3>
        <p>スクリーン右端まで幅50の矩形を並べて描画する．</p>
        <img src={`${imageFolder}for_rects8.png`} alt="for_rects8" />
        <CodeBlock>{`size( 400, 100 );\n\nfor( int iX = 0; iX < 400; iX += 50 )\n{\n  rect( iX, 10, 50, 80 );\n}`}</CodeBlock>
        <h3 id='例2-17'>例2</h3>
        <p>スクリーン右端まで幅20の矩形を並べて描画する．</p>
        <img src={`${imageFolder}for_rects20.png`} alt="for_rects20" />
        <CodeBlock>{`size( 400, 100 );\n\nfor( int iX = 0; iX < 400; iX += 20 )\n{\n  rect( iX, 10, 20, 80 );\n}`}</CodeBlock>
        <h3 id='例3-10'>例3</h3>
        <p>スクリーン下端まで矩形を並べて描画する．
        変数名を<code>iY</code>に変更している．</p>
        <img src={`${imageFolder}for_rects8_column.png`} alt="for_rects8_column" />
        <CodeBlock>{`size( 100, 400 );\n\nfor( int iY = 0; iY < 400; iY += 50 )\n{\n  rect( 0, iY, 100, 50 );\n}`}</CodeBlock>
        <h3 id='例4-3'>例4</h3>
        <p>ストライプ状に縦線を描画する．</p>
        <img src={`${imageFolder}for_lines.png`} alt="for_lines" />
        <CodeBlock>{`size( 200, 200 );\n\nstrokeWeight( 4 );\n\nfor( int iX = 0; iX < 200; iX += 20 )\n{\n  line( iX, 0, iX, 200 );\n}`}</CodeBlock>
        <h2 id='h1'>演習１</h2>
        <p>「大きさ40の正方形を右端まで並べるプログラム」をfor文を使ったプログラムに書き換える．</p>
        <img src={`${imageFolder}for_squares10.png`} alt="for_squares10" />
        <ol>
        <li><p>下のプログラムをコピーする．</p>
        <p>for文を使わないプログラム</p>
        <CodeBlock>{`size( 400, 100 );\n\nint iX = 0;\n\nsquare( iX, 30, 40 );\niX += 40;\n\nsquare( iX, 30, 40 );\niX += 40;\n\nsquare( iX, 30, 40 );\niX += 40;\n\nsquare( iX, 30, 40 );\niX += 40;\n\nsquare( iX, 30, 40 );\niX += 40;\n\nsquare( iX, 30, 40 );\niX += 40;\n\nsquare( iX, 30, 40 );\niX += 40;\n\nsquare( iX, 30, 40 );\niX += 40;\n\nsquare( iX, 30, 40 );\niX += 40;\n\nsquare( iX, 30, 40 );\niX += 40;`}</CodeBlock>
        </li>
        <li><p>プログラムをfor文を使ったものに書き換える．</p>
        <p>最終的には，元プログラム３行目の<code>int iX = 0;</code>は削除しなければエラーになる．</p>
        </li>
        <li><p>以下，完成プログラム</p>
        <CodeBlock>{`size( 400, 100 );\n\nfor ( int iX = 0; iX < 400; iX += 40 )\n{\n  square( iX, 30, 40 );\n}`}</CodeBlock>
        </li>

        </ol>
        <h2 id='h2'>for文の基本２</h2>
        <h3 id='二重定義）エラー'>二重（定義）エラー</h3>
        <p>for文の変数と同じ名前の変数を前に記述するとエラーとなる．</p>
        <img src={`${imageFolder}duplicate_variable_error.jpg`} alt="duplicate_variable_error" />
        <h3 id='ブロック-1'>ブロック</h3>
        <ul>
        <li><p>プログラムにおける複数の文のまとまりのこと．</p>
        </li>
        <li><p>「繰り返す命令文」を記述する箇所はブロック構造となっている．</p>
        <img src={`${imageFolder}for_block.jpg`} alt="for_block" />
        </li>

        </ul>
        <h4 id='ブロック構造'>ブロック構造</h4>
        <ul>
        <li><p>ブロックには複数の文を記述できる．</p>
        </li>
        <li><p>ブロック内の文は以下のいずれかの方法でインデント（字下げ）を行う．</p>
        <ul>
        <li><p><code>Tab</code>キー</p></li>
        <li><p>半角スペース2マス</p></li>
        <li><p>自動フォーマット機能</p></li>

        </ul>
        </li>

        </ul>
        <img src={`${imageFolder}for_block_indent.jpg`} alt="for_block_indent" />
        <h3 id='自動フォーマット機能'>自動フォーマット機能</h3>
        <p>Processingにはボタンを１度押すだけでプログラムを自動で整形する機能がある．</p>
        <p>{`編集 > 自動フォーマット`}</p>
        <p>ショートカットキー：<code>Ctrl + T</code></p>
        <img src={`${imageFolder}auto_format.jpg`} alt="auto_format" />
        <img src={`${imageFolder}auto_format_code.jpg`} alt="auto_format_code" />
        <h3 id='例-26'>例</h3>
        <p>大きい円→小さい円と描画するため，
        for文の変数の値を400→10へ20ずつ減らしていく例</p>
        <img src={`${imageFolder}for_circles_rev.png`} alt="for_circles_rev" />
        <CodeBlock>{`size( 400, 400 );\n\nfor ( int iSize = 400; iSize > 10; iSize -= 20 )\n{\n  fill( random( 255 ) );\n  circle( 200, 200, iSize );\n}`}</CodeBlock>
        <h2 id='h3'>演習２</h2>
        <p>「剰余算を使った白黒ストライプのプログラム」をfor文を使ったプログラムに書き換える．</p>
        <img src={`${imageFolder}var_squares_shirokuro.png`} alt="var_squares_shirokuro" />
        <ol>
        <li><p>下のプログラムをコピーする．</p>
        <p>for文を使わないプログラム</p>
        <CodeBlock>{`size( 400, 100 );\n\nint iX = 0;\n\nfill( ( iX % 100 ) * 5 );\nsquare( iX, 25, 50 );\niX += 50;\n\nfill( ( iX % 100 ) * 5 );\nsquare( iX, 25, 50 );\niX += 50;\n\nfill( ( iX % 100 ) * 5 );\nsquare( iX, 25, 50 );\niX += 50;\n\nfill( ( iX % 100 ) * 5 );\nsquare( iX, 25, 50 );\niX += 50;\n\nfill( ( iX % 100 ) * 5 );\nsquare( iX, 25, 50 );\niX += 50;\n\nfill( ( iX % 100 ) * 5 );\nsquare( iX, 25, 50 );\niX += 50;\n\nfill( ( iX % 100 ) * 5 );\nsquare( iX, 25, 50 );\niX += 50;\n\nfill( ( iX % 100 ) * 5 );\nsquare( iX, 25, 50 );\niX += 50;`}</CodeBlock>
        </li>
        <li><p>プログラムをfor文を使ったものに書き換える．</p>
        <p>最終的には，元プログラム３行目の<code>int iX = 0;</code>は削除しなければエラーになる．</p>
        </li>
        <li><p>以下，完成プログラム</p>
        <CodeBlock>{`size( 400, 100 );\n\nfor ( int iX = 0; iX < 400; iX += 50 )\n{\n  fill( ( iX % 100 ) * 5 );\n  square( iX, 25, 50 );\n}`}</CodeBlock>
        </li>

        </ol>
        <h2 id='h4'>決められた回数繰り返すfor文による表現</h2>
        <h3 id='繰り返す回数を指定するfor文'>繰り返す回数を指定するfor文</h3>
        <p>これまでの正方形を並べるfor文は，「８回繰り返す」というfor文に書き変えることもできる．</p>
        <p>下のプログラムのようになる．</p>
        <img src={`${imageFolder}var_add_squares.png`} alt="var_add_squares" />
        <p>&nbsp;</p>
        <CodeBlock>{`size( 400, 100 );\n\nfor( int i = 0; i < 8; i++ )\n{\n  square( i * 50, 25, 50 );\n}`}</CodeBlock>
        <h4 id='繰り返す回数'>繰り返す回数</h4>
        <p>条件式の右辺に記述する．</p>
        <img src={`${imageFolder}for_no_kaisuu.jpg`} alt="for_no_kaisuu" />
        <h4 id='更新で１ずつ増やす'>更新で１ずつ増やす．</h4>
        <ul>
        <li><p>更新により変数の値を1だけ増やす．</p></li>
        <li><p>変数<code>i</code>の値が0→<strong>7</strong>まで計８回繰り返される．</p></li>

        </ul>
        <img src={`${imageFolder}for_no_1add.jpg`} alt="for_no_1add" />
        <h3 id='インクリメント演算子'>インクリメント演算子</h3>
        <p><strong><code>++</code></strong></p>
        <h4 id='構文-57'>構文</h4>
        <p><code>++</code>変数  もしくは  変数<code>++</code></p>
        <h4 id='説明-45'>説明</h4>
        <ul>
        <li><p>変数の値を<strong>1だけ増やす</strong>操作に用いる．</p>
        </li>
        <li><p>前置・後置という構文があるが，本授業ではどちらを使っても構わない．</p>
        </li>
        <li><p>これらは，基本的には以下と同じ結果が得られる.</p>
        <p><code>i += 1;</code></p>
        </li>

        </ul>
        <p><a href='https://processing.org/reference/increment.html' target='_blank' >https://processing.org/reference/increment.html</a></p>
        <h3 id='デクリメント演算子'>デクリメント演算子</h3>
        <p> <strong><code>--</code></strong></p>
        <h4 id='構文-58'>構文</h4>
        <p><code>--</code>変数  もしくは  変数<code>--</code></p>
        <h4 id='説明-46'>説明</h4>
        <ul>
        <li><p>変数の値を<strong>1だけ減らす</strong>操作に用いる．</p>
        </li>
        <li><p>これらは，基本的には以下と同じ結果が得られる.</p>
        <p><code>i -= 1;</code></p>
        </li>

        </ul>
        <p><a href='https://processing.org/reference/decrement.html' target='_blank' >https://processing.org/reference/decrement.html</a></p>
        <h3 id='例１-8'>例１</h3>
        <p>20個の円をランダムな位置に描画する．</p>
        <img src={`${imageFolder}for_no_random_circles.png`} alt="for_no_random_circles" />
        <CodeBlock>{`size( 300, 300 );\n\nfor( int i = 0; i < 20; i++ )\n{\n  circle( random( 300 ), random( 300 ), 40 );\n}`}</CodeBlock>
        <h3 id='例２-8'>例２</h3>
        <p>200個のランダムな正方形を減算で描画する．</p>
        <img src={`${imageFolder}for_squares_sub.png`} alt="for_squares_sub" />
        <CodeBlock>{`size( 300, 300 );\n\nblendMode( SUBTRACT );	// 減算\n\nfor( int i = 0; i < 200; i++ )\n{\n  fill( 100 );\n  square( random( 300 ), random( 300 ), random( 20, 30 ) );\n}`}</CodeBlock>
        <h3 id='例３-2'>例３</h3>
        <p>20本のランダムな線を除外で描画する．</p>
        <img src={`${imageFolder}for_lines_exc.png`} alt="for_lines_exc" />
        <CodeBlock>{`size( 300, 300 );\n\nblendMode( EXCLUSION );  // 除外\n\nstrokeWeight( 3 );\nstroke( 255 );\n\nfor( int i = 0; i < 20; i++ )\n{\n  line( random( 0, 20 ), random( 0, 300 ), random( 280, 300 ), random( 0, 300 ) );\n}`}</CodeBlock>
        <h2 id='h5'>演習３</h2>
        <p>任意のブレンドモードを指定し，100個以上の任意の図形をランダムに描画するプログラムを自由に作成する．</p>
        <p>下のサンプルをベースに進めてもよい．</p>
        <h3 id='サンプル'>サンプル</h3>
        <p>ブレンドモード：ブレンド（デフォルト）で100個の正円を描画する．</p>
        <img src={`${imageFolder}for_no_100circles_plain.png`} alt="for_no_100circles_plain" />
        <CodeBlock>{`size( 300, 300 );\n\nblendMode( BLEND );\n\nfor( int i = 0; i < 100; i++ )\n{\n  circle( random( 300 ), random( 300 ), 20 );\n}`}</CodeBlock>
        <h3 id='作例-5'>作例</h3>
        <p>ブレンドモード：差の絶対値で100個のランダムな正円を描画する．</p>
        <img src={`${imageFolder}for_no_100circles_blend.png`} alt="for_no_100circles_blend" />
        <CodeBlock>{`size( 300, 300 );\n\nblendMode( DIFFERENCE );\n\nfor( int i = 0; i < 100; i++ )\n{\n  fill( 200 );\n  circle( random( 300 ), random( 300 ), random( 20, 200 ) );\n}`}</CodeBlock>
        <h2 id='h6'>for文と座標変換を使った表現</h2>
        <p>特に<code>pushMatrix()</code>，<code>popMatrix()</code>を使う場合は慣れが必要となる．</p>
        <h3 id='例１-9'>例１</h3>
        <p>座標変換を使用して正方形を８つ並べる．</p>
        <img src={`${imageFolder}var_add_squares.png`} alt="var_add_squares" />
        <CodeBlock>{`size( 400, 100 );\n\ntranslate( 0, 25 );\n\nfor ( int iSquare = 0; iSquare < 8; iSquare++ )\n{\n  square( 0, 0, 50 );\n  translate( 50, 0 );\n}`}</CodeBlock>
        <h3 id='例２-9'>例２</h3>
        <p>円周上に正方形を１２個並べる．</p>
        <p>参考：<a href="#rectmode">矩形の描画方法を指定する．</a></p>
        <img src={`${imageFolder}for_squares_enshuu.png`} alt="for_squares_enshuu" />
        <CodeBlock>{`size( 400, 400 );\n\nrectMode( CENTER );\ntranslate( 200, 200 );\n\n// 30度ずつ回転させて12回繰り返す．\nfor ( int iSquare = 0; iSquare < 12; iSquare++ )\n{\n  rotate( radians( 30 ) );\n  square( 100, 0, 42 );\n}`}</CodeBlock>
        <h3 id='例３-3'>例３</h3>
        <p>回転させた正方形を水平に並べる．</p>
        <p>座標系の保存を使用する必要があるため難易度が上がる．</p>
        <img src={`${imageFolder}for_squares_rot.png`} alt="for_squares_rot" />
        <CodeBlock>{`size( 400, 100 );\n\nrectMode( CENTER );\ntranslate( 0, 50 );\n\nfor ( int iX = 25; iX < 400; iX += 50 )\n{\n  pushMatrix();\n  \n  translate( iX, 0 );\n  rotate( radians( iX ) );\n  square( 0, 0, 40 );\n  \n  popMatrix();\n}`}</CodeBlock>
        <h3 id='例４-2'>例４</h3>
        <p>5枚の花弁をもつ花の模様</p>
        <img src={`${imageFolder}5petals_flower.png`} alt="5petals_flower" />
        <CodeBlock>{`size( 400, 400 );\n\nstroke( 204 );\nstrokeWeight( 4 );\n\ntranslate( 200, 200 );\n\nrotate( radians( -18 ) );\n\nfor( int i = 0; i < 5; i++ )\n{\n  ellipse( 95, 0, 160, 50 ); \n  rotate( radians( 72 ) );\n}\n\nellipse( 0, 0, 40, 40 );`}</CodeBlock>
        <h3 id='例５-1'>例５</h3>
        <p>256個の正円を使った螺旋状の表現</p>
        <img src={`${imageFolder}for_rasen.png`} alt="for_rasen" />
        <CodeBlock>{`size( 400, 400 );\n\ntranslate( 200, 200 );\n\nfor( int iCircle = 0; iCircle < 256; iCircle++ )\n{\n  fill( iCircle );\n  circle( iCircle + 10, 0, sqrt( iCircle*2 ) );\n  \n  rotate( radians( 6 ) );\n}`}</CodeBlock>
        <h2 id='h7'>演習４</h2>
        <p>下のサンプルを元に，回転対称の表現を自由に記述する．</p>
        <ul>
        <li><p>数値を一部変更する．</p></li>
        <li><p>色やブレンドモードを指定する．</p></li>

        </ul>
        <p>長方形を60°ずつ回転させながら描画した回転対称の表現</p>
        <img src={`${imageFolder}for_rects_rot.png`} alt="for_rects_rot" />
        <CodeBlock>{`size( 400, 400 );\n\ntranslate( 200, 200 );\n\nfor( int iDeg = 0; iDeg < 360; iDeg += 60 )\n{\n  pushMatrix();\n  \n  rotate( radians( iDeg ) );\n  rect( 0, 0, 60, 120 );\n  \n  popMatrix();\n}`}</CodeBlock>
        <p>以下作例</p>
        <img src={`${imageFolder}for_rects_rot_diff.png`} alt="for_rects_rot_diff" />
        <CodeBlock>{`size( 400, 400 );\n\nblendMode( DIFFERENCE );  // 差の絶対値\n\ntranslate( 200, 200 );\n\nfor( int iDeg = 0; iDeg < 360; iDeg += 40 )\n{\n  pushMatrix();\n  \n  fill( 200 );\n  rotate( radians( iDeg ) );\n  rect( 0, -36, 140, 180 );\n  \n  popMatrix();\n}`}</CodeBlock>
        <h2 id='h8'>変数を多用した多角形の表現</h2>
        <p>乱数の値を保存する変数を使った連続した線の描画</p>
        <img src={`${imageFolder}for_var_lines.png`} alt="for_var_lines" />
        <CodeBlock>{`size( 600, 200 );\n\nfloat fStartY = 100;	// 開始点のY座標値\n\ncolorMode( RGB, 600 );\nstrokeWeight( 2 );\n\nfor( int iX = 0; iX < 600; iX += 4 )\n{\n  float fEndY = random( 200 );	// 描画する線の終点をランダムに決める．\n  \n  stroke( iX );\n  strokeWeight( random( 1, 4 ) );\n  line( iX, fStartY, iX + 4, fEndY );\n  \n  fStartY = fEndY;	// 描画した線の終点を次の線の開始点にする．\n}`}</CodeBlock>
        <h3 id='for文のブロック外で定義した変数'>for文のブロック外で定義した変数．</h3>
        <p>変数が使える範囲を変数のスコープという．</p>
        <img src={`${imageFolder}var_scope.jpg`} alt="var_scope" />
        <h3 id='for文の初期化で定義した変数'>for文の初期化で定義した変数．</h3>
        <img src={`${imageFolder}for_countvar_scope.jpg`} alt="for_countvar_scope" />
        <h3 id='for文のブロック内で定義した変数'>for文のブロック内で定義した変数．</h3>
        <img src={`${imageFolder}for_var_scope.jpg`} alt="for_var_scope" />
        <h3 id='例１-10'>例１</h3>
        <p>五角形を描画する．</p>
        <p>多角形描画<code>vertex()</code>は座標変換を使えないため，<code>sin()</code>と<code>cos()</code>を用いて頂点の計算を行う．
        数学的に理解する必要は無いのでテンプレートとして使えればよい．</p>
        <img src={`${imageFolder}for_var_5quad.png`} alt="for_var_5quad" />
        <CodeBlock>{`size( 300, 300 );\n\nfloat fRadius = 100;  // 中心から頂点への距離\n\ntranslate( 150, 150 );\n\nbeginShape();\nfor( int iDeg = -18; iDeg < 360; iDeg += 72 )\n{\n  float fX = fRadius * cos( radians( iDeg ) );\n  float fY = fRadius * sin( radians( iDeg ) );\n  \n  vertex( fX, fY );\n}\nendShape( CLOSE );`}</CodeBlock>
        <h3 id='例２-10'>例２</h3>
        <p>六角形を描画する．</p>
        <p>例１の数値を一部変更するだけでできる．</p>
        <img src={`${imageFolder}for_var_6quad.png`} alt="for_var_6quad" />
        <CodeBlock>{`size( 300, 300 );\n\nfloat fRadius = 100;  // 中心から頂点への距離\n\ntranslate( 150, 150 );\n\nbeginShape();\nfor( int iDeg = 0; iDeg < 360; iDeg += 60 )\n{\n  float fX = fRadius * cos( radians( iDeg ) );\n  float fY = fRadius * sin( radians( iDeg ) );\n  \n  vertex( fX, fY );\n}\nendShape( CLOSE );`}</CodeBlock>
        <h3 id='例３-4'>例３</h3>
        <p>凹凸のある多角形の描画</p>
        <img src={`${imageFolder}for_var_uni.png`} alt="for_var_uni" />
        <CodeBlock>{`size( 300, 300 );\n\nfloat fRadiusS = 40;  // 中心から頂点への距離（凹）\nfloat fRadiusL = 100; // 中心から頂点への距離（凸）\n\ntranslate( 150, 150 );\n\nbeginShape();\nfor( int iDeg = 0; iDeg < 360; iDeg += 20 )\n{\n  // 凹の頂点\n  float fSX = fRadiusS * cos( radians( iDeg ) );\n  float fSY = fRadiusS * sin( radians( iDeg ) );\n  \n  // 凸の頂点\n  float fLX = fRadiusL * cos( radians( iDeg + 10 ) );	// 凹→凸へ10°進める\n  float fLY = fRadiusL * sin( radians( iDeg + 10 ) );\n  \n  vertex( fSX, fSY );\n  vertex( fLX, fLY );\n}\nendShape( CLOSE );`}</CodeBlock>
        <h2 id='h9'>演習５</h2>
        <p>例３のプログラムを元に，下図のような星型の描画のプログラムを作る．</p>
        <p>一部の数値を変更するだけでできる．</p>
        <img src={`${imageFolder}for_var_star.png`} alt="for_var_star" />
        <p>以下，完成プログラム</p>
        <CodeBlock>{`size( 300, 300 );\n\nfloat fRadiusS = 40;  // 中心から頂点への距離（凹）\nfloat fRadiusL = 100; // 中心から頂点への距離（凸）\n\ntranslate( 150, 150 );\n\nbeginShape();\nfor( int iDeg = 18; iDeg < 360; iDeg += 72 )\n{\n  // 凹の頂点\n  float fSX = fRadiusS * cos( radians( iDeg ) );\n  float fSY = fRadiusS * sin( radians( iDeg ) );\n  \n  // 凸の頂点\n  float fLX = fRadiusL * cos( radians( iDeg + 36 ) );\n  float fLY = fRadiusL * sin( radians( iDeg + 36 ) );\n  \n  vertex( fSX, fSY );\n  vertex( fLX, fLY );\n}\nendShape( CLOSE );`}</CodeBlock>
        <h2 id='h10'>for文のネストを使った表現</h2>
        <p>正方形をスクリーン全体に並べて描画する．</p>
        <img src={`${imageFolder}for_nest_grid.png`} alt="for_nest_grid" />
        <CodeBlock>{`size( 400, 400 );\n\nfor ( int iY = 0; iY < 400; iY += 50 )\n{\n  for ( int iX = 0; iX < 400; iX += 50 )\n  {\n    square( iX, iY, 50 );\n  }
        }`}</CodeBlock>
        <h3 id='for文のネスト入れ子構造'>for文のネスト入れ子構造</h3>
        <p>for文の中にさらにfor文を記述することを，for文のネストという．</p>
        <h3 id='for文のネストの実行順序'>for文のネストの実行順序</h3>
        <ul>
        <li><p>for文の入れ子構造は先に内側のfor文が実行される．</p></li>
        <li><p>例えば，３行目の正方形が描画されるまでの流れは下のGIFのようになる．</p></li>

        </ul>
        <img src={`${imageFolder}for_nest_grid.gif`} alt="for_nest_grid" />
        <h3 id='for文の変数を文に記述する-2'>for文の変数を文に記述する．</h3>
        <p>ネストの分変数が増えるので，繰り返す文の中に２つの変数を記述できる．</p>
        <img src={`${imageFolder}for_nest_variables.jpg`} alt="for_nest_variables" />
        <h3 id='例１-11'>例１</h3>
        <p>大きさ40の正方形をスクリーン全体に並べて描画する．</p>
        <img src={`${imageFolder}for_nest_squares.png`} alt="for_nest_squares" />
        <CodeBlock>{`size( 400, 400 );\n\nfor ( int iY = 5; iY < 400; iY += 50 )\n{\n  for ( int iX = 5; iX < 400; iX += 50 )\n  {\n    square( iX, iY, 40 );\n  }
        }`}</CodeBlock>
        <h3 id='例２-11'>例２</h3>
        <p>例１を元に変数<code>iX</code>の値を彩度，変数<code>iY</code>の値を輝度に使用した例．</p>
        <img src={`${imageFolder}for_nest_squares_hsb.png`} alt="for_nest_squares_hsb" />
        <CodeBlock>{`size( 400, 400 );\n\ncolorMode( HSB, 400 );\n\nfor ( int iY = 5; iY < 400; iY += 50 )\n{\n  for ( int iX = 5; iX < 400; iX += 50 )\n  {\n    fill( 0, iX, iY );\n    square( iX, iY, 40 );\n  }\n}`}</CodeBlock>
        <h3 id='例３-5'>例３</h3>
        <p>例２を元に点（ドット）を描画する例．</p>
        <img src={`${imageFolder}for_nest_points_hsb.png`} alt="for_nest_points_hsb" />
        <CodeBlock>{`size( 400, 400 );\n\ncolorMode( HSB, 400 );\n\nfor ( int iY = 0; iY < 400; iY++ )\n{\n  for ( int iX = 0; iX < 400; iX++ )\n  {\n    stroke( 0, iX, iY );\n    point( iX, iY );\n  }\n}`}</CodeBlock>
        <h3 id='例４-3'>例４</h3>
        <p>正方形と正円を重ねて並べて描画する．</p>
        <p>参考：<a href='#楕円の描画方法を指定する-1'>楕円の描画方法を指定する．</a></p>
        <img src={`${imageFolder}for_nest_square_circle.png`} alt="for_nest_square_circle" />
        <CodeBlock>{`size( 400, 400 );\n\nellipseMode( CORNER );\n\nfor ( int iY = 5; iY < 400; iY += 50 )\n{\n  for ( int iX = 5; iX < 400; iX += 50 )\n  {\n    square( iX, iY, 40 );\n    circle( iX, iY, 40 );\n  }\n}`}</CodeBlock>
        <h3 id='例５-2'>例５</h3>
        <p>正方形を回転させながら並べて描画する．</p>
        <img src={`${imageFolder}for_nest_square_rotate.png`} alt="for_nest_square_rotate" />
        <CodeBlock>{`size( 400, 400 );\n\nrectMode( CENTER );\n\nfor ( int iY = 25; iY < 400; iY += 50 )\n{\n  for ( int iX = 25; iX < 400; iX += 50 )\n  {\n    pushMatrix();\n    \n    translate( iX, iY );\n    rotate( radians( iX + iY ) );\n    square( 0, 0, 38 );\n    \n    popMatrix();\n  }\n}`}</CodeBlock>
        <h3 id='例６-1'>例６</h3>
        <p>ランダムな楕円を20個重ね，X軸方向へ10個描画する．</p>
        <img src={`${imageFolder}for_nest_elps_x.png`} alt="for_nest_elps_x" />
        <CodeBlock>{`size( 400, 200 );\n\nnoFill();\n\nfor ( int iX = 20; iX < 400; iX += 40 )\n{\n  for( int iElpsNo = 0; iElpsNo < 20; iElpsNo++ )\n  {\n    ellipse( iX, 100, random( 20, 40 ), random( 40, 200 ) );\n  }\n}`}</CodeBlock>
        <h3 id='例７'>例７</h3>
        <p>ランダムな位置に30個の花を描画する．</p>
        <img src={`${imageFolder}for_nest_flower.png`} alt="for_nest_flower" />
        <CodeBlock>{`size( 400, 400 );\n\nstroke( 204 );\nstrokeWeight( 3 );\n\nfor( int iFlower = 0; iFlower < 30; iFlower++ )\n{\n  // スクリーン全体を薄く塗りつぶす\n  fill( 204, 4 );\n  rect( 0, 0, width, height );\n  \n  pushMatrix();\n  \n  // 花のXY座標値と回転をランダム\n  translate( random( 400 ), random( 400 ) );\n  rotate( radians( random( 90 ) ) );\n  \n  // 一つの花を描画する．\n  fill( 255 );\n  for( int i = 0; i < 5; i++ )\n  {\n    ellipse( 30, 0, 55, 16 ); \n    rotate( radians( 72 ) );\n  }\n  ellipse( 0, 0, 14, 14 );\n  \n  popMatrix();\n}`}</CodeBlock>
        <h2 id='h11'>演習６</h2>
        <p>任意の例のサンプルプログラムをベースに自由に変更を加える．</p>
        <p>例５をベースにした作例</p>
        <img src={`${imageFolder}for_nest_square_circle_ex.png`} alt="for_nest_square_circle_ex" />
        <CodeBlock>{`size( 400, 400 );\n\nrectMode( CENTER );\nstrokeWeight( 2 );\n\nfor ( int iY = 25; iY < 400; iY += 50 )\n{\n  pushMatrix();\n  translate( 0, iY );\n\n  for ( int iX = 25; iX < 400; iX += 50 )\n  {\n    pushMatrix();\n    \n    translate( iX, 0 );\n    rotate( radians( iX + iY ) );\n    \n    noFill();\n    circle( 0, 0, 50 );\n    \n    fill( 0, iX, iY );\n    square( 0, 0, 34 );\n    circle( 0, 0, 34 );\n    \n    popMatrix();\n  }\n  \n  popMatrix();\n}`}</CodeBlock>
        <h2 id='h12'>多重ネストを使った表現</h2>
        <ul>
        <li><p>ネストは三重以上にもできる．</p></li>
        <li><p>ネストを増やすほど難解になるのでほどほどに．</p></li>

        </ul>
        <h3 id='例１-12'>例１</h3>
        <p>ランダムに重ねた8個の正方形をスクリーンに並べる．</p>
        <img src={`${imageFolder}for_nests_square.png`} alt="for_nests_square" />
        <CodeBlock>{`size( 400, 400 );\n\nnoFill();\n\nfor ( int iY = 5; iY < 400; iY += 50 )\n{\n  for ( int iX = 5; iX < 400; iX += 50 )\n  {\n    for ( int i = 0; i < 8; i++ )\n    {\n      square( iX, iY, random( 20, 40 ) );\n    }\n  }\n}`}</CodeBlock>
        <h3 id='例２-12'>例２</h3>
        <p>ランダムに回転して重ねた（差の絶対値）6個の正方形をスクリーンに並べる．</p>
        <img src={`${imageFolder}for_nests_squres_rot.png`} alt="for_nests_squres_rot" />
        <CodeBlock>{`size( 400, 400 );\n\nblendMode( DIFFERENCE );\nnoStroke();\nrectMode( CENTER );\n\nfor ( int iY = 25; iY < 400; iY += 50 )\n{\n  for ( int iX = 25; iX < 400; iX += 50 )\n  {\n    for ( int iDeg = 0; iDeg < 360; iDeg += 60 )\n    {\n      pushMatrix();\n      translate( iX, iY );\n      \n      rotate( radians( iDeg ) );\n      square( 0, 0, random( 20, 40 ) );\n      \n      popMatrix();\n    }\n  }\n}`}</CodeBlock>
        <h3 id='例３-6'>例３</h3>
        <p>60°ずつ回転して重ねた（乗算）6個の楕円をスクリーンに並べる．</p>
        <img src={`${imageFolder}for_nests_ellipses_rot.png`} alt="for_nests_ellipses_rot" />
        <CodeBlock>{`size( 400, 400 );\n\nblendMode( MULTIPLY );\n\nfor ( int iY = 25; iY < 400; iY += 50 )\n{\n  for ( int iX = 25; iX < 400; iX += 50 )\n  {\n    for ( int iDeg = 0; iDeg < 360; iDeg += 60 )\n    {\n      pushMatrix();\n      translate( iX, iY );\n      \n      rotate( radians( iDeg ) );\n      fill( random( 200, 255 ), random( 140, 150 ), random( 160, 200) );\n      ellipse( 10, 0, 80, 20 );\n      \n      popMatrix();\n    }\n  }\n}`}</CodeBlock>
        <h2 id='h13'>演習７</h2>
        <p>以下の多重ネストのプログラムを自由に編集し，多重ネストにより複雑な描画結果が出力されることを確認する．</p>
        <ul>
        <li><p>特に例２，３を参考にするとよい．</p></li>

        </ul>
        <p>120°ずつ回転して重ねた3個の長方形をスクリーンに並べる．</p>
        <img src={`${imageFolder}for_nests_rect.png`} alt="for_nests_rect" />
        <CodeBlock>{`size( 400, 400 );\n\nfor ( int iY = 25; iY < 400; iY += 50 )\n{\n  for ( int iX = 25; iX < 400; iX += 50 )\n  {\n    for ( int iDeg = 0; iDeg < 360; iDeg += 120 )\n    {\n      pushMatrix();\n      translate( iX, iY );\n      \n      rotate( radians( iDeg ) );\n      rect( 0, 0, 20, 10 );\n      \n      popMatrix();\n    }\n  }\n}`}</CodeBlock>
        <h4 id='作例-6'>作例</h4>
        <img src={`${imageFolder}for_nests_sasa.png`} alt="for_nests_sasa" />
        <CodeBlock>{`size( 400, 400 );\n\nbackground( 5 );\n\nfor ( int iY = 25; iY < 400; iY += 50 )\n{\n  for ( int iX = 25; iX < 400; iX += 50 )\n  {\n    for ( int iDeg = 0; iDeg < 360; iDeg += 120 )\n    {\n      pushMatrix();\n      translate( iX, iY );\n      \n      rotate( radians( iDeg + ( iX % 100 ) * 1.2 ) );\n      fill( random( 100, 140 ), iX, random( 25, 50 ) );\n      ellipse( 22, 0, 42, 10 );\n      line( 0, 0, 44, 0 );\n      \n      popMatrix();\n    }\n  }\n}`}</CodeBlock>
        <h2 id='h14'>その他のサンプル</h2>
        <h3 id='反復'>反復</h3>
        <img src={`${imageFolder}repeat.png`} alt="repeat" />
        <CodeBlock>{`size(400,200);\nnoStroke();\ncolorMode( HSB,360, width-40, 1.0 ); \nfor( int iX=0; iX<width; iX+=30 )\n{\n  fill( 180, iX+40, 1 );\n  rect( iX,    50,    8, height );  // 矩形:長\n  fill( 300, iX+40, 1 );\n  rect( iX+10, 50+40, 8, height );  // 矩形:中\n  fill( 60,  iX+40, 1 );\n  rect( iX+20, 50+80, 8, height );  // 矩形:短\n}`}</CodeBlock>
        <h3 id='振動'>振動</h3>
        <img src={`${imageFolder}for_wave.png`} alt="for_wave" />
        <CodeBlock>{`int iFurehaba = 80;  // 振幅（大きな波の高さ）\nint iFreq = 2;       // 振動数\nint iLineWidth = 4;  // 描画する線のX幅（小さくすると解像度上がる）\n\nsize( 400,200 );\ncolorMode( HSB, 360, 1.0, 1.0, 1.0 );\nstrokeWeight( 8 );\n\nfor( int iX= 0; iX < width; iX += iLineWidth )\n{\n  // 位相（角度）\n  int iDeg0 = iX * iFreq;\n  int iDeg1 = ( iX + iLineWidth ) * iFreq;\n  \n  stroke( iDeg0 % 360, 1.0, 1.0 );  // iDeg（角度）を360以内に収める\n  line( iX, height/2 + iFurehaba * cos( radians(iDeg0) ),\n        iX + iLineWidth, height/2 + iFurehaba * cos( radians(iDeg1) ) );\n}`}</CodeBlock>
        <h3 id='遷移'>遷移</h3>
        <img src={`${imageFolder}strips_transition.png`} alt="transition" />
        <CodeBlock>{`size(400,200);\nnoStroke();\ncolorMode( HSB,width, 1.0, 1.0 );\nfor( int iX=0; iX<width; iX+=10 )\n{\n  fill( iX, 1, 1 );\n  rect( iX, iX*height/width, 8, height );  // 矩形\n}`}</CodeBlock>
        <h3 id='乱雑さ'>乱雑さ</h3>
        <img src={`${imageFolder}strips_random.png`} alt="random" />
        <CodeBlock>{`size(400,200);\nnoStroke();\ncolorMode( HSB, width, height, 1.0 );\nfor( int iX=0; iX<width; iX+=10 )\n{\n  float fY = 20 + random(height-20);  // Y座標値を表す変数．\n  fill( iX, height-fY+20, 1 );\n  rect( iX, fY, 8, height );  // 矩形
        }`}</CodeBlock>
        <h3 id='モアレ'>モアレ</h3>
        <img src={`${imageFolder}moire.png`} alt="moire" />
        <CodeBlock>{`size(400,200);\nbackground(0, 0, 0);\nnoStroke();\ncolorMode( HSB, width, height, 1.0 );\n\n// 正円を格子状に描画．\nfor( int iY=3; iY<height; iY+=12 )\n{\n  for( int iX=3; iX<width; iX+=12 )\n  {\n    fill( iX, height, 1 );\n    circle( iX, iY, 6 );  // 円\n  }\n}\n\ntranslate( width/3, -height/2 );  // 座標系を平行移動\nrotate( radians(45) );            // 座標系を45度回転 \n\n// 正円を格子状に描画．\nfor( int iY=3; iY<height; iY+=12 )\n{\n  for( int iX=3; iX<width; iX+=12 )\n  {\n    fill( iX, height, 1 );\n    circle( iX, iY, 6 );  // 円\n  }\n}`}</CodeBlock>
        <h3 id='回転対称'>回転対称</h3>
        <p>ひし型の回転対称</p>
        <img src={`${imageFolder}synmetry_rot.png`} alt="synmetry_rot" />
        <CodeBlock>{`int iRepeatTotal = 9;   // ひし形の数.\nint iRotDeg = 40;       // 回転させる角度.\nsize(400,400);\ncolorMode( HSB, 3, 1, 1 );\n\ntranslate( width/2, height/2 );  // 原点座標をスクリーン中心へ移動.\n\n// ひし形の数だけ繰り返す.\nfor( int iHishigata = 0; iHishigata < iRepeatTotal; iHishigata++ )\n{\n  fill( iHishigata % 3, 1, 1 );  // 3種類の色相を交互に.\n  quad( 0, 0, 100, -15, 200, 0, 100,  15 );            // ひし形の描画.\n  rotate( radians(iRotDeg) );  // 座標系の回転.\n}`}</CodeBlock>
        <h3 id='並進対称'>並進対称</h3>
        <img src={`${imageFolder}synmetry_trl.png`} alt="synmetry_trl" />
        <CodeBlock>{`int iColumnRepeat = 7; // 横に繰り返す回数.\nsize(400,400);\ncolorMode( HSB, 10, iColumnRepeat+2, 1 );\nnoStroke();\ntranslate( -40, 0);  // スクリーンを充填するため，最初に少しずらす．\nfor( int iRowIdx = 0; iRowIdx < 10; iRowIdx++ )\n{\n  pushMatrix(); // 原点座標の位置(左端)を記憶.\n  // 横にiColumnRepeatセットの三角形ペアを描画.\n  for( int iColumnIdx = 0; iColumnIdx < iColumnRepeat; iColumnIdx++ )\n  {\n    fill( iRowIdx, iColumnIdx+2, 1 );        // 下向きの三角.\n    triangle( 0, 0, 80, 0, 40, 40 );\n    fill( iRowIdx+1, iColumnIdx+2, 1 );      // 上向きの三角.\n    triangle( 40, 40, 80, 0, 120, 40 );\n    translate( 80, 0 );  // 右にずらす.\n  }\n  popMatrix(); // 原点座標を左端に戻す.\n  \n  translate( 0, 40 );   // 原点座標を一段下げる.
        }`}</CodeBlock>
    </div>
  );
};

export default Programming04;
