// Programming03.tsx

import React, { useEffect} from 'react';
import { useLocation } from 'react-router-dom';
import { LinkP, JumpToDefaultScroll, SketchCode, CodeBlock } from '../ContentCommon';
import { MathJaxProvider, MathJaxFormula } from 'mathjax3-react';
import '../ContentCommon.css';

const imageFolder: string = "/musabi-react/images/programming/";

interface ContentProps {
  practiceId: string;
}

const Programming03: React.FC<ContentProps> = ({ practiceId }) => {

  /* URLから初期スクロール位置 */
  const location = useLocation();
  useEffect(() => {
    JumpToDefaultScroll(practiceId, location as any );    
  }, [practiceId,location]);
  return (
    <div className="common-style">
      <h1 id='h0'>第３回：演算と変数</h1>
      <hr color='#8dacf9' />
      <h2 id='引数に式を書く'>引数に式を書く．</h2>
      <img src={`${imageFolder}params_suushiki.png`} alt="params_suushiki" />
      <CodeBlock>{'size( 200, 100 );\n\ncircle( 50,       50, 40 );\n\ncircle( 50 + 50,  50, 40 );\n\ncircle( 50 + 100, 50, 40 );'}</CodeBlock>
      <h3 id='引数は式を書くこともできる'>引数は式を書くこともできる．</h3>
      <p>式がコンピュータにより計算され，答の値が引数になる．</p>
      <h3 id='式とは'>式とは</h3>
      <ul>
        <li><p>式（Expression）</p></li>
        <li><p>コンピュータが演算を行うための記述形式のこと．</p></li>
      </ul>
      <h3 id='算術式とは-1'>算術式とは</h3>
      <ul>
      <li><p>数値を演算（計算）する式のこと．</p>
      </li>
      <li><p>四則演算が代表的</p>
      <p>プログラムの算術式は数学と形式が多少異なる．</p>
      <figure><table>
      <thead>
      <tr><th>数学の数式</th><th>プログラムの算術式</th></tr></thead>
      <tbody><tr><td>1 + 2 = 3</td><td><code>1 + 2</code></td></tr><tr><td>2 - 1 = 1</td><td><code>2 - 1</code></td></tr><tr><td>3 × 2 = 6</td><td><code>3 * 2</code></td></tr><tr><td>6 ÷ 3 = 2</td><td><code>6 / 3</code></td></tr></tbody>
      </table></figure>
      </li>
      <li><p>プログラムの数式はコンピュータが答えを演算（計算）するので，右辺（<code>=</code>以降）を記述しない．</p>
      </li>
      <li><p>式だけでは命令文として成り立たず，プログラムが実行できない．</p>
      <ul>
      <li><p>式は文の中の要素の一つ．</p></li>

      </ul>
      </li>

      </ul>
      <h3 id='演算子-1'>演算子</h3>
      <p>プログラムの式に用いる演算を行うための記号を演算子という．</p>
      <h3 id='算術演算子'>算術演算子</h3>
      <ul>
      <li><p>算術演算をおこなう演算子を算術演算子という．</p></li>
      <li><p>以下に演算記号とプログラムにおける演算子の対応表を記載する．</p></li>

      </ul>
      <figure><table>
      <thead>
      <tr><th>算数（演算記号）</th><th>プログラム（演算子）</th><th>記号の読み方</th><th>日本語キーボードにおけるキーの場所の対応</th></tr></thead>
      <tbody><tr><td>+</td><td><code>+</code></td><td>プラス</td><td>Shift + 「れ」</td></tr><tr><td>−</td><td><code>-</code></td><td>ハイフン</td><td>「ほ」</td></tr><tr><td>×</td><td><code>*</code></td><td>アスタリスク</td><td>Shift + 「け」</td></tr><tr><td>÷</td><td><code>/</code></td><td>スラッシュ</td><td>「め」</td></tr></tbody>
      </table></figure>
      <h3 id='式と演算子'>式と演算子</h3>
      <p>式と演算子の関係をイメージで理解しておく．</p>
      <img src={`${imageFolder}math_operater.jpg`} alt="math_operater" />
      <h2 id='h1'>演習１</h2>
      <p>引数に数式を記述し，4つの円を上下左右に表示する．</p>
      <ol>
      <li><p>以下のプログラムをコピーする．</p>
      <img src={`${imageFolder}params_suushiki_circle.png`} alt="params_suushiki_circle" />
      <CodeBlock>{'size( 200, 200 );\n\ncircle( 100, 100, 40 );'}</CodeBlock>
      </li>
      <li><p>以下の円の描画命令を追加する．</p>
      <p><code>circle( 100 + 50, 100, 40 );</code></p>
      <img src={`${imageFolder}params_suushiki_circle2.png`} alt="params_suushiki_circle2" />
      </li>
      <li><p>ステップ2のように，最初の円の座標値<code>100</code>に加算／減算する数式を記述し，円の描画命令を追加していく．
      以下のような表示が行われれば完成．</p>
      <img src={`${imageFolder}params_suushiki_circle5.png`} alt="params_suushiki_circle5" />
      </li>
      <li><p>以下完成プログラム</p>
      <CodeBlock>{'size( 200, 200 );\n\ncircle( 100, 100, 40 );\n\ncircle( 100 + 50, 100, 40 );\n\ncircle( 100 - 50, 100, 40 );\n\ncircle( 100, 100 + 50, 40 );\n\ncircle( 100, 100 - 50, 40 );'}</CodeBlock>\n</li>

      </ol>
      <p>このような式への置き換えは意味がない気がするが，変数を用いる場合に有効になる．</p>
      <h2 id='h2'>変数を使う．</h2>
      <p>演習１のプログラムを変数で置き換えたプログラム．</p>
      <img src={`${imageFolder}params_suushiki_circle5.png`} alt="params_suushiki_circle5" />
      <CodeBlock>{'int iCenterX = 100;		// 図形の中心座標X\nint iCenterY = 100;		// 図形の中心座標Y\n\nsize( 200, 200 );\n\ncircle( iCenterX, iCenterY, 40 );\n\ncircle( iCenterX + 50, iCenterY, 40 );\n\ncircle( iCenterX - 50, iCenterY, 40 );\n\ncircle( iCenterX, iCenterY + 50, 40 );\n\ncircle( iCenterX, iCenterY - 50, 40 );'}</CodeBlock>
      <p>変数の値を変えることで，簡単に図形の描画を変化させることができる．</p>
      <img src={`${imageFolder}params_suushiki_circle5_zure.png`} alt="params_suushiki_circle5_zure" />
      <CodeBlock>{'int iCenterX = 60;\nint iCenterY = 60;\n\nsize( 200, 200 );\n\ncircle( iCenterX, iCenterY, 40 );\n\ncircle( iCenterX + 50, iCenterY, 40 );\n\ncircle( iCenterX - 50, iCenterY, 40 );\n\ncircle( iCenterX, iCenterY + 50, 40 );\n\ncircle( iCenterX, iCenterY - 50, 40 );'}</CodeBlock>
      <h3 id='変数とは'>変数とは</h3>
      <ul>
      <li><p>値を保存するための名前付きスペース．</p>
      <ul>
      <li><p>データの記憶領域はメモリ上にある．</p></li>

      </ul>
      </li>
      <li><p>メモリ上に保存領域を確保するために準備（定義）をする必要がある．</p>
      </li>
      <li><p>1つの変数は1つのデータ（値）を保持する．</p>
      </li>

      </ul>
      <h3 id='変数を定義する'>変数を定義する．</h3>
      <p>変数を作ることを変数を<strong>定義</strong>するという．</p>
      <h4 id='構文-39'>構文</h4>
      <p><code>int 変数名 = 数値;</code></p>
      <p><code>float 変数名 = 数値;</code></p>
      <h4 id='説明-32'>説明</h4>
      <img src={`${imageFolder}int_koubun.jpg`} alt="int_koubun" />
      <ul>
      <li><p><code>int</code>と<code>float</code> はデータ型（Datatype）という．</p>
      <ul>
      <li><p><code>int</code>か<code>float</code>どちらを記述するかで<strong>入れられる数値の種類が決まる</strong>．</p></li>

      </ul>
      </li>
      <li><p>変数名は半角英字で自由に名前を記述する．</p>
      </li>
      <li><p><code>=</code>に続いて変数に保存する初期値を記述する．（値の代入）</p>
      </li>

      </ul>
      <p><strong><code>int</code></strong></p>
      <ul>
      <li><p>整数型（ Integer ）</p>
      </li>
      <li><p>値として小数は持てない<strong>整数</strong>の型</p>
      <ul>
      <li><p><code>1, 2, 10,  23, 1028, 5270</code> など</p></li>

      </ul>
      </li>
      <li><p>マイナスの数値も持てる．</p>
      <ul>
      <li><p><code>-1, -97, -254, -1097, -23819</code> など</p></li>

      </ul>
      </li>
      <li><p>最大で<code>2147483647</code>，最小で<code>-2147483648</code>までの値を持てる</p>
      </li>

      </ul>
      <p><strong><code>float</code></strong></p>
      <ul>
      <li><p>実数型（  floating-point numbers ）</p>
      </li>
      <li><p>値として<strong>小数</strong>を持てる型</p>
      <ul>
      <li><p><code>1.02,  32.890,  403.98, 3109.37</code> など</p></li>

      </ul>
      </li>
      <li><p>マイナスの数値も持てる．</p>
      <ul>
      <li><p><code>1.02,  32.890,  403.98, 3109.37</code> など</p></li>

      </ul>
      </li>
      <li><p>最大で3.40282347E+38，最小で-3.40282347E+38 の値を持てる</p>
      <ul>
      <li><p>E+38 は「10の38乗」の意味</p></li>
      <li><p>通常使用する範囲でならあらゆる数値を扱える．</p></li>

      </ul>
      </li>

      </ul>
      <h3 id='変数を使う-2'>変数を使う．</h3>
      <ul>
      <li><p>変数を命令の引数として記述する．</p>
      </li>
      <li><p>変数を式に組み込むこともできる．</p>
      <ul>
      <li><p>数値と同じように扱える．</p></li>

      </ul>
      </li>

      </ul>
      <h4 id='例１-1'>例１</h4>
      <p>int型の変数を作る例．
      図形のXY座標値を変数に保存している．</p>
      <img src={`${imageFolder}var_circle.png`} alt="var_circle" />
      <CodeBlock>{'int iCenterX = 60;    // 円の中心座標X\nint iCenterY = 60;    // 円の中心座標Y\n\ncircle( iCenterX, iCenterY, 40 );'}</CodeBlock>
      <h4 id='例２-1'>例２</h4>
      <p>例１を元に，float型の変数を作る例．
      HSBで用いる色情報を変数に保存している．</p>
      <img src={`${imageFolder}var_circle_col.png`} alt="var_circle_col" />
      <CodeBlock>{'int iCenterX = 60;    // 円の中心座標X\nint iCenterY = 60;    // 円の中心座標Y\nint iHue = 184;      // 色相\nfloat fS = 0.9;      // 彩度\nfloat fB = 0.75;     // 輝度\n\ncolorMode( HSB, 360, 1.0, 1.0 );\n\nfill( iHue, fS, fB );\ncircle( iCenterX, iCenterY, 40 );'}</CodeBlock>
      <h4 id='例３-1'>例３</h4>
      <p>演習１のプログラムに変数を加えたもの．</p>
      <ul>
      <li>変数<code>iCenterX</code>と<code>iCenterY</code>を式に組み込んでいる．</li>
      <li><p>変数の初期値を変更すると図形の中心が移動することを確認する．</p></li>

      </ul>
      <img src={`${imageFolder}params_suushiki_circle5_zure.png`} alt="params_suushiki_circle5_zure" />
      <p>&nbsp;</p>
      <CodeBlock>{'int iCenterX = 60;		// 変数iCenterX\nint iCenterY = 60;		// 変数iCenterY\n\nsize( 200, 200 );\n\ncircle( iCenterX, iCenterY, 40 );\n\ncircle( iCenterX + 50, iCenterY, 40 );\n\ncircle( iCenterX - 50, iCenterY, 40 );\n\ncircle( iCenterX, iCenterY + 50, 40 );\n\ncircle( iCenterX, iCenterY - 50, 40 );'}</CodeBlock>
      <h4 id='例４-1'>例４</h4>
      <p>例３に<code>float</code>型の変数を追加し，変数を使って色の指定を行う例．
      彩度と輝度は共通にするため，変数を用いている．</p>
      <img src={`${imageFolder}var_int_float_circle5.png`} alt="var_int_float_circle5" />
      <CodeBlock>{'int iCenterX = 100;\nint iCenterY = 100;\nfloat fS = 0.9;        // 彩度\nfloat fB = 1.0;        // 輝度\n\nsize( 200, 200 );\n\ncolorMode( HSB, 360, 1.0, 1.0 );\n\nfill( 0, fS, fB );\ncircle( iCenterX, iCenterY, 40 );\n\nfill( 80, fS, fB );\ncircle( iCenterX + 50, iCenterY, 40 );\n\nfill( 160, fS, fB );\ncircle( iCenterX - 50, iCenterY, 40 );\n\nfill( 240, fS, fB );\ncircle( iCenterX, iCenterY + 50, 40 );\n\nfill( 320, fS, fB );\ncircle( iCenterX, iCenterY - 50, 40 );'}</CodeBlock>
      <h2 id='h3'>演習２</h2>
      <p>例４のプログラムに円の大きさ用の変数を一つ追加し，命令文の数値を変数に置き換える．</p>
      <ol>
      <li><p>例２のプログラムをコピーする．</p>
      </li>
      <li><p>円の大きさ用の<code>int</code>型の変数を１つ作成する．</p>
      </li>
      <li><p>描画命令の引数部分を変数に置き換える．</p>
      </li>
      <li><p>追加した変数の初期値を変更すると全ての円の大きさが変わることを確認する．</p>
      <img src={`${imageFolder}var_int_float_circle5_size.png`} alt="var_int_float_circle5_size" />
      </li>
      <li><p>以下，完成プログラム</p>
      <CodeBlock>{'int iCenterX = 100;\nint iCenterY = 100;\nint iSize = 50;\n\nfloat fS = 0.9;        // 彩度\nfloat fB = 1.0;        // 輝度\n\nsize( 200, 200 );\n\ncolorMode( HSB, 360, 1.0, 1.0 );\n\nfill( 0, fS, fB );\ncircle( iCenterX, iCenterY, iSize );\n\nfill( 80, fS, fB );\ncircle( iCenterX + 50, iCenterY, iSize );\n\nfill( 160, fS, fB );\ncircle( iCenterX - 50, iCenterY, iSize );\n\nfill( 240, fS, fB );\ncircle( iCenterX, iCenterY + 50, iSize );\n\nfill( 320, fS, fB );\ncircle( iCenterX, iCenterY - 50, iSize );'}</CodeBlock>
      </li>

      </ol>
      <h2 id='h4'>変数の値を操作する．</h2>
      <p>変数の値は後から変更することができる．</p>
      <ul>
      <li><p>値が変更できるから「変」数という．</p></li>
      <li><p>変更できないものを定数という．</p></li>

      </ul>
      <img src={`${imageFolder}var_add_squares.png`} alt="var_add_squares" />
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nint iY = 25;\nint iSize = 50;\n\nsquare( iX, iY, iSize );\niX += iSize;\n\nsquare( iX, iY, iSize );\niX += iSize;\n\nsquare( iX, iY, iSize );\niX += iSize;\n\nsquare( iX, iY, iSize );\niX += iSize;\n\nsquare( iX, iY, iSize );\niX += iSize;\n\nsquare( iX, iY, iSize );\niX += iSize;\n\nsquare( iX, iY, iSize );\niX += iSize;\n\nsquare( iX, iY, iSize );\niX += iSize;'}</CodeBlock>
      <h3 id='変数に値を足す加算）'>変数に値を足す（加算）</h3>
      <h4 id='構文-40'>構文</h4>
      <p><code>変数名 += 値;</code></p>
      <h4 id='説明-33'>説明</h4>
      <ul>
      <li><p>事前に変数は作成しておく必要がある．</p></li>
      <li><p>変数に保存されている値に右辺の値を足す．</p></li>

      </ul>
      <p><a href='https://processing.org/reference/addassign.html' target='_blank' >https://processing.org/reference/addassign.html</a></p>
      <h4 id='例１-2'>例１</h4>
      <p>X座標値の変数に値を足しながら正方形を横に並べる．</p>
      <img src={`${imageFolder}var_add_squares.png`} alt="var_add_squares" />
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;			// X座標値\nint iY = 25;\nint iSize = 50;\n\nsquare( iX, iY, iSize );\niX += iSize;\n\nsquare( iX, iY, iSize );\niX += iSize;\n\nsquare( iX, iY, iSize );\niX += iSize;\n\nsquare( iX, iY, iSize );\niX += iSize;\n\nsquare( iX, iY, iSize );\niX += iSize;\n\nsquare( iX, iY, iSize );\niX += iSize;\n\nsquare( iX, iY, iSize );\niX += iSize;\n\nsquare( iX, iY, iSize );\niX += iSize;'}</CodeBlock>
      <h3 id='変数から値を引く減算）'>変数から値を引く（減算）</h3>
      <h4 id='構文-41'>構文</h4>
      <p><code>変数名 -= 値;</code></p>
      <h4 id='説明-34'>説明</h4>
      <ul>
      <li><p>事前に変数は作成しておく必要がある．</p></li>
      <li><p>変数に保存されている値から右辺の値を引く．</p></li>

      </ul>
      <p><a href='https://processing.org/reference/subtractassign.html' target='_blank' >https://processing.org/reference/subtractassign.html</a></p>
      <h3 id='変数に値をかける乗算）'>変数に値をかける（乗算）</h3>
      <h4 id='構文-42'>構文</h4>
      <p><code>変数名 *= 値;</code></p>
      <h4 id='説明-35'>説明</h4>
      <ul>
      <li><p>事前に変数は作成しておく必要がある．</p></li>
      <li><p>変数に保存されている値に右辺の値をかける．</p></li>

      </ul>
      <p><a href='https://processing.org/reference/multiplyassign.html' target='_blank' >https://processing.org/reference/multiplyassign.html</a></p>
      <h4 id='例２-2'>例２</h4>
      <p>例１のプログラムに，正方形の大きさの値の変数に<code>0.9</code>をかける記述を追加した例．</p>
      <img src={`${imageFolder}var_mult_squares.png`} alt="var_mult_squares" />
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nint iY = 25;\nint iSize = 50;\n\nsquare( iX, iY, iSize );\niX += iSize;\niSize *= 0.9;		// 乗算\n\nsquare( iX, iY, iSize );\niX += iSize;\niSize *= 0.9;\n\nsquare( iX, iY, iSize );\niX += iSize;\niSize *= 0.9;\n\nsquare( iX, iY, iSize );\niX += iSize;\niSize *= 0.9;\n\nsquare( iX, iY, iSize );\niX += iSize;\niSize *= 0.9;\n\nsquare( iX, iY, iSize );\niX += iSize;\niSize *= 0.9;\n\nsquare( iX, iY, iSize );\niX += iSize;\niSize *= 0.9;\n\nsquare( iX, iY, iSize );\niX += iSize;\niSize *= 0.9;'}</CodeBlock>
      <h3 id='変数を値でわる除算）'>変数を値でわる（除算）</h3>
      <h4 id='構文-43'>構文</h4>
      <p><code>変数名 /= 値;</code></p>
      <h4 id='説明-36'>説明</h4>
      <ul>
      <li><p>事前に変数は作成しておく必要がある．</p></li>
      <li><p>変数に保存されている値から右辺の値をわる．</p></li>

      </ul>
      <p><a href='https://processing.org/reference/divideassign.html' target='_blank' >https://processing.org/reference/divideassign.html</a></p>
      <h3 id='変数へ値を上書きする'>変数へ値を上書きする．</h3>
      <h4 id='構文-44'>構文</h4>
      <p><code>変数名 = 値;</code></p>
      <h4 id='説明-37'>説明</h4>
      <ul>
      <li><p>変数へ単純に新しい値を上書きすることができる．</p></li>
      <li><p>変数へ値を保存することを代入という．</p></li>

      </ul>
      <h3 id='代入演算子'>代入演算子</h3>
      <ul>
      <li><p>これらの演算子を代入演算子という．</p></li>
      <li><p>単なる算術演算子と違い，変数へ値の代入まで行っている．</p></li>

      </ul>
      <figure><table>
      <thead>
      <tr><th>四則計算</th><th>演算</th><th>代入演算子</th></tr></thead>
      <tbody><tr><td>足し算</td><td>加算</td><td><code>+=</code></td></tr><tr><td>引き算</td><td>減算</td><td><code>-=</code></td></tr><tr><td>かけ算</td><td>乗算</td><td><code>*=</code></td></tr><tr><td>割り算</td><td>除算</td><td><code>/=</code></td></tr></tbody>
      </table></figure>
      <h2 id='h5'>エディタ：検索と置換</h2>
      <p>プログラムの同じ個所を同時に書き換えたい場合，文字列の置換を行うとよい．</p>
      <ol>
      <li><p>エディタ内の書き換えたい文字列を選択する．</p></li>
      <li><p><code>Ctrl</code> キーを押しながら <code>F</code>キー を押す．</p></li>
      <li><p>「置換」欄に書き換えたい（新しい）文字列を入力する．</p></li>
      <li><p>「すべて置換」をクリックする．</p></li>

      </ol>
      <img src={`${imageFolder}mojiretsu_chikan.png`} alt="mojiretsu_chikan" />
      <h2 id='h6'>演習３</h2>
      <p>例２のプログラムに代入演算子を使った文を複数追加し，下図のような表示が行われるプログラムを作る．</p>
      <img src={`${imageFolder}var_squares_addy.png`} alt="var_squares_addy" />
      <ol>
      <li><p>例２のプログラムをコピーする．</p>
      </li>
      <li><p>スクリーンサイズを400×200へ変更する．</p>
      </li>
      <li><p>いずれかの変数と代入演算子を使った文を複数追加し，上図のように表示されるプログラムを作成する．</p>
      </li>
      <li><p>以下，完成プログラム．</p>
      <CodeBlock>{'size( 400, 200 );\n\nint iX = 0;\nint iY = 0;\nint iSize = 50;\n\nsquare( iX, iY, iSize );\niX += iSize;\niY += iSize / 2;\niSize *= 0.9;    // 乗算\n\nsquare( iX, iY, iSize );\niX += iSize;\niY += iSize / 2;\niSize *= 0.9;\n\nsquare( iX, iY, iSize );\niX += iSize;\niY += iSize / 2;\niSize *= 0.9;\n\nsquare( iX, iY, iSize );\niX += iSize;\niY += iSize / 2;\niSize *= 0.9;\n\nsquare( iX, iY, iSize );\niX += iSize;\niY += iSize / 2;\niSize *= 0.9;\n\nsquare( iX, iY, iSize );\niX += iSize;\niY += iSize / 2;\niSize *= 0.9;\n\nsquare( iX, iY, iSize );\niX += iSize;\niY += iSize / 2;\niSize *= 0.9;\n\nsquare( iX, iY, iSize );\niX += iSize;\niY += iSize / 2;\niSize *= 0.9;'}</CodeBlock>
      </li>

      </ol>
      <h2 id='h7'>剰余算を使った表現</h2>
      <p>ストライプやボーダーなどの縞模様の表現に用いることが多い</p>
      <img src={`${imageFolder}var_squares_jouyo.png`} alt="var_squares_jouyo" />
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nint iY = 25;\nint iSize = 50;\nint iRepeat = 200;	// 剰余算用の変数\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;'}</CodeBlock>
      <h3 id='剰余算-1'>剰余算</h3>
      <ul>
      <li><p>「割り算の余り」を求める記述．</p>
      <ul>
      <li><p>割り算の余りを剰余という．</p></li>

      </ul>
      </li>
      <li><p>プログラムの算術式は数学と形式が多少異なる．</p>
      <figure><table>
      <thead>
      <tr><th>数学の数式</th><th>プログラムの算術式</th></tr></thead>
      <tbody><tr><td>10 ÷ 3 = 3 余り 1</td><td><code>10 % 3</code></td></tr></tbody>
      </table></figure>
      </li>

      </ul>
      <h4 id='演算子-2'>演算子</h4>
      <figure><table>
      <thead>
      <tr><th>算数（演算記号）</th><th>プログラム（演算子）</th><th>記号の読み方</th><th>日本語キーボードにおけるキーの場所の対応</th></tr></thead>
      <tbody><tr><td>÷</td><td><code>%</code></td><td>パーセント</td><td>Shift +「え」</td></tr></tbody>
      </table></figure>
      <h4 id='例１-3'>例１</h4>
      <p>正方形の座標値Xの値を200で割った余りを塗色に設定した例</p>
      <img src={`${imageFolder}var_squares_jouyo.png`} alt="var_squares_jouyo" />
      <p>&nbsp;</p>
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nint iY = 25;\nint iSize = 50;\nint iRepeat = 200;	// 剰余算用の変数\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;'}</CodeBlock>
      <h4 id='例２-3'>例２</h4>
      <p>環状に並んだ円の角度を180で割った余りを塗色に設定した例</p>
      <img src={`${imageFolder}var_circles_jouyo.png`} alt="var_circles_jouyo" />
      <CodeBlock>{'size( 200, 200 );\n\nint iX = 80;\nint iSize = 30;\nint iDegree = 0;      // 現在の回転角度（°）\nint iInterval = 30;   // 回転間隔\nint iRepeat = 180;\n\ntranslate( 100, 100 );\n\nfill( iDegree % iRepeat );\ncircle( iX, 0, iSize );\nrotate( radians( iInterval ) );\niDegree += iInterval;\n\nfill( iDegree % iRepeat );\ncircle( iX, 0, iSize );\nrotate( radians( iInterval ) );\niDegree += iInterval;\n\nfill( iDegree % iRepeat );\ncircle( iX, 0, iSize );\nrotate( radians( iInterval ) );\niDegree += iInterval;\n\nfill( iDegree % iRepeat );\ncircle( iX, 0, iSize );\nrotate( radians( iInterval ) );\niDegree += iInterval;\n\nfill( iDegree % iRepeat );\ncircle( iX, 0, iSize );\nrotate( radians( iInterval ) );\niDegree += iInterval;\n\nfill( iDegree % iRepeat );\ncircle( iX, 0, iSize );\nrotate( radians( iInterval ) );\niDegree += iInterval;\n\nfill( iDegree % iRepeat );\ncircle( iX, 0, iSize );\nrotate( radians( iInterval ) );\niDegree += iInterval;\n\nfill( iDegree % iRepeat );\ncircle( iX, 0, iSize );\nrotate( radians( iInterval ) );\niDegree += iInterval;\n\nfill( iDegree % iRepeat );\ncircle( iX, 0, iSize );\nrotate( radians( iInterval ) );\niDegree += iInterval;\n\nfill( iDegree % iRepeat );\ncircle( iX, 0, iSize );\nrotate( radians( iInterval ) );\niDegree += iInterval;\n\nfill( iDegree % iRepeat );\ncircle( iX, 0, iSize );\nrotate( radians( iInterval ) );\niDegree += iInterval;\n\nfill( iDegree % iRepeat );\ncircle( iX, 0, iSize );\nrotate( radians( iInterval ) );\niDegree += iInterval;'}</CodeBlock>
      <h2 id='h8'>演習４</h2>
      <p>例１のプログラムの変数<code>iRepeat</code>の初期値を書き換え，様々な模様のパターンを表示する．
      余裕があれば<code>fill()</code>を書き換え，RGB値を指定してみる．</p>
      <img src={`${imageFolder}var_squares_jouyo_01.png`} alt="var_squares_jouyo_01" />
      <img src={`${imageFolder}var_squares_jouyo_02.png`} alt="var_squares_jouyo_02" />
      <img src={`${imageFolder}var_squares_jouyo_03.png`} alt="var_squares_jouyo_03" />
      <h2 id='h9'>複雑な計算式</h2>
      <h3 id='四則混合'><strong>四則</strong>混合</h3>
      <ul>
        <li><p>以下のような四則演算が混じった計算のこと．</p></li>
      </ul>
      <ul>
        <li><p>プログラムにおける算術式においても，もちろん表現できる．</p>
          <ul>
          <li><p>上記の数式を算術式で表現すると以下のようになる．</p>
          <CodeBlock>{'( 1 + 2 ) - 3 *4 / 5'}</CodeBlock>
          </li>
          </ul>
        </li>
        <li><p>算数と同様に，計算する順番が決まっている．</p>
        </li>
      </ul>
      <h3 id='算術演算子の優先順位'>算術演算子の優先順位</h3>
      <ul>
      <li><p>ある一つの四則混合の計算が行われる際，内部の一つ一つの演算が順番に実行されていく．</p>
      </li>
      <li><p>この順番は算数の四則計算に準じる．</p>
      <ul>
      <li><p>式の左から逐次実行されるわけではない．</p></li>
      <li><p>下の表において，優先度の高い演算子から順番に演算が行われる．</p></li>

      </ul>
      </li>

      </ul>
      <figure><table>
      <thead>
      <tr><th>優先度</th><th>種類</th><th>演算子</th></tr></thead>
      <tbody><tr><td>高い</td><td>括弧(カッコ)</td><td><code>( )</code></td></tr><tr><td>&nbsp;</td><td>乗算</td><td><code>*</code></td></tr><tr><td>&nbsp;</td><td>除算</td><td><code>/</code></td></tr><tr><td>&nbsp;</td><td>剰余算</td><td><code>%</code></td></tr><tr><td>&nbsp;</td><td>加算</td><td><code>+</code></td></tr><tr><td>低</td><td>減算</td><td><code>-</code></td></tr></tbody>
      </table></figure>
      <h2 id='h10'>演習５</h2>
      <p>白黒ストライプを作る．</p>
      <img src={`${imageFolder}var_squares_shirokuro.png`} alt="var_squares_shirokuro" />
      <ol>
      <li><p>以下のプログラムをコピーする．</p>
      <img src={`${imageFolder}var_squares_shirrogray.png`} alt="var_squares_shirrogray" />
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nint iY = 25;\nint iSize = 50;\nint iRepeat = 100;  // 剰余算用の変数\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( iX % iRepeat );\nsquare( iX, iY, iSize );\niX += iSize;'}</CodeBlock>
      </li>
      <li><p><code>fill( iX % iRepeat )</code>の中の引数を書き換え，白黒ストライプが描画されるようにする．</p>
      <p>カッコや他の算術演算子を加え，四則混合の式にする必要がある．</p>
      <img src={`${imageFolder}var_squares_shirokuro.png`} alt="var_squares_shirokuro" />
      </li>
      <li><p>以下，完成プログラム</p>
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nint iY = 25;\nint iSize = 50;\nint iRepeat = 100;  // 剰余算用の変数\n\nfill( ( iX % iRepeat ) * 5 );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( ( iX % iRepeat ) * 5 );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( ( iX % iRepeat ) * 5 );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( ( iX % iRepeat ) * 5 );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( ( iX % iRepeat ) * 5 );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( ( iX % iRepeat ) * 5 );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( ( iX % iRepeat ) * 5 );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( ( iX % iRepeat ) * 5 );\nsquare( iX, iY, iSize );\niX += iSize;'}</CodeBlock>
      </li>

      </ol>
      <h2 id='h11'>三角関数を用いた表現</h2>
      <h3 id='三角関数'>三角関数</h3>
      <p><strong><code>sin()</code></strong></p>
      <p><strong><code>cos()</code></strong></p>
      <p><strong><code>tan()</code></strong></p>
      <h4 id='構文-45'>構文</h4>
      <CodeBlock>{`sin( 角度のラジアン値 )\ncos( 角度のラジアン値 )\ntan( 角度のラジアン値 )`}</CodeBlock>
      
      <p>角度をラジアンへ変換する命令を含めると以下のようになる</p>
      <CodeBlock>{`sin( radians( 角度 ) )\ncos( radians( 角度 ) )\ntan( radians( 角度 ) )`}</CodeBlock>
      <h4 id='説明-38'>説明</h4>
      <ul>
      <li><p>サイン、コサイン、タンジェントなどの値を求める関数．</p>
      </li>
      <li><p>角度の値から周期的な値を求めることができる．</p>
      <ul>
      <li><p>波，振動</p></li>
      <li><p>特に <code>sin()</code>, <code>cos()</code>は必ず -1~1の間の実数を返すので，波形のような表現ができる．</p></li>

      </ul>
      </li>

      </ul>
      <h4 id='角度と計算結果の値の関係'>角度と計算結果の値の関係</h4>
      <figure><table>
      <thead>
      <tr><th>関数名</th><th>構文</th><th>返す値の範囲</th><th>角度と返す値の関係</th></tr></thead>
      <tbody><tr><td>正弦関数</td><td><code>sin( α )</code></td><td><code>-1～1</code>の間</td><td><img src={`${imageFolder}calv_var_sin_graph.png`} alt="calv_var_sin_graph" /></td></tr><tr><td>余弦関数</td><td><code>cos( α )</code></td><td><code>-1～1</code>の間</td><td><img src={`${imageFolder}calv_var_cos_graph.png`} alt="calv_var_cos_graph" /></td></tr><tr><td>正接関数</td><td><code>tan( α )</code></td><td><code>-∞～-∞</code> 90°等，値が定義できない場合がある．</td><td><img src={`${imageFolder}calv_var_tan_graph.png`} alt="calv_var_tan_graph" /></td></tr></tbody>
      </table></figure>
      <p><a href='https://processing.org/reference/sin_.html' target='_blank' >https://processing.org/reference/sin_.html</a></p>
      <p><a href='https://processing.org/reference/cos_.html' target='_blank' >https://processing.org/reference/cos_.html</a></p>
      <p><a href='https://processing.org/reference/tan_.html' target='_blank' >https://processing.org/reference/tan_.html</a></p>
      <h4 id='例１-4'>例１</h4>
      <p>並んだ正方形のY座標値に<code>sin()</code>を使った例</p>
      <img src={`${imageFolder}var_squares_sin.png`} alt="var_squares_sin" />
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nint iY = 25;\nint iOffsetY = 25;\nint iSize = 50;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;'}</CodeBlock>
      <h4 id='例２-4'>例２</h4>
      <p>並んだ正方形の塗色に<code>cos()</code>を使った例</p>
      <img src={`${imageFolder}var_squares_col_cos.png`} alt="var_squares_col_cos" />
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nint iY = 25;\nint iSize = 40;\n\ncolorMode( RGB, 2 );	// 最大値を2へ\n\nfill( 1 + sin( radians( iX ) ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( 1 + sin( radians( iX ) ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( 1 + sin( radians( iX ) ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( 1 + sin( radians( iX ) ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( 1 + sin( radians( iX ) ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( 1 + sin( radians( iX ) ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( 1 + sin( radians( iX ) ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( 1 + sin( radians( iX ) ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( 1 + sin( radians( iX ) ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( 1 + sin( radians( iX ) ) );\nsquare( iX, iY, iSize );\niX += iSize;'}</CodeBlock>
      <h2 id='h12'>演習６</h2>
      <p>例１を元に，塗色に三角関数に使い白黒のグラデーションをつける．</p>
      <img src={`${imageFolder}var_squares_sin_fill_gyaku.png`} alt="var_squares_sin_fill_gyaku" />
      <ol>
      <li><p>以下のプログラムをコピーする．</p>
      <img src={`${imageFolder}var_squares_sin.png`} alt="var_squares_sin" />
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nint iY = 25;\nint iOffsetY = 25;\nint iSize = 50;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;'}</CodeBlock>
      </li>
      <li><p><code>iY</code>を<code>iCenterY</code>で置換する．</p>
      <img src={`${imageFolder}var_squares_sin_chikan_cent.png`} alt="var_squares_sin_chikan_cent" />
      </li>
      <li><p>新しくY座標値用変数を作る．</p>
      <p><code>float fY = 0;</code></p>
      </li>
      <li><p>正方形描画命令の２つ目の引数を切り取り，変数<code>fY</code>に代入する．</p>
      <p><code>fY = iCenterY * sin( radians( iX ) ) + iOffsetY;</code></p>
      <p>この代入文は正方形描画命令の前の行に記述する．</p>
      </li>
      <li><p>ステップ4で切り取った部分には変数<code>fY</code>を記述する．</p>
      <p>以下，ここまででできたプログラム</p>
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nfloat fY = 0;\nint iCenterY = 25;\nint iOffsetY = 25;\nint iSize = 50;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nsquare( iX, fY, iSize );\niX += iSize;\n\nsquare( iX, iCenterY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iCenterY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iCenterY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iCenterY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iCenterY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iCenterY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;\n\nsquare( iX, iCenterY * sin( radians( iX ) ) + iOffsetY, iSize );\niX += iSize;'}</CodeBlock>
      </li>
      <li><p>他の正方形描画命令の２つ目の引数を<code>fY</code>で置換する．</p>
      <p>「次を探す」・「置換＆検索」を使って１つずつ置換する．</p>
      <img src={`${imageFolder}var_squares_sin_chikan_fy.png`} alt="var_squares_sin_chikan_fy" />
      </li>
      <li><p>ステップ4の文を他の正方形描画命令の前行にも記述する．</p>
      <p>以下，ここまででできたプログラム</p>
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nfloat fY = 0;\nint iCenterY = 25;\nint iOffsetY = 25;\nint iSize = 50;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nsquare( iX, fY, iSize );\niX += iSize;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nsquare( iX, fY, iSize );\niX += iSize;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nsquare( iX, fY, iSize );\niX += iSize;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nsquare( iX, fY, iSize );\niX += iSize;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nsquare( iX, fY, iSize );\niX += iSize;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nsquare( iX, fY, iSize );\niX += iSize;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nsquare( iX, fY, iSize );\niX += iSize;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nsquare( iX, fY, iSize );\niX += iSize;'}</CodeBlock>
      </li>
      <li><p>ステップ7までで，引数の中に記述していた式の値を変数<code>fY</code>に保存するように変更を行った．</p>
      </li>
      <li><p>全ての正方形描画命令の前に<code>fill( fY );</code>を追加してみる．</p>
      <img src={`${imageFolder}var_squares_sin_fill.png`} alt="var_squares_sin_fill" />
      </li>
      <li><p><code>fill( fY );</code>の引数が大きくなるよう式を組む．</p>
      <p>変数<code>fY</code>は0～50の間の値なので，このままだと暗い．</p>
      <img src={`${imageFolder}var_squares_sin_fill_5bai.png`} alt="var_squares_sin_fill_5bai" />
      </li>
      <li><p>スクリーン上のほうが正方形が白くなる方法を考えてみる．</p>
      <img src={`${imageFolder}var_squares_sin_fill_gyaku.png`} alt="var_squares_sin_fill_gyaku" />
      </li>
      <li><p>以下，完成プログラム</p>
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nfloat fY = 0;\nint iCenterY = 25;\nint iOffsetY = 25;\nint iSize = 50;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nfill( 255 - fY * 5 );\nsquare( iX, fY, iSize );\niX += iSize;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nfill( 255 - fY * 5 );\nsquare( iX, fY, iSize );\niX += iSize;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nfill( 255 - fY * 5 );\nsquare( iX, fY, iSize );\niX += iSize;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nfill( 255 - fY * 5 );\nsquare( iX, fY, iSize );\niX += iSize;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nfill( 255 - fY * 5 );\nsquare( iX, fY, iSize );\niX += iSize;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nfill( 255 - fY * 5 );\nsquare( iX, fY, iSize );\niX += iSize;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nfill( 255 - fY * 5 );\nsquare( iX, fY, iSize );\niX += iSize;\n\nfY = iCenterY * sin( radians( iX ) ) + iOffsetY;\nfill( 255 - fY * 5 );\nsquare( iX, fY, iSize );\niX += iSize;'}</CodeBlock>
      </li>

      </ol>
      <h2 id='h13'>乱数を用いた表現</h2>
      <h3 id='乱数を得る'>乱数を得る</h3>
      <p><strong><code>random()</code></strong></p>
      <h4 id='構文-46'>構文</h4>
      <p><code>random( 最大値 )</code></p>
      <p><code>random( 最小値, 最大値 )</code></p>
      <h4 id='説明-39'>説明</h4>
      <ul>
      <li><p>乱数を生成する命令．</p>
      <ul>
      <li><p>乱数とは，ランダムな値のことをいう．</p></li>

      </ul>
      </li>
      <li><p>この命令を実行する度に，新たなランダム値を返す．</p>
      <ul>
      <li><p>あるタイミングで生成した乱数を再度使いたい場合，変数に保存しておく必要がある．</p></li>

      </ul>
      </li>
      <li><p>この乱数をうまく組み合わせることで，人間の手作業では「揺れ幅」を表現できる．</p>
      <ul>
      <li><p>効果的に使用するには，後日学習する「繰り返し」の習得が必要．</p></li>

      </ul>
      </li>

      </ul>
      <h4 id='引数-29'>引数</h4>
      <ul>
      <li><p>得られるランダム値の最大値</p></li>
      <li><p>得られるランダム値の最小値と最大値</p></li>

      </ul>
      <h4 id='返り値-1'>返り値</h4>
      <p>ランダムな<code>float</code>値</p>
      <p><a href='https://processing.org/reference/random_.html' target='_blank' >https://processing.org/reference/random_.html</a></p>
      <h4 id='例１-5'>例１</h4>
      <p>正方形の並びのY座標値をランダムな値にする．</p>
      <img src={`${imageFolder}var_squares_randomy.png`} alt="var_squares_randomy" />
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nint iMaxY = 50;\nint iSize = 50;\n\nsquare( iX, random( 0, iMaxY ), iSize );\niX += iSize;\n\nsquare( iX, random( 0, iMaxY ), iSize );\niX += iSize;\n\nsquare( iX, random( 0, iMaxY ), iSize );\niX += iSize;\n\nsquare( iX, random( 0, iMaxY ), iSize );\niX += iSize;\n\nsquare( iX, random( 0, iMaxY ), iSize );\niX += iSize;\n\nsquare( iX, random( 0, iMaxY ), iSize );\niX += iSize;\n\nsquare( iX, random( 0, iMaxY ), iSize );\niX += iSize;\n\nsquare( iX, random( 0, iMaxY ), iSize );\niX += iSize;'}</CodeBlock>
      <h4 id='例２-5'>例２</h4>
      <p>並んだ正方形の塗色をランダムなグレー値にする．</p>
      <img src={`${imageFolder}var_squares_randomcol.png`} alt="var_squares_randomcol" />
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nint iY = 25;\nint iSize = 50;\n\nfill( random( 255 ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( random( 255 ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( random( 255 ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( random( 255 ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( random( 255 ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( random( 255 ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( random( 255 ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( random( 255 ) );\nsquare( iX, iY, iSize );\niX += iSize;'}</CodeBlock>
      <h2 id='h14'>演習７</h2>
      <p>色のブレがあるストライプ柄を作る．</p>
      <img src={`${imageFolder}var_squares_randomcol_bure.png`} alt="var_squares_randomcol_bure" />
      <p>条件</p>
      <ul>
      <li><p>例2のプログラムを参考に，色のブレがあるストライプ柄を自由に作成する．</p>
      </li>
      <li><p>例として，完成にランダムな塗色の指定は以下のような記述になる．</p>
      <p><code>fill( random( 255 ), random( 255 ), random( 255 ) );</code></p>
      </li>
      <li><p>演習５のように剰余算を使う必要はない．</p>
      </li>
      <li><p>２種類の<code>fill()</code>命令文を交互に記述する．</p>
      </li>
      <li><p>下の完成プログラムで使用している<code>fill()</code>命令文の例</p>
      <p><code>fill( random( 200, 255 ), random( 40, 50 ), random( 30, 50 ) );		// 濃い色</code></p>
      <p><code>fill( random( 250, 255 ), random( 240, 250 ), random( 240, 250 ) );	// 薄い色</code></p>
      </li>
      <li><p>以下，完成プログラム</p>
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nint iY = 25;\nint iSize = 50;\n\nfill( random( 200, 255 ), random( 40, 50 ), random( 30, 50 ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( random( 250, 255 ), random( 240, 250 ), random( 240, 250 ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( random( 200, 255 ), random( 40, 50 ), random( 30, 50 ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( random( 250, 255 ), random( 240, 250 ), random( 240, 250 ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( random( 200, 255 ), random( 40, 50 ), random( 30, 50 ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( random( 250, 255 ), random( 240, 250 ), random( 240, 250 ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( random( 200, 255 ), random( 40, 50 ), random( 30, 50 ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( random( 250, 255 ), random( 240, 250 ), random( 240, 250 ) );\nsquare( iX, iY, iSize );\niX += iSize;'}</CodeBlock>
      </li>

      </ul>
      <h2 id='h15'>べき乗の計算を行う命令</h2>
      <p><strong><code>pow()</code></strong></p>
      <h4 id='構文-47'>構文</h4>
      <p><code>pow( 基数, 指数 )</code></p>
      <h4 id='説明-40'>説明</h4>
      <ul>
      <li><p>べき乗の計算を行い，計算結果の値を返す命令．</p>
      </li>
      <li><p>指数関数ともいう．</p>
      <ul>
      <li><p>俗にいう「指数的に増加する」</p></li>
      <li><p>指数を増加していくと，急激に返り値が増加するようになる．</p></li>

      </ul>
      </li>
      <li><p>べき乗とは</p>
      <ul>
      <li><p>基数を指数の回数だけ掛け合わせる計算</p>
      <img src={`${imageFolder}shisuu_kansuu.png`} alt="shisuu_kansuu" />
      </li>
      <li><p>以下，基数：3, 指数：5の例</p>
      </li>

      </ul>
      </li>

      </ul>
      <h4 id='引数-30'>引数</h4>
      <p>基数，指数</p>
      <h4 id='返り値-2'>返り値</h4>
      <p>べき乗の計算結果の<code>float</code>型の値</p>
      <h4 id='例１-6'>例１</h4>
      <p>正方形のY座標値をべき乗の計算で大きくしていく例
      変数<code>iX</code>を指数としている．</p>
      <img src={`${imageFolder}var_squares_pow_y.png`} alt="var_squares_pow_y" />
      <CodeBlock>{'size( 400, 200 );\n\nint iX = 0;\nint iSize = 50;\nfloat fN = 1.014;  // 基数\n\nsquare( iX, pow( fN, iX ), iSize );\niX += iSize;\n\nsquare( iX, pow( fN, iX ), iSize );\niX += iSize;\n\nsquare( iX, pow( fN, iX ), iSize );\niX += iSize;\n\nsquare( iX, pow( fN, iX ), iSize );\niX += iSize;\n\nsquare( iX, pow( fN, iX ), iSize );\niX += iSize;\n\nsquare( iX, pow( fN, iX ), iSize );\niX += iSize;\n\nsquare( iX, pow( fN, iX ), iSize );\niX += iSize;\n\nsquare( iX, pow( fN, iX ), iSize );\niX += iSize;'}</CodeBlock>
      <h4 id='例２-6'>例２</h4>
      <p>正方形の塗色をべき乗の計算で明るくしていく例
      変数<code>iX</code>を指数としている．</p>
      <img src={`${imageFolder}var_squares_pow_col.png`} alt="var_squares_pow_col" />
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nint iY = 25;\nint iSize = 50;\nfloat fN = 1.017;	// 基数\n\nfill( pow( fN, iX ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( pow( fN, iX ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( pow( fN, iX ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( pow( fN, iX ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( pow( fN, iX ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( pow( fN, iX ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( pow( fN, iX ) );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( pow( fN, iX ) );\nsquare( iX, iY, iSize );\niX += iSize;'}</CodeBlock>
      <p><a href='https://processing.org/reference/pow_.html' target='_blank' >https://processing.org/reference/pow_.html</a></p>
      <h2 id='h16'>演習８</h2>
      <ol>
      <li><p>例２のプログラムをコピーする</p>
      </li>
      <li><p>基数を値として保存する変数<code>fN</code>の初期値を増やして実行してみる．</p>
      </li>
      <li><p>正方形の色が急激に明るくなることを確認する．</p>
      <img src={`${imageFolder}var_squares_pow_col_zouka.png`} alt="var_squares_pow_col_zouka" />
      </li>

      </ol>
      <h2 id='h17'>その他の数学関連の命令</h2>
      <h3 id='平方根の計算を行う命令'>平方根の計算を行う命令</h3>
      <p><strong><code>sqrt()</code></strong></p>
      <h4 id='構文-48'>構文</h4>
      <p><code>sqrt( 平方根を求める数値 )</code></p>
      <h4 id='説明-41'>説明</h4>
      <ul>
      <li><p>２乗すると引数の値となる値の計算を行い，計算結果の値を返す命令．</p>
      </li>
      <li><p>平方根とは</p>
      <ul>
      <li><p>ある数値を正方形の面積とし，それを元に一辺の長さを求める計算．</p></li>

      </ul>
      <img src={`${imageFolder}root_func.png`} alt="root_func" />
      </li>

      </ul>
      <img src={`${imageFolder}sqrt_graph.png`} alt="sqrt_graph" />
      <h4 id='引数-31'>引数</h4>
      <p>平方根を求める数値</p>
      <h4 id='例１-7'>例１</h4>
      <p>正方形のY座標値に<code>sqrt()</code>を使用した例</p>
      <img src={`${imageFolder}var_squares_sqrt_y.png`} alt="var_squares_sqrt_y" />
      <CodeBlock>{'size( 400, 200 );\n\nint iX = 0;\nint iSize = 50;\n\nsquare( iX, sqrt( iX ) * 8, iSize );\niX += iSize;\n\nsquare( iX, sqrt( iX ) * 8, iSize );\niX += iSize;\n\nsquare( iX, sqrt( iX ) * 8, iSize );\niX += iSize;\n\nsquare( iX, sqrt( iX ) * 8, iSize );\niX += iSize;\n\nsquare( iX, sqrt( iX ) * 8, iSize );\niX += iSize;\n\nsquare( iX, sqrt( iX ) * 8, iSize );\niX += iSize;\n\nsquare( iX, sqrt( iX ) * 8, iSize );\niX += iSize;\n\nsquare( iX, sqrt( iX ) * 8, iSize );\niX += iSize;'}</CodeBlock>
      <p><a href='https://processing.org/reference/sqrt_.html' target='_blank' >https://processing.org/reference/sqrt_.html</a></p>
      <h4 id='例２-7'>例２</h4>
      <p>正方形の塗色に<code>sqrt()</code>を使用した例</p>
      <img src={`${imageFolder}var_squares_sqrt_col.png`} alt="var_squares_sqrt_col" />
      <CodeBlock>{'size( 400, 100 );\n\nint iX = 0;\nint iY = 25;\nint iSize = 50;\n\nfill( sqrt( iX ) * 10 );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( sqrt( iX ) * 10 );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( sqrt( iX ) * 10 );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( sqrt( iX ) * 10 );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( sqrt( iX ) * 10 );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( sqrt( iX ) * 10 );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( sqrt( iX ) * 10 );\nsquare( iX, iY, iSize );\niX += iSize;\n\nfill( sqrt( iX ) * 10 );\nsquare( iX, iY, iSize );\niX += iSize;'}</CodeBlock>
      <h3 id='絶対値を求める'>絶対値を求める</h3>
      <p><strong><code>abs()</code></strong></p>
      <h4 id='構文-49'>構文</h4>
      <p><code>abs( 絶対値を求める数値 )</code></p>
      <h4 id='説明-42'>説明</h4>
      <ul>
      <li><p>正（プラス）の値はそのままの値を返す．</p></li>
      <li><p>負（マイナス）の値はマイナスが無視された値を返す．</p></li>

      </ul>
      <h4 id='引数-32'>引数</h4>
      <p>絶対値を求める数値</p>
      <h4 id='例-22'>例</h4>
      <CodeBlock>{'print( abs(-16) );   // コンソールに 16 が表示される．'}</CodeBlock>
      <p><a href='https://processing.org/reference/abs_.html' target='_blank' >https://processing.org/reference/abs_.html</a>
      <a href='https://processing.org/reference/ceil_.html' target='_blank' >https://processing.org/reference/ceil_.html</a>
      <a href='https://processing.org/reference/floor_.html' target='_blank' >https://processing.org/reference/floor_.html</a>
      <a href='https://processing.org/reference/round_.html' target='_blank' >https://processing.org/reference/round_.html</a></p>
      <h2 id='h18'>型の変換</h2>
      <ul>
      <li><p><strong>整数型(int)と実数型(float)の値を同一の式の中に混合するケース</strong>によく使う．</p></li>
      <li><p>できればこちらではなく，<code>floor()</code>, <code>ceil()</code>, <code>round()</code>を使うほうが好ましい．</p></li>
      <li><p>慣れないうちはどうしてもエラーが発生したり，意図しない挙動を起こしてしまうので，適切に対処する必要がある．</p></li>

      </ul>
      <h3 id='小数点以下を切り上げる'>小数点以下を切り上げる．</h3>
      <p><strong><code>ceil()</code></strong></p>
      <h4 id='構文-50'>構文</h4>
      <p><code>ceil( 数値 )</code></p>
      <h4 id='例1-13'>例1</h4>
      <CodeBlock>{'print( ceil(8.22) );\nprint(iB);'}</CodeBlock>
      <h4 id='例2-13'>例2</h4>
      <p><strong>float型の値をint型の値に変換する</strong>ために用いることができる．</p>
      <CodeBlock>{'float fA = 123.456;   // 実数型の変数fA\n\nint iB = ceil(fA);    // 整数型の変数iBに計算結果を代入．\n\nprint(iB);   // コンソールに 124 が表示される．'}</CodeBlock>
      <h3 id='小数点以下を切り捨てる'>小数点以下を切り捨てる．</h3>
      <p><strong><code>floor()</code></strong></p>
      <h4 id='構文-51'>構文</h4>
      <p><code>floor( 数値 )</code></p>
      <h4 id='例1-14'>例1</h4>
      <CodeBlock>{'print( floor(2.88) );   // コンソールに 2 が表示される．'}</CodeBlock>
      <h4 id='例2-14'>例2</h4>
      <p><strong>float型の値をint型の値に変換する</strong>ために用いることができる．</p>
      <CodeBlock>{'float fA = 123.456;    // 実数型の変数fA\n\nint iB = floor(fA);    // 整数型の変数iBに計算結果を代入．\n\nprint(iB);   // コンソールに 123 が表示される．'}</CodeBlock>
      <h3 id='小数点以下を四捨五入する'>小数点以下を四捨五入する．</h3>
      <p><strong><code>round()</code></strong></p>
      <h4 id='構文-52'>構文</h4>
      <p><code>round( 数値 )</code></p>
      <h4 id='例1-15'>例1</h4>
      <CodeBlock>{'print( round(9.2) );   // コンソールに 9 が表示される．\nprint( round(9.5) );   // コンソールに 10 が表示される．\nprint( round(9.9) );   // コンソールに 10 が表示される．'}</CodeBlock>
      <p><strong>float型の値をint型の値に変換する</strong>ために用いることができる．</p>
      <h4 id='例2-15'>例2</h4>
      <CodeBlock>{'float fA = 123.456;    // 実数型の変数fA\n\nint iB = round(fA);    // 整数型の変数iBに計算結果を代入．\n\nprint(iB);   // コンソールに 123 が表示される．'}</CodeBlock>
      <h3 id='int-2'>int()</h3>
      <p>整数型に変換する．
      小数点以下は切り捨てられる．</p>
      <h4 id='構文-53'>構文</h4>
      <p><code>int( 変換するfloat型変数や値 )</code></p>
      <h4 id='例-23'>例</h4>
      <CodeBlock>{'float   fA = 1.234;\nint     iA = int(5/fA);	// iAに代入する直前に整数型に変換'}</CodeBlock>
      <h3 id='float-2'>float()</h3>
      <p>実数型に変換する．</p>
      <h4 id='構文-54'>構文</h4>
      <p><code>float( 変換するint型変数や値 )</code></p>
      <h4 id='例-24'>例</h4>
      <p>変換しなかった場合，計算結果の小数点以下が切り捨てられてしまう．</p>
      <CodeBlock>{'float   fA = 10 / float(width);	// widthはint型のシステム変数なので変換が必要'}</CodeBlock>
      <p>変数でなく，数値の場合以下のような書き方もできる</p>
      <CodeBlock>{'float   fA = 10 / float(1000);'}</CodeBlock>
      <CodeBlock>{'float   fA = 10 / 1000.0;	// 型変換を行わず，このような書き方もできる'}</CodeBlock>
      <h2 id='h19'>システム変数</h2>
      <ul>
      <li><p>Processingが事前に用意している変数．</p></li>
      <li><p>ユーザーが値を代入（保存）することはできない．</p></li>

      </ul>
      <h3 id='画面のサイズを取得する'>画面のサイズを取得する</h3>
      <h4 id='横のピクセル数'>横のピクセル数</h4>
      <p><strong><code>width</code></strong></p>
      <h4 id='縦のピクセル数'>縦のピクセル数</h4>
      <p><strong><code>height</code></strong></p>
      <h4 id='説明-43'>説明</h4>
      <ul>
      <li><p>命令ではなく，変数と呼ばれるもの．</p>
      <ul>
      <li><p>カッコ「()」が後ろに付かないことに注意．</p></li>

      </ul>
      </li>
      <li><p>必ず<code>size()</code>記述以後に記述する必要がある．</p>
      </li>
      <li><p><code>size()</code> 命令以降に，画面サイズの<u>数値の代わりに</u> <code>width</code>, <code>height</code> を用いることができる．</p>
      <ul>
      <li><p>同じ数値を繰り返し記述しなくてよい利点がある</p></li>

      </ul>
      </li>

      </ul>
      <img src={`${imageFolder}transform_origin01.png`} alt="transform_origin01" />
      <h4 id='例1-16'>例1</h4>
      <p>スクリーンの角から対角までの線を描画する．</p>
      <img src={`${imageFolder}window_size.png`} alt="window_size" />
      <CodeBlock>{'size(400,400);\n\n// スクリーン右下へ線を描画する．\nline(0, 0, width, height);'}</CodeBlock>
      <h4 id='例2-16'>例2</h4>
      <ul>
      <li><p><code>width</code>, <code>height</code> を用いることで，スクリーンの大きさに比例した図形描画を行うことができる．</p></li>
      <li><p>下の例では幅，高さともにスクリーンの２分の１の大きさの矩形（四角形）を描画している．</p></li>

      </ul>
      <img src={`${imageFolder}size_rect.png`} alt="size_rect" />
      <CodeBlock>{'size(600,400);\n\n// 幅: 1/2, 高さ: 1/2 の矩形を描画する.\nrect(0, 0, width/2, height/2);'}</CodeBlock>
      <p><a href='https://processing.org/reference/width.html' target='_blank' >https://processing.org/reference/width.html</a>
      <a href='https://processing.org/reference/height.html' target='_blank' >https://processing.org/reference/height.html</a></p>
      <h2 id='h20'>定数</h2>
      <h3 id='final'>final</h3>
      <h4 id='構文-55'>構文</h4>
      <p><code>final 変数型 定数名 = 初期値;</code></p>
      <h4 id='説明-44'>説明</h4>
      <ul>
      <li><p><u>変数の宣言文の先頭に記述</u>することで，変数ではなく定数を定義できる．</p>
      </li>
      <li><p>定数とは値が変化しない変数のこと．</p>
      <ul>
      <li><p>定数は値の代入はできない．</p></li>
      <li><p>変化させたくない固定のパラメータなどに用いる．</p></li>

      </ul>
      </li>

      </ul>
      <h4 id='例-25'>例</h4>
      <p>矩形の大きさの引数に用いた例．
      変数と区別しやすくするため，定数名を大文字のみで記述するなど工夫をすることを推奨．</p>
      <CodeBlock>{'final float RECT_WIDTH = 400;\nfinal float RECT_HEIGHT = 200;\n\nrect( 0,0, RECT_WIDTH, RECT_HEIGHT );'}</CodeBlock>
      <p><a href='https://processing.org/reference/final.html' target='_blank' >https://processing.org/reference/final.html</a></p>
    </div>
  );
};

export default Programming03;
