// Programming07.tsx

import React, { useEffect} from 'react';
import { useLocation } from 'react-router-dom';
import { LinkP, JumpToDefaultScroll, SketchCode, CodeBlock, Img } from '../ContentCommon';
import { MathJaxProvider, MathJaxFormula } from 'mathjax3-react';
import '../ContentCommon.css';

const imageFolder: string = "/musabi-react/images/programming/";

interface ContentProps {
  practiceId: string;
}

const Programming07: React.FC<ContentProps> = ({ practiceId }) => {

  /* URLから初期スクロール位置 */
  const location = useLocation();
  useEffect(() => {
    JumpToDefaultScroll(practiceId, location as any );    
  }, [practiceId,location]);
  return (
    <div className="common-style">
      <h1 id='h0'>第７回：関数の作成</h1>
        <h2 id='関数を作成する理由'>関数を作成する理由</h2>
        <ol>
        <li><p>プログラムを見やすくする．</p>
        <p>特に<code>draw(){}</code>ブロックが長くなり，混乱することを防ぐ．</p>
        </li>
        <li><p>同じような処理を共通化する．</p>
        <p>引数が異なるだけで命令文が重複する箇所などを関数で共通化し，プログラム全体の長さと効率を改善する．</p>
        </li>
        <li><p>役割によってプログラムを分け，１つの機能の作成に集中しやすくする．</p>
        <p>特に<code>draw(){}</code>ブロック内に記述される「図形描画」「アニメーションの更新」など役割でプログラムを分け，それぞれの機能を作成する際に，他の処理を意識しなくてよいようにする．</p>
        </li>

        </ol>
        <h2 id='h1'>描画命令文をまとめる関数</h2>
        <p>４つの図形描画を行う関数<code>draw4Primitives()</code></p>
        <Img src={`${imageFolder}func_4primitives.gif`} />
        <CodeBlock>{'int iX = 0;\n\nvoid setup()\n{\n  size( 400, 200 );\n}\n\nvoid draw()\n{ \n  background( 204 );\n  \n  translate( iX, 80 );\n  \n  draw4Primitives();\n  \n  iX++;\n  if( iX &gt; width )\n  {\n    iX = 0;\n  }\n}\n\nvoid draw4Primitives()\n{\n  square( 0, 0, 20 );\n  triangle( 30, 0, 20, 20, 40, 20 );\n  circle( 30, 30, 20 );\n  quad( 10, 20, 20, 30, 10, 40, 0, 30 );\n}'}</CodeBlock>
        <h3 id='関数-function-とは'>関数( function )とは</h3>
        <ul>
        <li><p>ある複数の（命令）文が実行される機能のこと</p>
        </li>
        <li><p>Processingにあらかじめ用意されているシステム関数</p>
        <p><code>size(), colorMode(), point(), rect(), ellipse(), random(), sin(), cos(), tan()...</code></p>
        </li>
        <li><p><code>()</code>が後ろに付くものはほぼ全て関数であると考えてよい．</p>
        </li>
        <li><p>逆に，関数ではないも</p>
        <p><code>for(), if(), else, else if(), switch() ...</code></p>
        </li>

        </ul>
        <h3 id='関数の呼び出し'>関数の呼び出し</h3>
        <ul>
        <li><p>関数を使用することを，関数を呼び出す（ <strong>コール</strong>する）という．</p></li>
        <li><p>関数をコールすることで<u>関数の定義</u>に記述されている処理が実行される．</p></li>

        </ul>
        <Img src={`${imageFolder}func_call.jpg`} />
        <h3 id='関数の定義'>関数の定義</h3>
        <ul>
        <li><p>関数がどのような処理をおこなうか記述すること．</p>
        </li>
        <li><p>関数の定義を行うことで，独自の関数を作ることができる．</p>
        <ul>
        <li><p>システム関数は既にProcessing側で定義済み</p></li>

        </ul>
        </li>

        </ul>
        <Img src={`${imageFolder}func_define.jpg`} />
        <h4 id='関数定義の構文'>関数定義の構文</h4>
        <ul>
        <li><p>引数が無く，返り値もない最もシンプルな関数定義の構文</p></li>
        <li><p>関数名は任意の名前を記述できる．</p></li>

        </ul>
        <CodeBlock>{'void 関数名()\n{\n  // 命令(文);\n}'}</CodeBlock>
        <h4 id='関数定義の記述場所'>関数定義の記述場所</h4>
        <p>関数定義は，<code>setup(){}</code>, <code>draw(){}</code>のブロック外に書く．
        通常は，プログラムの後ろに記述することを推奨．</p>
        <CodeBlock>{'void setup()\n{\n}\n\nvoid draw()\n{\n  myFunc();\n}\n\nvoid myFunc()\n{\n\n}'}</CodeBlock>
        <h3 id='例１-30'>例１</h3>
        <p>for文で繰り返し正方形描画を行う関数<code>drawSquares()</code></p>
        <Img src={`${imageFolder}func_squares.gif`} />
        <CodeBlock>{'int iX = 0;\n\nvoid setup()\n{\n  size( 400, 200 );\n}\n\nvoid draw()\n{ \n  background( 204 );\n  \n  drawSquares();\n  \n  iX++;\n  if( iX &gt; width )\n  {\n    iX = 0;\n  }\n}\n\nvoid drawSquares()\n{\n  rectMode( CENTER );\n  for( int iSquare = 0; iSquare &lt; 4; iSquare++ )\n  {\n    square( iX, 100, 40 - 6 * iSquare );\n  }\n}'}</CodeBlock>
        <h3 id='例２-29'>例２</h3>
        <p>正方形を円状に描画する <code>drawSquares()</code></p>
        <Img src={`${imageFolder}anim_squares_rot12.gif`} />
        <CodeBlock>{'int iShapeNum = 1;  // 描画する図形の数（1~12）\n\nvoid setup()\n{\n  size( 400, 400 );\n  frameRate( 4 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  translate( 200, 200 );\n  \n  drawSquares();\n  \n  iShapeNum++;\n  if( iShapeNum &gt; 12 )\n  {\n    iShapeNum = 1;\n  }\n}\n\nvoid drawSquares()\n{\n  rectMode( CENTER );\n  for ( int iShape = 0; iShape &lt; iShapeNum; iShape++ )\n  {\n    square( 0, -100, 42 );\n    rotate( radians( 30 ) );\n  }\n}'}</CodeBlock>
        <h3 id='例３-16'>例３</h3>
        <p>雨の描画を行う関数<code>drawRain()</code></p>
        <Img src={`${imageFolder}func_rain_circle.gif`} />
        <CodeBlock>{'// 100個の雨粒の情報\n// [0]:X座標値，[1]:Y座標値，[2]:スピード\nfloat[][] fAmeXYS = new float[100][3];\n\nint iX = 0;\n\nvoid setup()\n{\n  size( 400, 400 );\n  frameRate( 30 );\n  \n  // 雨粒の初期化\n  for( int iAmeIdx=0; iAmeIdx &lt; fAmeXYS.length; iAmeIdx++ )\n  {\n    fAmeXYS[iAmeIdx][0] = random( width );     // X座標値\n    fAmeXYS[iAmeIdx][1] = random( height );    // Y座標値\n    fAmeXYS[iAmeIdx][2] = random( 10, 20 );    // 雨粒のスピード\n  }\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  fill( 255 );\n  circle( iX, 300, 100 );\n    \n  drawRain();\n  \n  iX++;\n  if( iX &gt; width )\n  {\n    iX = 0;\n  }\n}\n\nvoid drawRain()\n{\n  pushMatrix();\n  \n  // 100個の雨粒のアニメーション  \n  rotate( radians( 10 ) );  // 雨の角度\n  translate( 30, -50 );     // 調整\n  for( int iAmeIdx=0; iAmeIdx &lt; fAmeXYS.length; iAmeIdx++ )\n  {\n    fill( 0 );\n    \n    // アニメーション後のY座標値．\n    fAmeXYS[iAmeIdx][1] += fAmeXYS[iAmeIdx][2];\n    fAmeXYS[iAmeIdx][1] %= height;  // 550超えたら0から繰り返す．\n    \n    // 雨粒の描画\n    line( fAmeXYS[iAmeIdx][0], fAmeXYS[iAmeIdx][1], fAmeXYS[iAmeIdx][0], fAmeXYS[iAmeIdx][1] + 40 );\n  }\n  \n  popMatrix();\n}'}</CodeBlock>
        <h2 id='h2'>演習１</h2>
        <p>以下のプログラムをコピーし，描画命令文を分けて関数化する．
        関数名は<code>draw4Primitives()</code>とする．</p>
        <Img src={`${imageFolder}func_4primitives.gif`} />
        <CodeBlock>{'int iX = 0;\n\nvoid setup()\n{\n  size( 400, 200 );\n}\n\nvoid draw()\n{ \n  background( 204 );\n  \n  translate( iX, 80 );\n  \n  square( 0, 0, 20 );\n  triangle( 30, 0, 20, 20, 40, 20 );\n  circle( 30, 30, 20 );\n  quad( 10, 20, 20, 30, 10, 40, 0, 30 );\n  \n  iX++;\n  if( iX &gt; width )\n  {\n    iX = 0;\n  }\n}'}</CodeBlock>
        <h2 id='h3'>アニメーション用変数の更新関数</h2>
        <p>４つの図形描画を行う関数<code>draw4Primitives()</code>
        アニメーション用変数の更新関数：<code>update()</code></p>
        <Img src={`${imageFolder}func_4primitives.gif`} />
        <CodeBlock>{'int iX = 0;\n\nvoid setup()\n{\n  size( 400, 200 );\n}\n\nvoid draw()\n{ \n  background( 204 );\n  \n  translate( iX, 80 );\n  \n  draw4Primitives();\n  \n  update();\n}\n\nvoid draw4Primitives()\n{\n  square( 0, 0, 20 );\n  triangle( 30, 0, 20, 20, 40, 20 );\n  circle( 30, 30, 20 );\n  quad( 10, 20, 20, 30, 10, 40, 0, 30 );\n}\n\nvoid update()\n{\n  iX++;\n  if( iX &gt; width )\n  {\n    iX = 0;\n  }\n}'}</CodeBlock>
        <h3 id='アニメーション更新用関数'>アニメーション更新用関数</h3>
        <ul>
        <li><p>アニメーションに用いる変数の更新関連の文はできるだけ関数にまとめるとよい．</p>
        <ul>
        <li><p><code>draw(){}</code>が見やすくなる．</p></li>

        </ul>
        </li>
        <li><p><code>update()</code>という関数名を使うのがよい．</p>
        </li>

        </ul>
        <Img src={`${imageFolder}func_update.jpg`} />
        <h3 id='例１-31'>例１</h3>
        <p>for文で繰り返し正方形描画を行う関数<code>drawSquares()</code>
        アニメーション用変数<code>iX</code>の更新を行う関数<code>update()</code></p>
        <Img src={`${imageFolder}func_squares.gif`} />
        <CodeBlock>{'int iX = 0;\n\nvoid setup()\n{\n  size( 400, 200 );\n}\n\nvoid draw()\n{ \n  background( 204 );\n  \n  drawSquares();\n  \n  update();\n}\n\nvoid drawSquares()\n{\n  rectMode( CENTER );\n  for( int iSquare = 0; iSquare &lt; 4; iSquare++ )\n  {\n    square( iX, 100, 40 - 6 * iSquare );\n  }\n}\n\nvoid update()\n{\n  iX++;\n  if( iX &gt; width )\n  {\n    iX = 0;\n  }\n}'}</CodeBlock>
        <h3 id='例２-30'>例２</h3>
        <p>正円のシーケンスアニメーションを行う関数<code>update()</code></p>
        <Img src={`${imageFolder}anim_sequence_simple.gif`} />
        <CodeBlock>{'int iFrame = 0;\nfloat fX = 0;\nfloat fY = 100;\n\nvoid setup()\n{\n  size( 200, 200 );\n}\n\nvoid draw()\n{\n  background( 204 );\n    \n  circle( fX, fY, 30 );\n  \n  update();\n}\n\nvoid update()\n{\n  if( iFrame &lt; 30 )       // 右へ\n  {\n    fX += 3;\n    iFrame++;\n  }\n  else if( iFrame &lt; 70 )  // 右上へ\n  {\n    fX += 1;\n    fY -= 1;\n    iFrame++;\n  }\n  else if( iFrame &lt; 100 )  // 下へ\n  {\n    fY += 7;\n    iFrame++;\n  }\n  else                    // アニメリセット\n  {\n    // 初期位置へ\n    fX = 0;\n    fY = 100;\n    iFrame = 0;\n  }\n}'}</CodeBlock>
        <h3 id='例３-17'>例３</h3>
        <p>円弧+正円のシーケンスアニメーションを行う関数<code>update()</code>
        円弧+正円の描画を行う関数<code>drawBB8()</code></p>
        <Img src={`${imageFolder}func_bb8.gif`} />
        <CodeBlock>{'int iFrame = 0;\nfloat fX = 0;\nfloat fY = 120;\n\nvoid setup()\n{\n  size( 400, 200 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  drawBB8();\n  \n  update();\n}\n\nvoid drawBB8()\n{\n  pushMatrix();\n  \n  translate( fX, fY );\n  \n  circle( 0, 0, 40 );\n  arc( 0, -20, 28, 28, radians( -180 ), radians( 0 ), PIE );\n  \n  popMatrix();\n}\n\nvoid update()\n{\n  if( iFrame &lt; 50 )       // 右へ\n  {\n    fX += 3;\n    iFrame++;\n  }\n  else if( iFrame &lt; 140 )  // ゆっくり右へ\n  {\n    fX += 1;\n    iFrame++;\n  }\n  else if( iFrame &lt; 200 )  // 右へ\n  {\n    fX += 4;\n    iFrame++;\n  }\n  else                    // アニメリセット\n  {\n    // 初期位置へ\n    fX = 0;\n    fY = 120;\n    iFrame = 0;\n  }\n}'}</CodeBlock>
        <h2 id='h4'>演習２</h2>
        <p>例３のプログラムに回転アニメーション用のグローバル変数を１つ追加し，その変数を使って下図のような回転アニメーションを加える．回転アニメーションは全く同じである必要はない．</p>
        <Img src={`${imageFolder}func_bb8_rot.gif`} />
        <p>以下，作例</p>
        <CodeBlock>{'int iFrame = 0;\nfloat fX = 0;\nfloat fY = 120;\nint iDeg = 0;\n\nvoid setup()\n{\n  size( 400, 200 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  drawBB8();\n  \n  update();\n}\n\nvoid drawBB8()\n{\n  pushMatrix();\n  \n  translate( fX, fY );\n  rotate( radians( iDeg ) );\n  \n  circle( 0, 0, 40 );\n  arc( 0, -20, 28, 28, radians( -180 ), radians( 0 ), PIE );\n  \n  popMatrix();\n}\n\nvoid update()\n{\n  if( iFrame &lt; 50 )       // 右へ\n  {\n    fX += 3;\n    iDeg += 1;\n    iFrame++;\n  }\n  else if( iFrame &lt; 140 )  // ゆっくり右へ\n  {\n    fX += 1;\n    iDeg -= 1;\n    iFrame++;\n  }\n  else if( iFrame &lt; 200 )  // 右へ\n  {\n    fX += 4;\n    iFrame++;\n  }\n  else                    // アニメリセット\n  {\n    // 初期位置へ\n    fX = 0;\n    fY = 100;\n    iFrame = 0;\n    iDeg = 0;\n  }\n}'}</CodeBlock>
        <h2 id='h5'>独自の図形描画関数を作る．</h2>
        <p>六角形を描画する関数<code>drawHexagon()</code>
        時分秒ごとに大きさが変わる六角形を描画する．</p>
        <Img src={`${imageFolder}func_hexagon.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 400, 200 );\n}\n\nvoid draw()\n{ \n  background( 204 );\n  \n  drawHexagon( 70, 100, hour() + 20 );\n  drawHexagon( 200, 100, minute() + 20 );\n  drawHexagon( 330, 100, second() + 20 );\n}\n\nvoid drawHexagon( int iX, int iY, float fRadius )\n{\n  beginShape();\n  for( int iDeg = 0; iDeg &lt; 360; iDeg += 60 )\n  {\n    float fX = fRadius * cos( radians( iDeg ) );\n    float fY = fRadius * sin( radians( iDeg ) );\n    \n    vertex( fX + iX, fY + iY );\n  }\n  endShape( CLOSE );\n}'}</CodeBlock>
        <h3 id='引数のある関数のコール'>引数のある関数のコール</h3>
        <ul>
        <li><p>既存の図形描画関数（<code>square()</code>, <code>ellipse()</code>, <code>rect()</code>など）と同様に，自作の関数においても引数を使用できる．</p></li>
        <li><p>関数コールにおいて記述する引数のことを<strong>実引数</strong>という．</p></li>

        </ul>
        <Img src={`${imageFolder}func_hexagon_call.jpg`} />
        <h3 id='引数のある関数の定義'>引数のある関数の定義</h3>
        <ul>
        <li><p><code>()</code>内に<strong>仮引数</strong>という変数の宣言を記述する．</p>
        </li>
        <li><p><u>仮引数は関数をコールしたときに実引数の値を受け取る</u>ための変数．</p>
        <p>そのため，初期値を代入しない．</p>
        </li>
        <li><p>仮引数は関数のブロック内でのみ使用できる．</p>
        </li>

        </ul>
        <Img src={`${imageFolder}func_hexagon_define.jpg`} />
        <h4 id='返り値のある関数定義の構文'>返り値のある関数定義の構文</h4>
        <p>仮引数は<code>,</code>区切りで複数記述できる．
        受け取る情報の数だけ仮引数を宣言する．</p>
        <CodeBlock>{'void 関数名( 引数の型 仮引数 )\n{\n  // 命令(文);\n}'}</CodeBlock>
        <h3 id='実引数と仮引数のおさらい'>実引数と仮引数のおさらい</h3>
        <ul>
        <li><p>実引数</p>
        <ul>
        <li><p>関数コールの際に記述する引数のこと．</p></li>
        <li><p>数値などの具体的な情報</p></li>

        </ul>
        </li>
        <li><p>仮引数</p>
        <ul>
        <li><p>関数定義時に記述する引数のこと．</p></li>
        <li><p>関数コール時に渡された実引数の値を受け取る．</p></li>
        <li><p>仮引数は関数内のみ使用できる．</p></li>

        </ul>
        </li>

        </ul>
        <Img src={`${imageFolder}func_hexagon_parameters.jpg`} />
        <h3 id='例１-32'>例１</h3>
        <p>回転角度を指定して星型多角形を描画する関数<code>drawStar()</code></p>
        <Img src={`${imageFolder}anim_star_rot.gif`} />
        <CodeBlock>{'float fRadiusS = 40;  // 中心から頂点への距離（凹）\nfloat fRadiusL = 100; // 中心から頂点への距離（凸）\n\nint iRotDeg = 0;  // 回転アニメーション用\n\nvoid setup()\n{\n  size( 300, 300 );\n}\n\nvoid draw()\n{ \n  background( 204 );\n  \n  drawStar( 150, 150, iRotDeg  );\n  \n  iRotDeg++;\n  if( iRotDeg &gt; 360 )\n  {\n    iRotDeg = 0;\n  }\n}\n\n// iDeg：星の回転角度\nvoid drawStar( int iX, int iY, int iDeg )\n{\n  pushMatrix();\n  \n  translate( iX, iY );\n  rotate( radians( iDeg ) );\n    \n  beginShape();\n  for( int iVertexDeg = 0; iVertexDeg &lt; 360; iVertexDeg += 72 )\n  {\n    // 凹の頂点\n    float fSX = fRadiusS * cos( radians( iVertexDeg ) );\n    float fSY = fRadiusS * sin( radians( iVertexDeg ) );\n    \n    // 凸の頂点\n    float fLX = fRadiusL * cos( radians( iVertexDeg + 36 ) );\n    float fLY = fRadiusL * sin( radians( iVertexDeg + 36 ) );\n    \n    vertex( fSX, fSY );\n    vertex( fLX, fLY );\n  }\n  endShape( CLOSE );\n  \n  popMatrix();\n}'}</CodeBlock>
        <h3 id='例２-31'>例２</h3>
        <p>突起の数を指定してスパイク多角形を描画する関数<code>drawSpikeBall()</code></p>
        <Img src={`${imageFolder}func_uni.gif`} />
        <CodeBlock>{'float fRadiusS = 100; // 中心から頂点への距離（凹）\nfloat fRadiusL = 140; // 中心から頂点への距離（凸）\n\nint iRotDeg = 0;  // 回転アニメーション用\n\nvoid setup()\n{\n  size( 300, 300 );\n}\n\nvoid draw()\n{ \n  background( 204 );\n  \n  drawSpikeBall( 150, 150, iRotDeg, second() + 2  );\n  \n  iRotDeg++;\n  if( iRotDeg &gt; 360 )\n  {\n    iRotDeg = 0;\n  }\n}\n\n// iDeg：全体の回転角度\n// fNum：突起の数\nvoid drawSpikeBall( int iX, int iY, int iDeg, float fNum )\n{\n  pushMatrix();\n  \n  translate( iX, iY );\n  rotate( radians( iDeg ) );\n  \n  float fIntervalDeg = 180 / fNum;\n  \n  beginShape();\n  for( float fVertexDeg = 0; fVertexDeg &lt; 360; fVertexDeg += fIntervalDeg )\n  {\n    // 凸の頂点\n    float fLX = fRadiusL * cos( radians( fVertexDeg ) );\n    float fLY = fRadiusL * sin( radians( fVertexDeg ) );\n\n    fVertexDeg += fIntervalDeg;\n    \n    // 凹の頂点\n    float fSX = fRadiusS * cos( radians( fVertexDeg  ) );\n    float fSY = fRadiusS * sin( radians( fVertexDeg  ) );\n        \n    vertex( fLX, fLY );\n    vertex( fSX, fSY );\n  }\n  endShape( CLOSE );\n  \n  popMatrix();\n}'}</CodeBlock>
        <h3 id='例３-18'>例３</h3>
        <p>位置やスケール値を指定して顔のような模様を描画する<code>drawChiikawa()</code></p>
        <Img src={`${imageFolder}func_chiikawa.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 300, 200 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  drawChiikawa( 150, 100, 1 + second() / 60.0  );\n}\n\n// fScale：スケール値（1が等倍）\nvoid drawChiikawa( int iX, int iY, float fScale )\n{\n  pushMatrix();\n  \n  translate( iX, iY );\n  scale( fScale );\n  \n  strokeWeight( 2 );\n  \n  fill( 255 );\n  ellipse( 0, 0, 80, 70 );\n  arc( -23, -27, 14, 20, radians( -220 ), radians( -10 ) );\n  arc( 23, -27, 14, 20, radians( -170 ), radians( 40 ) );\n  \n  fill( 0 );\n  circle( -13, -2, 9 );\n  circle( 13, -2, 9 );\n  \n  line( -18, -12, -10, -13 );\n  line( 18, -12, 10, -13 );\n  \n  fill( 255 );\n  noStroke();\n  ellipse( -13, -4, 6, 5 );\n  ellipse( 13, -4, 6, 5 );\n\n  stroke( 0 );\n  arc( -3, 10, 6, 6, radians( 0 ), radians( 180 ) );\n  arc( 3, 10, 6, 6, radians( 0 ), radians( 180 ) );\n  \n  popMatrix();\n}'}</CodeBlock>
        <h3 id='例４-12'>例４</h3>
        <p>位置やHSB値を指定して顔のような模様を描画する<code>drawChiikawa()</code></p>
        <Img src={`${imageFolder}func_chiikawa_hsb.gif`} />
        <CodeBlock>{'int iX = 0;\nfloat fHSB[] = { 0, 0, 0 };\n\nvoid setup()\n{\n  size( 300, 200 );\n  // 色をランダム\n  fHSB[0] = random( 360 );\n  fHSB[1] = 100;\n  fHSB[2] = 100;\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  drawChiikawa( iX, 100, fHSB[0], fHSB[1], fHSB[2] );\n  \n  update();\n}\n\nvoid drawChiikawa( int iX, int iY, float fH, float fS, float fB )\n{\n  pushMatrix();\n  \n  translate( iX, iY );\n  \n  colorMode( HSB, 360, 100, 100 );\n  strokeWeight( 2 );\n  \n  fill( fH, fS, fB );\n  ellipse( 0, 0, 80, 70 );\n  arc( -23, -27, 14, 20, radians( -220 ), radians( -10 ) );\n  arc( 23, -27, 14, 20, radians( -170 ), radians( 40 ) );\n  \n  fill( 0 );\n  circle( -13, -2, 9 );\n  circle( 13, -2, 9 );\n  \n  line( -18, -12, -10, -13 );\n  line( 18, -12, 10, -13 );\n  \n  fill( fH, fS, fB );\n  noStroke();\n  ellipse( -13, -4, 6, 5 );\n  ellipse( 13, -4, 6, 5 );\n\n  stroke( 0 );\n  arc( -3, 10, 6, 6, radians( 0 ), radians( 180 ) );\n  arc( 3, 10, 6, 6, radians( 0 ), radians( 180 ) );\n  \n  popMatrix();\n}\n\nvoid update()\n{\n  iX += 2;\n  if( iX &gt; 350 )\n  {\n    // 色をランダム\n    fHSB[0] = random( 360 );\n    fHSB[1] = 100;\n    fHSB[2] = 100;\n    iX = 0;\n  }\n}'}</CodeBlock>
        <h2 id='h6'>演習３</h2>
        <p>以下のデ情君ロゴを描画する関数<code>drawDejoukun()</code>に引数を追加し，任意のアニメーションを加えた表現を作る．</p>
        <Img src={`${imageFolder}dinfo_logo_sample.png`} />
        <CodeBlock>{'void setup()\n{\n  size( 400, 400 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  drawDejoukun( 200, 200 );  \n}\n\nvoid drawDejoukun( int iX, int iY )\n{\n  pushMatrix();\n  \n  translate( iX, iY );\n    \n  noStroke();  // fuchi nashi\n\n  // finger\n  fill( 0 );\n  ellipse( 100, -49, 104, 136 );\n  \n  // finger mask\n  fill( 204 );\n  ellipse( 190, -70, 200, 200 );\n  \n  // daen\n  fill( 0 );\n  ellipse( 0, 30, 260, 200 );\n  \n  // mouth mask\n  fill( 204 );\n  ellipse( 0, 4, 116, 186 );\n  \n  // ue daen mask\n  fill( 0 );\n  ellipse( 0, -4, 200, 134 );\n  \n  // daen mask\n  fill( 204 );\n  ellipse( 0, 4, 90, 60 );\n  \n  popMatrix();\n}'}</CodeBlock>
        <p>以下，作例</p>
        <Img src={`${imageFolder}func_dejoukun_rot_randcol.gif`} />
        <CodeBlock>{'int iFrame = 0;\nfloat fRGB[] = { 0, 0, 0 };\n\nvoid setup()\n{\n  size( 400, 400 );\n  // 色をランダム\n  fRGB[0] = random( 255 );\n  fRGB[1] = random( 255 );\n  fRGB[2] = random( 255 );\n}\n\nvoid draw()\n{\n  background( 204 );\n    \n  drawDejoukun( 200, 200, iFrame, fRGB[0], fRGB[1], fRGB[2] );\n  \n  update();\n}\n\nvoid drawDejoukun( int iX, int iY, float iDeg, float fR, float fG, float fB )\n{\n  pushMatrix();\n  \n  translate( iX, iY );\n  rotate( radians( iDeg ) );\n    \n  noStroke();  // fuchi nashi\n\n  // finger\n  fill( fR, fG, fB );\n  ellipse( 100, -49, 104, 136 );\n  \n  // finger mask\n  fill( 204 );\n  ellipse( 190, -70, 200, 200 );\n  \n  // daen\n  fill( fR, fG, fB );\n  ellipse( 0, 30, 260, 200 );\n  \n  // mouth mask\n  fill( 204 );\n  ellipse( 0, 4, 116, 186 );\n  \n  // ue daen mask\n  fill( fR, fG, fB );\n  ellipse( 0, -4, 200, 134 );\n  \n  // daen mask\n  fill( 204 );\n  ellipse( 0, 4, 90, 60 );\n  \n  popMatrix();\n}\n\nvoid update()\n{\n  // 30フレーム毎に色をランダム\n  if( iFrame % 30 == 0 )\n  {\n    fRGB[0] = random( 255 );\n    fRGB[1] = random( 255 );\n    fRGB[2] = random( 255 );\n  }\n\n  iFrame++;\n  if( iFrame &gt;= 360 )\n  {\n    iFrame = 0;\n  }\n}'}</CodeBlock>
        <h2 id='h7'>関数内で別の関数を呼び出す．</h2>
        <h3 id='例１-33'>例１</h3>
        <p>12個の六角形を描画する関数<code>draw12Hexagons()</code>内で六角形を描画する関数<code>drawHexagon()</code>を呼び出す．</p>
        <Img src={`${imageFolder}func_func_hexagons.png`} />
        <CodeBlock>{'void setup()\n{\n  size( 300, 300 );\n}\n\nvoid draw()\n{ \n  background( 204 );\n  \n  drawHexagons();\n}\nvoid drawHexagons()\n{\n  for( int iHexagon = 0; iHexagon &lt; 12; iHexagon++ )\n  {\n    drawHexagon( 150, 150, 150 - iHexagon * 12 );\n  }\n}\n\nvoid drawHexagon( int iX, int iY, float fRadius )\n{\n  beginShape();\n  for( int iDeg = 0; iDeg &lt; 360; iDeg += 60 )\n  {\n    float fX = fRadius * cos( radians( iDeg ) );\n    float fY = fRadius * sin( radians( iDeg ) );\n    \n    vertex( fX + iX, fY + iY );\n  }\n  endShape( CLOSE );\n}'}</CodeBlock>
        <h3 id='例２-32'>例２</h3>
        <p>25個の顔を描画する<code>draw25Faces()</code>内で顔を描画する<code>drawFace()</code>を呼び出す．</p>
        <Img src={`${imageFolder}func_face_mouthopen.gif`} />
        <CodeBlock>{'int iMouthDeg = 0;\n\nvoid setup()\n{\n  size( 400, 400 );\n  frameRate( 20 );\n}\nvoid draw()\n{\n  background( 204 );\n  \n  draw25Faces();\n  \n  update();\n}\n\nvoid draw25Faces()\n{\n  // 口の開き具合 0~1\n  float fMouthOpenCos = ( 1 + cos( radians( iMouthDeg ) ) )/2.0;\n  float fMouthOpenSin = ( 1 + sin( radians( iMouthDeg ) ) )/2.0;\n  \n  // 顔を並べて描画\n  translate( 40, 40 );\n  for( int iFace=0; iFace &lt; 25; iFace++ )\n  {\n    int iColumn = iFace % 5;\n    int iRow    = iFace / 5;\n    float fMouthOpen = 0.0;\n    \n    // 口の開き具合(cos,sin)どちらの値を使うか\n    if( iRow % 2 == 0 )\n    {\n      fMouthOpen = fMouthOpenCos;\n    }\n    else\n    {\n      fMouthOpen = fMouthOpenSin;\n    }\n    // 顔の描画\n    drawFace( 80 * iColumn, 80 * iRow, fMouthOpen );\n  }\n}\n\n// fMouthOpen：0~1\nvoid drawFace( float fX, float fY, float fMouthOpen )\n{\n  pushMatrix();\n  \n  stroke( 0 );\n  fill( 255 );\n  circle( fX, fY, 80 );  // 輪郭\n  \n  fill( 0 );\n  circle( fX - 16, fY - 20, 8 );// 目\n  circle( fX + 16, fY - 20, 8 );\n  \n  //   位置調整のため，細かい数値が入っています．\n  float fBeginDeg = 60 - 60 * fMouthOpen;\n  float fEndDeg = 120 + 60 * fMouthOpen;\n  float fMouthY = fY - 10 + 16 * fMouthOpen;\n  float fMouthWidth = 40 - 20 * fMouthOpen;\n  \n  // 口の描画\n  fill( 255, 0, 0 );\n  arc( fX, fMouthY, fMouthWidth, 30, radians( fBeginDeg ), radians( fEndDeg ), CHORD );\n  \n  popMatrix();\n}\n\nvoid update()\n{\n  iMouthDeg += 4;\n  if( iMouthDeg &gt; 360 )\n  {\n    iMouthDeg-= 360;\n  }\n}'}</CodeBlock>
        <h2 id='h8'>時間を考慮したアニメーションのためのフレームワーク</h2>
        <p>下記のプログラムをテンプレートとして使用することができる．</p>
        <p>一見複雑に見えるが，主に記述する箇所は以下の３か所</p>
        <ol>
        <li><p>１行目：アニメーション用変数定義を記述する．</p></li>
        <li><p>図形描画<code>drawPrimitives(){}</code>のブロック内を記述する．</p></li>
        <li><p>アニメーション用変数の更新<code>updateAnim()</code>のブロック内を記述する．</p></li>

        </ol>
        <CodeBlock>{'// アニメーション用変数\n\n// 時間用変数\nint iSecond = 0;\nint iSecondFrame = 60;        // 秒が変わってから0~59\nint iMinute = 0;\nint iMinuteFrame = 60 * 60;   // 分が変わってから0~3599\nint iHour = 0;\nint iHourFrame = 60 * 60 * 60;// 時が変わってから0~215999\n\nvoid setup()\n{\n  size( 400, 400 );\n  iSecond = second();\n  iMinute = minute();\n  iHour = hour();\n  frameRate( 60 );\n}\n\nvoid draw()\n{\n  background( 204 );\n\n  // 時間関連の変数の更新\n  updateTime();\n\n  // アニメーションの更新\n  updateAnim();\n\n  // 図形描画\n  drawPrimitives();\n}\n\n/**\n *  図形描画\n *  座標変換など描画に関する命令文はここに記述する．\n */\nvoid drawPrimitives()\n{\n}\n\n/**\n *  アニメーション用変数の更新\n *  if文によるシーケンス制御などは主にここに記述する．\n */\nvoid updateAnim()\n{\n}\n\n/**\n *  時間やフレームの更新\n *  基本的には変更を加えない．\n */\nvoid updateTime()\n{\n  // 秒の切り替え検知\n  if ( iSecond!= second() )  // 秒が変わった\n  {\n    iSecond = second();\n    iSecondFrame = 0;\n  }\n  else\n  {\n    iSecondFrame++;\n  }\n\n  // 分の切り替え検知\n  if ( iMinute!= minute() )  // 分が変わった\n  {\n    iMinute = minute();\n    iMinuteFrame = 0;\n  }\n  else\n  {\n    iMinuteFrame++;\n  }\n\n  // 時の切り替え検知\n  if ( iHour!= hour() )     // 時が変わった\n  {\n    iHour = hour();\n    iHourFrame = 0;\n  }\n  else\n  {\n    iHourFrame++;\n  }\n}'}</CodeBlock>
        <h3 id='例１-34'>例１</h3>
        <p>１分間で水が溜まっていく表現</p>
        <Img src={`${imageFolder}func_anim_framework_mizu.gif`} />
        <CodeBlock>{'// アニメーション用変数\nfloat fSuitekiY = 0;\nfloat fSuiiY = 0;\n\n// 時間用変数\nint iSecond = 0;\nint iSecondFrame = 60;        // 秒が変わってから0~60\nint iMinute = 0;\nint iMinuteFrame = 60 * 60;   // 分が変わってから0~3600\nint iHour = 0;\nint iHourFrame = 60 * 60 * 60;// 時が変わってから0~216000\n\nvoid setup()\n{\n  size( 100, 300 );\n  iSecond = second();\n  iMinute = minute();\n  iHour = hour();\n  frameRate( 60 );\n}\n\nvoid draw()\n{\n  background( 204 );\n\n  // 時間関連の変数の更新\n  updateTime();\n\n  // アニメーションの更新\n  updateAnim();\n\n  // 図形描画\n  drawPrimitives();\n}\n\n/**\n *  図形描画\n *  座標変換など描画に関する命令文はここに記述する．\n */\nvoid drawPrimitives()\n{\n  // 水滴\n  fill( 255 );\n  stroke( 0 );\n  circle( 50, fSuitekiY, 20 );\n\n  // 水たまり\n  noStroke();\n  rect( 0, fSuiiY, width, height );\n}\n\n/**\n *  アニメーション用変数の更新\n *  if文によるシーケンス制御などは主にここに記述する．\n */\nvoid updateAnim()\n{\n  if( iMinuteFrame &lt; 120 )  // 水が引くアニメーション\n  {\n    fSuitekiY = -20;\n    fSuiiY = 2.4 * iMinuteFrame;\n  }\n  else                      // 水滴が落ちて水カサが増える．\n  {\n    fSuitekiY = 0.2 * iSecondFrame * iSecondFrame;\n    fSuiiY = height - 5 * iSecond;\n  }\n}\n\n/**\n *  時間やフレームの更新\n *  基本的には変更を加えない．\n */\nvoid updateTime()\n{\n  // 秒の切り替え検知\n  if ( iSecond!= second() )  // 秒が変わった\n  {\n    iSecond = second();\n    iSecondFrame = 0;\n  }\n  else\n  {\n    iSecondFrame++;\n  }\n\n  // 分の切り替え検知\n  if ( iMinute!= minute() )  // 分が変わった\n  {\n    iMinute = minute();\n    iMinuteFrame = 0;\n  }\n  else\n  {\n    iMinuteFrame++;\n  }\n\n  // 時の切り替え検知\n  if ( iHour!= hour() )     // 時が変わった\n  {\n    iHour = hour();\n    iHourFrame = 0;\n  }\n  else\n  {\n    iHourFrame++;\n  }\n}'}</CodeBlock>
        <h3 id='例２-33'>例２</h3>
        <p>１秒ごとに幅が拡大縮小し，分の切り替わりでジャンプするデ情くんの表現</p>
        <Img src={`${imageFolder}func_dejoukun_jump.gif`} />
        <CodeBlock>{'// アニメーション用変数\nfloat fDejouScale = 0;\nfloat fDejouY = 200;\n\n// 時間用変数\nint iSecond = 0;\nint iSecondFrame = 60;        // 秒が変わってから0~60\nint iMinute = 0;\nint iMinuteFrame = 60 * 60;   // 分が変わってから0~3600\nint iHour = 0;\nint iHourFrame = 60 * 60 * 60;// 時が変わってから0~216000\n\nvoid setup()\n{\n  size( 400, 400 );\n  iSecond = second();\n  iMinute = minute();\n  iHour = hour();\n  frameRate( 60 );\n}\n\nvoid draw()\n{\n  background( 204 );\n\n  // 時間関連の変数の更新\n  updateTime();\n\n  // アニメーションの更新\n  updateAnim();\n\n  // 図形描画\n  drawPrimitives();\n}\n\n/**\n *  図形描画\n *  座標変換など描画に関する命令文はここに記述する．\n */\nvoid drawPrimitives()\n{\n  drawDejoukun( 200, fDejouY, fDejouScale );\n}\n\nvoid drawDejoukun( int iX, float fY, float fScaleX )\n{\n  pushMatrix();\n  \n  translate( iX, fY );\n  scale( fScaleX, 1 );\n  \n  noStroke();  // fuchi nashi\n\n  // finger\n  fill( 0 );\n  ellipse( 100, -49, 104, 136 );\n  \n  // finger mask\n  fill( 204 );\n  ellipse( 190, -70, 200, 200 );\n  \n  // daen\n  fill( 0 );\n  ellipse( 0, 30, 260, 200 );\n  \n  // mouth mask\n  fill( 204 );\n  ellipse( 0, 4, 116, 186 );\n  \n  // ue daen mask\n  fill( 0 );\n  ellipse( 0, -4, 200, 134 );\n  \n  // daen mask\n  fill( 204 );\n  ellipse( 0, 4, 90, 60 );\n  \n  popMatrix();\n}\n\n/**\n *  アニメーション用変数の更新\n *  if文によるシーケンス制御などは主にここに記述する．\n */\nvoid updateAnim()\n{\n  if( iMinuteFrame &lt; 180 )  // ジャンプ\n  {\n    fDejouY = 200 - 100 * sin( radians( iMinuteFrame ) );\n  }\n  \n  fDejouScale = sin( radians( 30 + iSecondFrame * 2 ) );\n}\n\n/**\n *  時間やフレームの更新\n *  基本的には変更を加えない．\n */\nvoid updateTime()\n{\n  // 秒の切り替え検知\n  if ( iSecond!= second() )  // 秒が変わった\n  {\n    iSecond = second();\n    iSecondFrame = 0;\n  }\n  else\n  {\n    iSecondFrame++;\n  }\n\n  // 分の切り替え検知\n  if ( iMinute!= minute() )  // 分が変わった\n  {\n    iMinute = minute();\n    iMinuteFrame = 0;\n  }\n  else\n  {\n    iMinuteFrame++;\n  }\n\n  // 時の切り替え検知\n  if ( iHour!= hour() )     // 時が変わった\n  {\n    iHour = hour();\n    iHourFrame = 0;\n  }\n  else\n  {\n    iHourFrame++;\n  }\n}'}</CodeBlock>
        <h3 id='例３-19'>例３</h3>
        <p>１秒ごとに下へ移動し，分の切り替わりでしぼむ顔の模様の表現</p>
        <Img src={`${imageFolder}func_chiikawa_king.gif`} />
        <CodeBlock>{'// アニメーション用変数\nfloat fFaceX = 0;\nfloat fFaceY = 0;\nfloat fFaceHSB[] = { 0, 0, 0 };\nfloat fFaceMinScale = 1;\n\n// 時間用変数\nint iSecond = 0;\nint iSecondFrame = 60;        // 秒が変わってから0~60\nint iMinute = 0;\nint iMinuteFrame = 60 * 60;   // 分が変わってから0~3600\nint iHour = 0;\nint iHourFrame = 60 * 60 * 60;// 時が変わってから0~216000\n\nvoid setup()\n{\n  size( 400, 400 );\n  iSecond = second();\n  iMinute = minute();\n  iHour = hour();\n  frameRate( 60 );\n  // 色をランダム\n  fFaceHSB[0] = random( 360 );\n  fFaceHSB[1] = 100;\n  fFaceHSB[2] = 100;\n}\n\nvoid draw()\n{\n  background( 204 );\n\n  // 時間関連の変数の更新\n  updateTime();\n\n  // アニメーションの更新\n  updateAnim();\n\n  // 図形描画\n  drawPrimitives();\n}\n\n/**\n *  図形描画\n *  座標変換など描画に関する命令文はここに記述する．\n */\nvoid drawPrimitives()\n{\n  drawChiikawa( fFaceX, fFaceY, 1, fFaceHSB[0], fFaceHSB[1], fFaceHSB[2] );\n  \n  drawChiikawa( 200, 350, fFaceMinScale, fFaceHSB[0], fFaceHSB[1], fFaceHSB[2] );\n}\n\nvoid drawChiikawa( float fX, float fY, float fScale, float fH, float fS, float fB )\n{\n  pushMatrix();\n  \n  translate( fX, fY );\n  scale( fScale );\n  \n  colorMode( HSB, 360, 100, 100 );\n  strokeWeight( 2 );\n  \n  fill( fH, fS, fB );\n  ellipse( 0, 0, 80, 70 );\n  arc( -23, -27, 14, 20, radians( -220 ), radians( -10 ) );\n  arc( 23, -27, 14, 20, radians( -170 ), radians( 40 ) );\n  \n  fill( 0 );\n  circle( -13, -2, 9 );\n  circle( 13, -2, 9 );\n  \n  line( -18, -12, -10, -13 );\n  line( 18, -12, 10, -13 );\n  \n  fill( fH, fS, fB );\n  noStroke();\n  ellipse( -13, -4, 6, 5 );\n  ellipse( 13, -4, 6, 5 );\n\n  stroke( 0 );\n  arc( -3, 10, 6, 6, radians( 0 ), radians( 180 ) );\n  arc( 3, 10, 6, 6, radians( 0 ), radians( 180 ) );\n  \n  popMatrix();\n}\n\n/**\n *  アニメーション用変数の更新\n *  if文によるシーケンス制御などは主にここに記述する．\n */\nvoid updateAnim()\n{\n  // 分アニメ\n  if( iMinuteFrame &lt; 240 )  // 萎む\n  {\n    fFaceMinScale = 3 - 2 * iMinuteFrame / 240.0;\n  }\n  else                      // 膨れる\n  {\n    fFaceMinScale = 1 + 2 * iMinuteFrame / 3600.0;\n  }\n  \n  // 秒アニメ\n  if( iSecondFrame == 0 )       // 初期化\n  {\n    // 初期位置へ\n    fFaceX = 50 + iSecond * 5;\n    fFaceY = 0;\n    \n    // 色をランダム\n    fFaceHSB[0] = random( 360 );\n    fFaceHSB[1] = 100;\n    fFaceHSB[2] = 100;\n  }\n  else  // 下へ（X移動ランダム）\n  {\n    fFaceX += random( -4, 4 );\n    fFaceY += 6;\n  }\n}\n\n/**\n *  時間やフレームの更新\n *  基本的には変更を加えない．\n */\nvoid updateTime()\n{\n  // 秒の切り替え検知\n  if ( iSecond!= second() )  // 秒が変わった\n  {\n    iSecond = second();\n    iSecondFrame = 0;\n  }\n  else\n  {\n    iSecondFrame++;\n  }\n\n  // 分の切り替え検知\n  if ( iMinute!= minute() )  // 分が変わった\n  {\n    iMinute = minute();\n    iMinuteFrame = 0;\n  }\n  else\n  {\n    iMinuteFrame++;\n  }\n\n  // 時の切り替え検知\n  if ( iHour!= hour() )     // 時が変わった\n  {\n    iHour = hour();\n    iHourFrame = 0;\n  }\n  else\n  {\n    iHourFrame++;\n  }\n}'}</CodeBlock>
        <h2 id='h9'>演習４</h2>
        <p>以下の時間を考慮したアニメーションのフレームワークを元に，任意のアニメーションを作成する．
        もしくは，前述の例１～３を元に変更を加える．</p>
        <CodeBlock>{'// アニメーション用変数\n\n// 時間用変数\nint iSecond = 0;\nint iSecondFrame = 60;        // 秒が変わってから0~59\nint iMinute = 0;\nint iMinuteFrame = 60 * 60;   // 分が変わってから0~3599\nint iHour = 0;\nint iHourFrame = 60 * 60 * 60;// 時が変わってから0~215999\n\nvoid setup()\n{\n  size( 400, 400 );\n  iSecond = second();\n  iMinute = minute();\n  iHour = hour();\n  frameRate( 60 );\n}\n\nvoid draw()\n{\n  background( 204 );\n\n  // 時間関連の変数の更新\n  updateTime();\n\n  // アニメーションの更新\n  updateAnim();\n\n  // 図形描画\n  drawPrimitives();\n}\n\n/**\n *  図形描画\n *  座標変換など描画に関する命令文はここに記述する．\n */\nvoid drawPrimitives()\n{\n}\n\n/**\n *  アニメーション用変数の更新\n *  if文によるシーケンス制御などは主にここに記述する．\n */\nvoid updateAnim()\n{\n}\n\n/**\n *  時間やフレームの更新\n *  基本的には変更を加えない．\n */\nvoid updateTime()\n{\n  // 秒の切り替え検知\n  if ( iSecond!= second() )  // 秒が変わった\n  {\n    iSecond = second();\n    iSecondFrame = 0;\n  }\n  else\n  {\n    iSecondFrame++;\n  }\n\n  // 分の切り替え検知\n  if ( iMinute!= minute() )  // 分が変わった\n  {\n    iMinute = minute();\n    iMinuteFrame = 0;\n  }\n  else\n  {\n    iMinuteFrame++;\n  }\n\n  // 時の切り替え検知\n  if ( iHour!= hour() )     // 時が変わった\n  {\n    iHour = hour();\n    iHourFrame = 0;\n  }\n  else\n  {\n    iHourFrame++;\n  }\n}'}</CodeBlock>
    </div>
  );
};

export default Programming07;
