// Programming06.tsx

import React, { useEffect} from 'react';
import { useLocation } from 'react-router-dom';
import { LinkP, JumpToDefaultScroll, SketchCode, CodeBlock, Img } from '../ContentCommon';
import { MathJaxProvider, MathJaxFormula } from 'mathjax3-react';
import '../ContentCommon.css';

const imageFolder: string = "/musabi-react/images/programming/";

interface ContentProps {
  practiceId: string;
}

const Programming06: React.FC<ContentProps> = ({ practiceId }) => {

  /* URLから初期スクロール位置 */
  const location = useLocation();
  useEffect(() => {
    JumpToDefaultScroll(practiceId, location as any );    
  }, [practiceId,location]);
  return (
    <div className="common-style">
      <h1 id='h0'>第６回：アニメーション</h1>
        <h2 id='processingにおけるアニメーション'>Processingにおけるアニメーション</h2>
        <p>ランダムな位置に正円を描画するアニメーション</p>
        <Img src={`${imageFolder}animation_random_circles.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 200, 200 );\n}\nvoid draw()\n{\n  circle( random( 200 ), random( 200 ), 20 );}'}</CodeBlock>
        <h3 id='activeモード'>Activeモード</h3>
        <p>アニメーションを行う場合，<strong>Activeモードでプログラムを記述する必要</strong>がある．
        全体のプログラムを二つのブロックに分けて記述する．</p>
        <ul>
        <li><p><code>void setup()</code> ブロック</p>
        <Img src={`${imageFolder}animation_setup.jpg`} />
        </li>
        <li><p><code>void draw()</code> ブロック</p>
        <Img src={`${imageFolder}animation_draw.jpg`} />
        </li>

        </ul>
        <h4 id='staticモード'>Staticモード</h4>
        <ul>
        <li><p>以前までの静止画を描画するモード．</p></li>
        <li><p>基本的に描画は１度のみ</p></li>

        </ul>
        <p><a href='https://processing.org/reference/setup_.html' target='_blank'>https://processing.org/reference/setup_.html</a>
        <a href='https://processing.org/reference/draw_.html' target='_blank'>https://processing.org/reference/draw_.html</a></p>
        <h3 id='動画のフレームとは'>動画のフレームとは</h3>
        <ul>
        <li><p>動画を構成する一枚一枚の静止画（コマ）のこと</p></li>
        <li><p>人間の目で動いているように見せるため，ごく短い時間間隔で切り替わっている．</p></li>

        </ul>
        <h3 id='フレームレートとは'>フレームレートとは</h3>
        <ul>
        <li><p>動画の滑らかさの指標．</p>
        </li>
        <li><p><strong>fps</strong> ( frames per second )</p>
        <ul>
        <li><p>1秒間に書き換えるフレームの数，フレーム毎秒</p></li>
        <li><p>Processing でもこれを指定することでフレームレートを指定できる．</p></li>

        </ul>
        </li>

        </ul>
        <h3 id='フレームレートの設定'>フレームレートの設定</h3>
        <p><strong><code>frameRate()</code></strong></p>
        <p>１秒間に<code>draw(){}</code>が実行される回数を指定できる．
        特に指定をしない場合，デフォルトは<strong>60</strong>となる．</p>
        <h4 id='構文-68'>構文</h4>
        <p><code>frameRate( fps )</code></p>
        <p><a href='https://processing.org/reference/frameRate_.html' target='_blank'>https://processing.org/reference/frameRate_.html</a></p>
        <h4 id='引数-33'>引数</h4>
        <p>１秒間に描画するフレーム数</p>
        <h3 id='例１-22'>例１</h3>
        <p>ランダムな位置に正円を描画するアニメーション</p>
        <Img src={`${imageFolder}animation_random_circles_fr8.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 200, 200 );\n  frameRate( 8 );\n  background( 204 );\n}\n\nvoid draw()\n{\n  circle( random( 200 ), random( 200 ), 20 );\n}'}</CodeBlock>
        <h3 id='例２-21'>例２</h3>
        <p>ランダムな大きさ，色の正円を描画するアニメーション</p>
        <Img src={`${imageFolder}anim_random_size_col_circle.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 200, 200 );\n  frameRate( 4 );\n  background( 204 );\n}\n\nvoid draw()\n{\n  fill( random( 255 ), random( 255 ), random( 255 ) );\n  circle( 100, 100, random( 10, 180 ) );\n}'}</CodeBlock>
        <h3 id='例３-9'>例３</h3>
        <p>横一列にランダムな色の正方形を並べて描画するアニメーション</p>
        <Img src={`${imageFolder}animation_random_squares.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 400, 200 );\n  frameRate( 4 );\n  background( 204 );\n}\n\nvoid draw()\n{\nfill( random( 255 ), random( 255 ), random( 255 ) );\nsquare( floor( random( 8 ) ) * 50, 75, 50 );\n}'}</CodeBlock>
        <h3 id='例４-6'>例４</h3>
        <p>縦横にランダムな色の正方形を並べて描画するアニメーション</p>
        <Img src={`${imageFolder}animation_random_squares_2d.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 400, 400 );\n  frameRate( 16 );\n  background( 204 );\n}\n\nvoid draw()\n{\n  fill( random( 255 ), random( 255 ), random( 255 ) );\n  square( floor( random( 8 ) ) * 50, floor( random( 8 ) ) * 50, 50 );\n}'}</CodeBlock>
        <h2 id='h1'>演習１</h2>
        <p>まずはActiveモードの挙動を体験する．</p>
        <p>以下のプログラムを元に，２つの図形描画命令を使ったランダムなアニメーションを自由に作成する．</p>
        <ul>
        <li><p>主に描画命令の引数に<code>random()</code>を追加するとよい．</p></li>
        <li><p>図形の種類は変更してもよい．（ <code>square()</code>や<code>ellipse()</code>などへ ）</p></li>

        </ul>
        <Img src={`${imageFolder}anim_randam_circles2.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 400, 400 );\n  frameRate( 30 );\n  background( 204 );\n}\n\nvoid draw()\n{\n  fill( 255 );\n  circle( random( 400 ), random( 400 ), 40 );\n  \n  fill( 0 );\n  circle( random( 400 ), random( 400 ), 40 );\n}'}</CodeBlock>
        <p>以下，作例</p>
        <Img src={`${imageFolder}anim_random_circles_sky.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 400, 400 );\n  frameRate( 30 );\n  background( 0 );\n}\n\nvoid draw()\n{\n  noStroke();\n  \n  fill( random( 100, 200 ), random( 100, 200 ), random( 100, 200 ) );\n  circle( random( 400 ), random( 400 ), random( 2, 4 ) );\n  \n  fill( random( 0, 20 ), random( 0, 20 ), random( 0, 20 ), random( 50, 80 ) );\n  circle( random( 400 ), random( 400 ), random( 40, 80 ) );\n}'}</CodeBlock>
        <h2 id='h2'>画面のリフレッシュによる表現</h2>
        <p>ランダムな位置に１つの正円を描画するアニメーション</p>
        <Img src={`${imageFolder}animation_random_circles_ref.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 200, 200 );\n  frameRate( 8 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  circle( random( 200 ), random( 200 ), 20 );\n}'}</CodeBlock>
        <h3 id='画面のリフレッシュ'>画面のリフレッシュ</h3>
        <ul>
        <li><p><code>draw(){}</code>で実行した描画結果は次のフレームにも残る．</p></li>
        <li><p>毎フレーム画面をリフレッシュしたい場合は，以下のコードのように<code>draw(){}</code>ブロック内で<code>background()</code>を記述する．</p></li>

        </ul>
        <Img src={`${imageFolder}animation_random_circles_ref_result.jpg`} />
        <h3 id='例１-23'>例１</h3>
        <p>同X軸上にランダムな色の正方形を１つ描画するアニメーション</p>
        <Img src={`${imageFolder}animation_random_squares_ref.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 400, 200 );\n  frameRate( 4 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  fill( random( 255 ), random( 255 ), random( 255 ) );\n  square( floor( random( 8 ) ) * 50, 75, 50 );\n}'}</CodeBlock>
        <h3 id='例２-22'>例２</h3>
        <p>ランダムな大きさ，色の正円を１つ描画するアニメーション</p>
        <Img src={`${imageFolder}anim_random_size_col_circle_one.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 200, 200 );\n  frameRate( 4 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  fill( random( 255 ), random( 255 ), random( 255 ) );\n  circle( 100, 100, random( 10, 180 ) );\n}'}</CodeBlock>
        <h3 id='アルファ値を使った画面リフレッシュ'>アルファ値を使った画面リフレッシュ</h3>
        <p><code>background()</code>の代わりに半透明の矩形を用いて画面をリフレッシュすることで，少しずつ古い図形が消えていくような表現が実現できる．</p>
        <CodeBlock>{'void draw()\n{\n  // 画面のリフレッシュ \n  fill( 204, 100 );\n  rect( 0, 0, width, height );\n}'}</CodeBlock>
        <h3 id='例１-24'>例１</h3>
        <p>同X軸上にランダムな色の正方形を１つ描画し，フェードアウトするアニメーション</p>
        <Img src={`${imageFolder}anim_squares_fade.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 400, 200 );\n  frameRate( 8 );\n}\n\nvoid draw()\n{\n  fill( 204, 180 );\n  noStroke();\n  rect( 0, 0, width, height );\n  \n  fill( random( 255 ), random( 255 ), random( 255 ) );\n  stroke( 0 );\n  square( floor( random( 8 ) ) * 50, 75, 50 );\n}'}</CodeBlock>
        <h3 id='例２-23'>例２</h3>
        <p>終端がランダムな線を描画し，フェードアウトするアニメーション</p>
        <Img src={`${imageFolder}anim_random_lines_sparks.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 200, 200 );\n  frameRate( 8 );\n}\n\nvoid draw()\n{\n  fill( 0, 100 );\n  noStroke();\n  rect( 0, 0, width, height );\n  \n  strokeWeight( random( 1, 3 ) );\n  stroke( random( 255 ), random( 255 ), random( 255 ) );\n  line( 100, 200, random( 20, 180 ), random( 40, 100 ) );\n}'}</CodeBlock>
        <h3 id='例３-10'>例３</h3>
        <p>縦横にランダムな色の正方形を並べて描画し，フェードアウトするアニメーション</p>
        <Img src={`${imageFolder}anim_random_squares_fade.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 400, 400 );\n  background( 0 );\n  frameRate( 10 );\n}\n\nvoid draw()\n{\n  fill( 0, 40 );\n  noStroke();\n  rect( 0, 0, width, height );\n  \n  fill( random( 255 ), random( 255 ), random( 255 ) );\n  square( floor( random( 8 ) ) * 50, floor( random( 8 ) ) * 50, 50 );\n}'}</CodeBlock>
        <h2 id='h3'>演習２</h2>
        <p>演習１に「アルファ値を用いた画面のリフレッシュ」を追加した以下のプログラムを元に，自由にアレンジを加える．</p>
        <Img src={`${imageFolder}anim_random_circles_fade.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 400, 400 );\n  frameRate( 30 );\n  background( 204 );\n}\n\nvoid draw()\n{\n  fill( 204, 10 );\n  noStroke();\n  rect( 0, 0, width, height );\n  \n  fill( 255 );\n  circle( random( 400 ), random( 400 ), 40 );\n  \n  fill( 0 );\n  circle( random( 400 ), random( 400 ), 40 );\n}'}</CodeBlock>
        <p>以下，作例</p>
        <Img src={`${imageFolder}anim_random_ellipses_suiteki.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 400, 400 );\n  frameRate( 10 );\n  background( 235, 240, 250 );\n}\n\nvoid draw()\n{\n  fill( 235, 240, 250, random( 30, 40 ) );\n  noStroke();\n  rect( 0, 0, width, height );\n  \n  float fX = random( 400 );\n  float fY = random( 400 );\n  float fHeight = random( 20, 30 );\n  float fWidth = random( 15, 30 );\n  \n  fill( 20, 30, 60 );\n  ellipse( fX, fY, fWidth, fHeight );  \n  fill( 230, 240, 250 );\n  ellipse( fX + random( -1, 1 ), fY + random( 1, 2 ), fWidth * 1.1, fHeight );\n  fill( 247, 247, 250 );\n  ellipse( fX, fY + 4, fWidth * 0.6, fHeight * 0.6 );\n}'}</CodeBlock>
        <h2 id='h4'>変数によるアニメーション表現</h2>
        <Img src={`${imageFolder}animation_var_square_loop.gif`} />
        <CodeBlock>{'int iX = 0;\n\nvoid setup()\n{\n  size( 400, 100 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  square( iX, 30, 40 );\n  \n  iX += 4;\n  if( iX &gt; width )\n  {\n    iX = 0;\n  }\n}'}</CodeBlock>
        <h3 id='グローバル変数-1'>グローバル変数</h3>
        <ul>
        <li><p><strong>ブロックの外で変数を定義すればグローバル変数</strong>となる．</p>
        <ul>
        <li><p><code>setup()</code>や<code>draw()</code>のブロックに入れないこと．</p></li>

        </ul>
        </li>
        <li><p>グローバル変数はアニメーションにより時間が経過してもメモリ上に領域を保持する．</p>
        </li>
        <li><p>グローバル変数の値を<code>draw()</code>ブロックで変化させることがアニメーションの基本となる．</p>
        </li>

        </ul>
        <Img src={`${imageFolder}animation_var_kihon.jpg`} />
        <h3 id='例１-25'>例１</h3>
        <p>フレーム毎に正方形の数を増やしながら並べて描画</p>
        <Img src={`${imageFolder}anim_var_squares.gif`} />
        <CodeBlock>{'int iSquare = 0;\n\nvoid setup()\n{\n  size( 400, 100 );\n  frameRate( 4 );\n}\n\nvoid draw()\n{ \n  square( iSquare * 50, 25, 50 );\n  \n  iSquare++;\n  if( iSquare &gt; 8 )\n  {\n    background( 204 );	// 最大まで増えた正方形をクリア\n    iSquare = 0;\n  }\n}'}</CodeBlock>
        <h3 id='例２-24'>例２</h3>
        <p>正方形のグレイ値を１ずつ増やすアニメーション</p>
        <Img src={`${imageFolder}anim_square_gray.gif`} />
        <CodeBlock>{'int iGray = 0;\n\nvoid setup()\n{\n  size( 200, 200 );\n}\n\nvoid draw()\n{ \n  fill( iGray );\n  square( 75, 75, 50 );\n  \n  iGray++;\n  if( iGray &gt; 255 )\n  {\n    iGray = 0;\n  }\n}'}</CodeBlock>
        <h3 id='例３-11'>例３</h3>
        <p>円弧の終わりの角度を１度ずつ増やすアニメーション</p>
        <Img src={`${imageFolder}anim_arc_pie.gif`} />
        <CodeBlock>{'int iDeg = 0;\n\nvoid setup()\n{\n  size( 200, 200 );\n}\n\nvoid draw()\n{ \n  background( 204 );\n  \n  arc( 100, 100, 100, 100, radians( 0 ), radians( iDeg ), PIE );\n  \n  iDeg++;\n  if( iDeg &gt; 360 )\n  {\n    iDeg = 0;\n  }\n}'}</CodeBlock>
        <h3 id='例４-7'>例４</h3>
        <p>楕円の幅を1ずつ減らすアニメーション</p>
        <Img src={`${imageFolder}anim_ellipse_width.gif`} />
        <CodeBlock>{'int iWidth = 200;\n\nvoid setup()\n{\n  size( 200, 200 );\n}\n\nvoid draw()\n{ \n  background( 204 );\n  \n  ellipse( 100, 100, iWidth, 100 );\n  \n  iWidth--;\n  if( iWidth &lt;= 0 )\n  {\n    iWidth = 200;\n  }\n}'}</CodeBlock>
        <h2 id='h5'>演習３</h2>
        <p>任意の図形を一つ描画し，グローバル変数を使ってループアニメーションを作る．</p>
        <ul>
        <li><p>色，座標，大きさなどアニメーションする項目は自由</p></li>
        <li><p>アニメーションがループすること</p></li>
        <li><p>座標変換は使わない．（次の演習で行う）</p></li>

        </ul>
        <p>以下，作例</p>
        <Img src={`${imageFolder}anim_trianglefan_loop.gif`} />
        <CodeBlock>{'int iDeg = 0;\n\nvoid setup()\n{\n  size( 400, 400 );\n  frameRate( 60 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  beginShape( TRIANGLE_FAN );\n  vertex( 200 + 40 * cos( radians( iDeg ) ), 200 + 40 * sin( radians( iDeg ) ) );\n  vertex( 200, 0 );\n  vertex( 400, 200 );\n  vertex( 200, 400 );\n  vertex( 0, 200 );\n  vertex( 200, 0 );\n  endShape( CLOSE );\n  \n  iDeg++;\n  if( iDeg &gt; 360 )\n  {\n    iDeg = 0;\n  }\n}'}</CodeBlock>
        <h2 id='h6'>座標変換によるアニメーション表現</h2>
        <h3 id='例１-26'>例１</h3>
        <p>正円が４度ずつ回転するアニメーション</p>
        <Img src={`${imageFolder}anim_circle_rot.gif`} />
        <CodeBlock>{'int iDeg = 0;\n\nvoid setup()\n{\n  size( 300, 300 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  translate( 150, 150 );\n  rotate( radians( iDeg ) );\n  \n  fill( 255 );\n  stroke( 0 );\n  circle( 80, 0, 100 );\n  \n  iDeg += 4;\n  if( iDeg &gt; 360 )\n  {\n    iDeg = 0;\n  }\n}'}</CodeBlock>
        <h3 id='例２-25'>例２</h3>
        <p>線が１度ずつ回転するアニメーション</p>
        <Img src={`${imageFolder}anim_line_rot.gif`} />
        <CodeBlock>{'int iDeg = 0;\n\nvoid setup()\n{\n  size( 200, 200 );\n}\n\nvoid draw()\n{ \n  background( 204 );\n  \n  translate( 100, 100 );\n  rotate( radians( iDeg ) );\n  \n  strokeWeight( 3 );\n  line( 0, 0, 100, 0 );\n  \n  iDeg++;\n  if( iDeg &gt; 360 )\n  {\n    iDeg = 0;\n  }\n}'}</CodeBlock>
        <h3 id='例３-12'>例３</h3>
        <p>60個の三角形を描画し，現在のフレームによって色を変えるアニメーション</p>
        <Img src={`${imageFolder}anim_triangle60.gif`} />
        <CodeBlock>{'int iFrame = 0;\n\nvoid setup()\n{\n  size( 300, 300 );\n  frameRate( 20 );\n}\n\nvoid draw()\n{ \n  fill( 204, 20 );\n  rect( 0, 0, width, height );\n  noStroke();\n  \n  translate( 150, 150 );\n  \n  for( int iTriangle = 0; iTriangle &lt; 60; iTriangle++ )\n  {\n    if( iTriangle == iFrame )\n    {\n      fill( 0 );\n    }\n    else\n    {\n      fill( 255, 10 );\n    }\n    triangle( 0, 0, -5, -140, 5, -140 );\n    rotate( radians( 6 ) );\n  }\n  \n  iFrame++;\n  if( iFrame &gt;= 60 )\n  {\n    iFrame = 0;\n  }\n}'}</CodeBlock>
        <h3 id='例４-8'>例４</h3>
        <p>星型多角形が１度ずつ回転するアニメーション</p>
        <Img src={`${imageFolder}anim_star_rot.gif`} />
        <CodeBlock>{'float fRadiusS = 40;  // 中心から頂点への距離（凹）\nfloat fRadiusL = 100; // 中心から頂点への距離（凸）\n\nint iRotDeg = 0;  // 回転アニメーション用\n\nvoid setup()\n{\n  size( 300, 300 );\n}\n\nvoid draw()\n{ \n  background( 204 );\n  \n  translate( 150, 150 );\n  rotate( radians( iRotDeg ) );\n    \n  beginShape();\n  for( int iDeg = 18; iDeg &lt; 360; iDeg += 72 )\n  {\n    // 凹の頂点\n    float fSX = fRadiusS * cos( radians( iDeg ) );\n    float fSY = fRadiusS * sin( radians( iDeg ) );\n    \n    // 凸の頂点\n    float fLX = fRadiusL * cos( radians( iDeg + 36 ) );\n    float fLY = fRadiusL * sin( radians( iDeg + 36 ) );\n    \n    vertex( fSX, fSY );\n    vertex( fLX, fLY );\n  }\n  endShape( CLOSE );\n  \n  iRotDeg++;\n  if( iRotDeg &gt; 360 )\n  {\n    iRotDeg = 0;\n  }\n}'}</CodeBlock>
        <h3 id='例５-4'>例５</h3>
        <p>１２個の正方形を円状に描画するアニメーション</p>
        <Img src={`${imageFolder}anim_squares_rot12.gif`} />
        <CodeBlock>{'int iShapeNum = 1;  // 描画する図形の数（1~12）\n\nvoid setup()\n{\n  size( 400, 400 );\n  frameRate( 4 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  rectMode( CENTER );\n  translate( 200, 200 );\n  \n  for ( int iShape = 0; iShape &lt; iShapeNum; iShape++ )\n  {\n    square( 0, -100, 42 );\n    rotate( radians( 30 ) );\n  }\n  \n  iShapeNum++;\n  if( iShapeNum &gt; 12 )\n  {\n    iShapeNum = 1;\n  }\n}'}</CodeBlock>
        <h2 id='h7'>演習４</h2>
        <p>例５のプログラムを元に，図形描画や色の指定などを追加し自由にアレンジする．</p>
        <p>以下，作例</p>
        <p>例５をベースにブレンドモードなどを使用した表現</p>
        <Img src={`${imageFolder}anim_12shapes_toryou.gif`} />
        <CodeBlock>{'int iShapeNum = 1;  // 描画する図形の数（1~12）\n\nvoid setup()\n{\n  size( 400, 400 );\n  frameRate( 4 );\n  colorMode( HSB, 360, 100, 100, 100 );\n}\n\nvoid draw()\n{\n  background( 0, 0, 100 );\n  \n  rectMode( CENTER );\n  translate( 200, 200 );\n  \n  for ( int iShape = 0; iShape &lt; iShapeNum; iShape++ )\n  {\n    blendMode( BLEND );\n    stroke( 0, 0, 0 );\n    fill( 0, 0, 100 );\n    square( 0, -42, 18 );\n    square( 0, -100, 42 );\n    fill( iShape * 30, 100, 100 );\n    square( 0, -65, 28 );\n  \n    blendMode( MULTIPLY );\n    noStroke();\n    fill( iShape * 30, 100, 100, 30 );\n    circle( 0, 0, 68 );\n    \n    rotate( radians( 30 ) );\n  }\n  \n  iShapeNum++;\n  if( iShapeNum &gt; 12 )\n  {\n    iShapeNum = 1;\n  }\n}'}</CodeBlock>
        <h2 id='h8'>簡易物理アニメーション</h2>
        <p>厳密な物理計算を行う必要はなく，簡単な算術式で表現ができる．
        フレーム番号をグローバル変数<code>iFrame</code>に保存しておき，時間の値として計算に用いている．</p>
        <h3 id='例１-27'>例１</h3>
        <p>自由落下</p>
        <Img src={`${imageFolder}anime_freefall_circle.gif`} />
        <CodeBlock>{'int iFrame = 0;\n\nvoid setup()\n{\n  size( 100, 200 );\n}\n\nvoid draw()\n{\n  background( 204 );\n    \n  fill( 255 );\n  stroke( 0 );\n  circle( 50, 0.2 * iFrame * iFrame, 20 );\n          \n  iFrame++;\n  if( iFrame &gt; 60 )\n  {\n    iFrame = 0;\n  }\n}'}</CodeBlock>
        <h3 id='例２-26'>例２</h3>
        <p>水平投射</p>
        <Img src={`${imageFolder}anim_throw_horizon.gif`} />
        <CodeBlock>{'int iFrame = 0;\n\nvoid setup()\n{\n  size( 200, 100 );\n}\n\nvoid draw()\n{\n  background( 204 );\n    \n  fill( 255 );\n  stroke( 0 );\n  circle( 10 * iFrame, 0.2 * iFrame * iFrame, 20 );\n          \n  iFrame++;\n  if( iFrame &gt; 60 )\n  {\n    iFrame = 0;\n  }\n}'}</CodeBlock>
        <h3 id='例３-13'>例３</h3>
        <p>鉛直投げ上げ</p>
        <Img src={`${imageFolder}anim_throw_up.gif`} />
        <CodeBlock>{'int iFrame = 0;\nfloat fJoushouY = 0;\nfloat fRakkaY = 0;\nfloat fY = 0;\n\nvoid setup()\n{\n  size( 100, 200 );\n}\n\nvoid draw()\n{\n  background( 204 );\n\n  fJoushouY = -12 * iFrame;\n  fRakkaY = 0.2 * iFrame * iFrame;\n  fY = 200 + fJoushouY + fRakkaY;\n  \n  fill( 255 );\n  stroke( 0 );\n  circle( 50, fY, 20 );\n\n  iFrame++;\n  if ( iFrame &gt; 120 )\n  {\n    iFrame = 0;\n  }\n}'}</CodeBlock>
        <h3 id='例４-9'>例４</h3>
        <p>斜方投射</p>
        <Img src={`${imageFolder}anime_throw_naname.gif`} />
        <CodeBlock>{'int iFrame = 0;\nfloat fJoushouY = 0;\nfloat fRakkaY = 0;\nfloat fY = 0;\n\nvoid setup()\n{\n  size( 200, 200 );\n}\n\nvoid draw()\n{\n  background( 204 );\n\n  fJoushouY = -12 * iFrame;\n  fRakkaY = 0.2 * iFrame * iFrame;\n  fY = 200 + fJoushouY + fRakkaY;\n  \n  fill( 255 );\n  stroke( 0 );\n  circle( 3 * iFrame, fY, 20 );\n\n  iFrame++;\n  if ( iFrame &gt; 120 )\n  {\n    iFrame = 0;\n  }\n}'}</CodeBlock>
        <h3 id='例５-5'>例５</h3>
        <p>単振動</p>
        <Img src={`${imageFolder}animation_wave_circle.gif`} />
        <CodeBlock>{'int iDeg = 0;\n\nvoid setup()\n{\n  size( 100, 200 );\n}\n\nvoid draw()\n{\n  fill( 204, 30 );\n  noStroke();\n  rect( 0, 0, width, height );\n  \n  translate( width/2, height/2 );\n  \n  fill( 255 );\n  stroke( 0 );\n  circle( 0, 90 * sin( radians( iDeg ) ), 20 );\n          \n  iDeg += 4;\n  if( iDeg &gt; 360 )\n  {\n    iDeg = 0;\n  }\n}'}</CodeBlock>
        <h3 id='例６-3'>例６</h3>
        <p>跳ね返り</p>
        <Img src={`${imageFolder}anim_circle_hanekaeri.gif`} />
        <CodeBlock>{'int  iX = 0;\nint  iDirX = 1; // 進む向き( 1:+X方向, -1:-X方向 )\n\nvoid setup()\n{\n  size( 200, 100 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  ellipse( iX, 50, 30, 30 );\n  \n  iX += iDirX * 4;\n  if( iX &gt;= width || iX &lt; 0 )\n  {\n    iDirX *= -1;  // 向きを逆方向に変える(-1をかける)\n  }\n}'}</CodeBlock>
        <h2 id='h9'>シーケンスによる表現</h2>
        <Img src={`${imageFolder}anim_sequence_simple.gif`} />
        <CodeBlock>{'int iFrame = 0;\n\n// 初期位置\nfloat fX = 0;\nfloat fY = 100;\n\nvoid setup()\n{\n  size( 200, 200 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  if( iFrame &lt; 30 )       // 右へ\n  {\n    fX += 3;\n    iFrame++;\n  }\n  else if( iFrame &lt; 70 )  // 右上へ\n  {\n    fX += 1;\n    fY -= 1;\n    iFrame++;\n  }\n  else if( iFrame &lt; 100 )  // 下へ\n  {\n    fY += 7;\n    iFrame++;\n  }\n  else                    // アニメリセット\n  {\n    // 初期位置へ\n    fX = 0;\n    fY = 100;\n    iFrame = 0;\n  }\n  \n  circle( fX, fY, 30 );\n}'}</CodeBlock>
        <h3 id='if文によるシーケンス'>if文によるシーケンス</h3>
        <ul>
        <li><p>フレーム番号<code>iFrame</code>の値に基づき，アニメーションを区切ることができる．</p></li>
        <li><p><code>else{}</code>のブロックにはループするためのリセット処理を記述する．</p></li>

        </ul>
        <Img src={`${imageFolder}anim_sequence_basic.jpg`} />
        <h3 id='例１-28'>例１</h3>
        <p>３つの動きを行う円のアニメーション</p>
        <p>ジャンプ用のグローバル変数<code>iJumpDeg</code>を追加している．</p>
        <Img src={`${imageFolder}anim_sequence_circle.gif`} />
        <CodeBlock>{'int iFrame = 0;\nfloat fX = 0;\nfloat fY = 120;\nint iJumpDeg = 0;\n\nvoid setup()\n{\n  size( 200, 200 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  if( iFrame &lt; 25 )       // 左から中央へ\n  {\n    fX += 4;\n    iFrame++;\n  }\n  else if( iFrame &lt; 45 )  // ジャンプ\n  {\n    iJumpDeg += 9;\n    fY = 120 - 50 * sin( radians( iJumpDeg ) );\n    iFrame++;\n  }\n  else if( iFrame &lt; 70 )  // 中央から右へ\n  {\n    fX += 5;\n    iFrame++;\n  }\n  else                    // アニメリセット\n  {\n    fX = 0;\n    fY = 120;\n    iJumpDeg = 0;\n    iFrame = 0;\n  }\n  \n  circle( fX, fY, 30 );\n}'}</CodeBlock>
        <h3 id='例２-27'>例２</h3>
        <p>例１に変更を加えたアニメーション</p>
        <Img src={`${imageFolder}anim_sequence_circle_jump.gif`} />
        <CodeBlock>{'int iFrame = 0;\nfloat fX = 0;\nfloat fY = 120;\nint iJumpDeg = 0;\n\nvoid setup()\n{\n  size( 200, 200 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  if( iFrame &lt; 25 )       // 左から中央へ\n  {\n    fX += 3;\n    iFrame++;\n  }\n  else if( iFrame &lt; 45 )  // ジャンプ\n  {\n    fX += 3;\n    iJumpDeg += 6;\n    fY = 120 - 60 * sin( radians( iJumpDeg ) );\n    iFrame++;\n  }\n  else if( iFrame &lt; 70 )  // 中央から右へ\n  {\n    fX += 3;\n    iFrame++;\n  }\n  else                    // アニメリセット\n  {\n    fX = 0;\n    fY = 120;\n    iJumpDeg = 0;\n    iFrame = 0;\n  }\n  \n  circle( fX, fY, 30 );\n}'}</CodeBlock>
        <h3 id='例３-14'>例３</h3>
        <p>連続して弾むようなアニメーション</p>
        <Img src={`${imageFolder}anim_sequence_circle_bound.gif`} />
        <CodeBlock>{'int iFrame = 0;\nfloat fX = 0;\nfloat fY = 120;\nint iJumpDeg = 0;\n\nvoid setup()\n{\n  size( 200, 200 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  if( iFrame &lt; 70 )      // ジャンプ\n  {\n    fX += 3;\n    iJumpDeg += 8;\n    iJumpDeg %= 180;     // 180を超えたら0へ戻る．\n    fY = 120 - 50 * sin( radians( iJumpDeg ) );\n    iFrame++;\n  }\n  else                   // アニメリセット\n  {\n    fX = 0;\n    fY = 120;\n    iFrame = 0;\n  }\n  \n  circle( fX, fY, 30 );\n}'}</CodeBlock>
        <h3 id='例４-10'>例４</h3>
        <p>正方形が遅れて動くアニメーション</p>
        <Img src={`${imageFolder}anim_sequence_block.gif`} />
        <CodeBlock>{'int iFrame = 0;\n\nfloat fCX = 50;\nfloat fCY = 200;\nint iCJumpDeg = 0;\n\nfloat fSX = 30;\nfloat fSY = 80;\nint iSJumpDeg = 0;\n\nvoid setup()\n{\n  size( 100, 200 );\n}\n\nvoid draw()\n{\n  background( 204 );\n\n  if( iFrame &lt; 25 )        // 正円だけ跳ねる\n  {\n    iCJumpDeg += 4;\n    fCY = 200 - 65 * sin( radians( iCJumpDeg ) );\n    iFrame++;\n  }\n  else if( iFrame &lt; 45 )   // 両方跳ねる\n  {\n    iCJumpDeg += 4;\n    fCY = 200 - 65 * sin( radians( iCJumpDeg ) );\n\n    iSJumpDeg += 9;\n    fSY = 80 - 20 * sin( radians( iSJumpDeg ) );\n    iFrame++;\n  }\n  else                    // アニメリセット\n  {\n    iCJumpDeg = 0;\n    iSJumpDeg = 0;\n    iFrame = 0;\n  }\n\n  circle( fCX, fCY, 30 );\n  square( fSX, fSY, 40 );\n}'}</CodeBlock>
        <h2 id='h10'>演習５</h2>
        <p>下の基本のシーケンス表現プログラムを自由にアレンジし，仕組みを体験的に理解する．</p>
        <Img src={`${imageFolder}anim_sequence_simple.gif`} />
        <CodeBlock>{'int iFrame = 0;\n\n// 初期位置\nfloat fX = 0;\nfloat fY = 100;\n\nvoid setup()\n{\n  size( 200, 200 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  if( iFrame &lt; 30 )       // 右へ\n  {\n    fX += 3;\n    iFrame++;\n  }\n  else if( iFrame &lt; 70 )  // 右上へ\n  {\n    fX += 1;\n    fY -= 1;\n    iFrame++;\n  }\n  else if( iFrame &lt; 100 )  // 下へ\n  {\n    fY += 7;\n    iFrame++;\n  }\n  else                    // アニメリセット\n  {\n    // 初期位置へ\n    fX = 0;\n    fY = 100;\n    iFrame = 0;\n  }\n  \n  circle( fX, fY, 30 );\n}'}</CodeBlock>
        <h2 id='h11'>時間を考慮したアニメーション表現</h2>
        <h3 id='テキスト描画'>テキスト描画</h3>
        <p><strong><code>text()</code></strong></p>
        <h4 id='構文-69'>構文</h4>
        <p>文字列の描画を行う場合，ダブルクォーテーション<code>&quot;&quot;</code>で囲む．</p>
        <p><code>text( &quot;描画する文字列&quot;, X座標値, Ｙ座標値 )</code></p>
        <p><code>text( 描画する値, X座標値, Ｙ座標値 )</code></p>
        <p><code>text( 変数, X座標値, Ｙ座標値 )</code></p>
        <h4 id='説明-47'>説明</h4>
        <p>スクリーンにテキストを描画する．
        フォントの色の変更は<code>fill()</code>を使う．</p>
        <p><a href='https://processing.org/reference/text_.html' target='_blank'>https://processing.org/reference/text_.html</a></p>
        <h3 id='テキストの大きさを指定する'>テキストの大きさを指定する．</h3>
        <p><strong><code>textSize()</code></strong></p>
        <h4 id='構文-70'>構文</h4>
        <p><code>textSize( size )</code></p>
        <h4 id='引数-34'>引数</h4>
        <p>フォントサイズ（ピクセル単位）</p>
        <h4 id='説明-48'>説明</h4>
        <p>テキスト描画のフォントサイズを設定する．</p>
        <p><a href='https://processing.org/reference/textSize_.html' target='_blank'>https://processing.org/reference/textSize_.html</a></p>
        <h4 id='例-31'>例</h4>
        <Img src={`${imageFolder}text_3way.png`} />
        <CodeBlock>{'size( 200, 200 );\n\ntextSize( 40 );\n\ntext( &quot;word&quot;, 40, 50 );\n\nfill( 50 );\ntext( 3.14, 30, 100 );\n\nint iText = 123;\nfill( 0 );\ntext( iText, 40, 150 ); '}</CodeBlock>
        <h3 id='テキストの揃え位置を指定する'>テキストの揃え位置を指定する．</h3>
        <p><strong><code>textAlign()</code></strong></p>
        <h4 id='構文-71'>構文</h4>
        <p><code>textAlign( alignX )</code></p>
        <p><code>textAlign( alignX, alignY )</code></p>
        <h4 id='説明-49'>説明</h4>
        <p>テキスト描画における行揃えの位置を指定する．</p>
        <h4 id='引数-35'>引数</h4>
        <p>alignX：左右の揃え位置<code>LEFT</code>, <code>CENTER</code>, or <code>RIGHT</code></p>
        <p>alignY：<code>TOP</code>, <code>BOTTOM</code>, <code>CENTER</code>, or <code>BASELINE</code></p>
        <h4 id='例-32'>例</h4>
        <Img src={`${imageFolder}text_align.png`} />
        <CodeBlock>{'size( 200, 200 );\n\ntextSize( 40 );\n\ntext( &quot;word&quot;, 100, 50 );\n\ntextAlign( CENTER );\nfill( 50 );\ntext( 3.14, 100, 100 );\n\ntextAlign( RIGHT );\nint iText = 123;\nfill( 0 );\ntext( iText, 100, 150 ); '}</CodeBlock>
        <h3 id='時刻を取得する'>時刻を取得する．</h3>
        <p><strong><code>second()</code></strong></p>
        <p><strong><code>minute()</code></strong></p>
        <p><strong><code>hour()</code></strong></p>
        <h4 id='構文-72'>構文</h4>
        <p><code>second()  // 秒(0 - 59)</code></p>
        <p><code>minute()  // 分(0 - 59)</code></p>
        <p><code>hour()    // 時(0 - 23)</code></p>
        <h4 id='説明-50'>説明</h4>
        <p>現在の時刻における秒，分，時を取得する．
        アニメーションに用いる場合，<code>draw(){}</code>の中で使用し，最新の時刻を常に取得する必要がある．</p>
        <p><a href='https://processing.org/reference/second_.html' target='_blank'>https://processing.org/reference/second_.html</a></p>
        <p><a href='https://processing.org/reference/minute_.html' target='_blank'>https://processing.org/reference/minute_.html</a></p>
        <p><a href='https://processing.org/reference/hour_.html' target='_blank'>https://processing.org/reference/hour_.html</a></p>
        <h3 id='例１-29'>例１</h3>
        <p>デジタル時計</p>
        <Img src={`${imageFolder}anim_time_digital.gif`} />
        <CodeBlock>{'int iColon = 0;\n\nvoid setup()\n{\n  size( 200, 100 );\n  frameRate( 1 );\n}\nvoid draw()\n{\n  background( 204 );\n  \n  textSize( 40 ); \n  textAlign( RIGHT, CENTER );\n  fill( 0 );\n  text( hour(), 60, 50 );\n  text( minute(), 115, 50 );\n  text( second(), 180, 50 );\n  \n  text( &quot;s&quot;, 140, 50 );\n  if( iColon == 1 )\n  {\n    text( &quot;:&quot;, 72, 46 );\n    iColon = 0;\n  }\n  else\n  {\n    iColon++;\n  }\n}'}</CodeBlock>
        <h3 id='例２-28'>例２</h3>
        <p>オーソドックスな時計</p>
        <Img src={`${imageFolder}anim_clock.gif`} />
        <CodeBlock>{'void setup()\n{\n  size( 400, 400 );\n  frameRate(1);\n}\n\nvoid draw()\n{\n  background( 255, 255, 255 );\n  fill( 0 );\n  noStroke();\n    \n  translate( 200, 210 );\n  \n  pushMatrix();\n  \n  // 時針描画\n  int iHour = hour();\n  rotate( radians( 30 * iHour - 90 ) );\n  rect( 0, -4, 100, 8 );\n  \n  popMatrix();\n  \n  pushMatrix();\n  \n  // 分針描画\n  int iMinute = minute();\n  rotate( radians( 6 * iMinute - 90 ) );\n  rect( 0, -3, 140, 6 );\n  \n  popMatrix();\n\n  pushMatrix();\n\n  // 秒針描画\n  int iSecond = second();\n  rotate( radians( 6 * iSecond - 90 ) );\n  rect( 0, -1, 145, 2 );\n  \n  popMatrix();\n  \n  // 1~12テキスト描画\n  textSize( 40 );\n  textAlign( CENTER );\n  for( int iHourLabel = 0; iHourLabel &lt; 12; iHourLabel++ )\n  {\n    float fRad = radians( 30 * iHourLabel - 60 );\n    text( iHourLabel + 1, 160 * cos( fRad ), 160 * sin( fRad ) );\n  }\n}'}</CodeBlock>
        <h3 id='例３-15'>例３</h3>
        <p>秒毎に正方形が大きくなるアニメーション</p>
        <Img src={`${imageFolder}anim_block_second.gif`} />
        <CodeBlock>{'int iFrame = 0;\n\nfloat fCX = 50;\nfloat fCY = 200;\nint iCJumpDeg = 0;\n\nfloat fSX = 50;\nfloat fSY = 120;\nint iSJumpDeg = 0;\n\nvoid setup()\n{\n  size( 100, 200 );\n  rectMode( CENTER );\n}\n\nvoid draw()\n{\n  background( 204 );\n\n  if( iFrame &lt; 25 )        // 正円だけ跳ねる\n  {\n    iCJumpDeg += 4;\n    fCY = 200 - 65 * sin( radians( iCJumpDeg ) );\n    iFrame++;\n  }\n  else if( iFrame &lt; 45 )   // 両方跳ねる\n  {\n    iCJumpDeg += 4;\n    fCY = 200 - 65 * sin( radians( iCJumpDeg ) );\n\n    iSJumpDeg += 9;\n    fSY = 120 - 20 * sin( radians( iSJumpDeg ) );\n    iFrame++;\n  }\n  else                    // アニメリセット\n  {\n    iCJumpDeg = 0;\n    iSJumpDeg = 0;\n    iFrame = 0;\n  }\n\n  float fSquareSize = second() + 10;\n  \n  circle( fCX, fCY, 30 );\n  square( fSX, fSY - fSquareSize / 2, fSquareSize );\n}'}</CodeBlock>
        <h3 id='例４-11'>例４</h3>
        <p>１秒毎に正円が大きくなり，分が切り替わると正円がしぼむアニメーションを再生する．</p>
        <Img src={`${imageFolder}anime_time_sequence_circle_shibomu.gif`} />
        <CodeBlock>{'int iMinute = 0;       // 現在の分\nint iMinuteFrame = 30; // 分切り替えアニメの再生フレーム\n\nvoid setup()\n{\n  size( 200, 200 );\n  frameRate( 10 );\n  iMinute =  minute();\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  // 分の切り替え検知\n  if( iMinute != minute() )  // 分が変わった\n  {\n    iMinute = minute();\n    \n    // 分切り替えアニメ開始\n    iMinuteFrame = 0;\n  }\n  \n  // 秒アニメ\n  circle( 100, 100, second() * 3 );\n  \n  // 分切り替えアニメ\n  if( iMinuteFrame &lt; 30 )  // 30フレームの間アニメーション\n  {\n    circle( 100, 100, 180 - 6 * iMinuteFrame );\n    iMinuteFrame++;\n  }\n}'}</CodeBlock>
        <Img src={`${imageFolder}anim_time_sequence_min_kirikae.jpg`} />
        <h3 id='例５-6'>例５</h3>
        <p>１秒毎に正円が自由落下し，分が切り替わると正円が広がるアニメーションを再生する．</p>
        <Img src={`${imageFolder}anime_time_sequence_freefall.gif`} />
        <CodeBlock>{'int iMinute = 0;       // 現在の分\nint iMinuteFrame = 30; // 分切り替えアニメの再生フレーム\n\nint iSecondFrame = 0; // 秒アニメの再生フレーム\nfloat fCircleX = 100;\nfloat fCircleY = 100;\n\nvoid setup()\n{\n  size( 200, 200 );\n  frameRate( 30 );\n  iMinute =  minute();\n  fCircleX = random( 200 );\n}\n\nvoid draw()\n{\n  background( 204 );\n  \n  // 分の切り替え検知\n  if( iMinute != minute() )  // 分が変わった\n  {\n    iMinute = minute();\n    \n    // 分切り替えアニメ開始\n    iMinuteFrame = 0;\n  }\n  \n  // 秒アニメ\n  if( iSecondFrame &lt; 30 )\n  {\n    // 自由落下\n    fCircleY = 0.3 * iSecondFrame * iSecondFrame;\n    iSecondFrame++;\n  }\n  else                    // リセット\n  {\n    fCircleX = random( 200 );\n    iSecondFrame = 0;\n  }\n  fill( 255 );\n  stroke( 0 );\n  circle( fCircleX, fCircleY, second() + 8 );\n  \n  // 分切り替えアニメ\n  if( iMinuteFrame &lt; 30 )  // 30フレームの間アニメーション\n  {\n    // 円が広がる\n    noFill();\n    stroke( 0, 0, 255 );\n    circle( 100, 100 - iMinuteFrame, iMinuteFrame * 8 );\n    iMinuteFrame++;\n  }\n}'}</CodeBlock>
        <h2 id='h12'>演習６</h2>
        <p>秒を取得する<code>second()</code>を用い，任意のアニメーションを作成する．
        余裕があれば<code>minute()</code>も使用する．</p>
        <p>課題３のための実験と考えるとよい．</p>
        <h2 id='h13'>サンプル集</h2>
        <h3 id='配列を使った雨の表現'>配列を使った雨の表現</h3>
        <Img src={`${imageFolder}anim_amatsubu.gif`} />
        <CodeBlock>{'// 100個の雨粒の情報\n// [0]:X座標値，[1]:Y座標値，[2]:スピード\nfloat[][] fAmeXYS = new float[100][3];\n\nvoid setup()\n{\n  size( 400, 400 );\n  frameRate( 30 );\n  \n  // 雨粒の初期化\n  for( int iAmeIdx=0; iAmeIdx &lt; fAmeXYS.length; iAmeIdx++ )\n  {\n    fAmeXYS[iAmeIdx][0] = random( width );     // X座標値\n    fAmeXYS[iAmeIdx][1] = random( height );    // Y座標値\n    fAmeXYS[iAmeIdx][2] = random( 10, 20 );    // 雨粒のスピード\n  }\n}\n\nvoid draw()\n{\n  background( 204 );\n    \n  // 100個の雨粒のアニメーション  \n  rotate( radians( 10 ) );  // 雨の角度\n  translate( 30, -50 );     // 調整\n  for( int iAmeIdx=0; iAmeIdx &lt; fAmeXYS.length; iAmeIdx++ )\n  {\n    fill( 0 );\n    \n    // アニメーション後のY座標値．\n    fAmeXYS[iAmeIdx][1] += fAmeXYS[iAmeIdx][2];\n    fAmeXYS[iAmeIdx][1] %= height;  // 550超えたら0から繰り返す．\n    \n    // 雨粒の描画\n    line( fAmeXYS[iAmeIdx][0], fAmeXYS[iAmeIdx][1], fAmeXYS[iAmeIdx][0], fAmeXYS[iAmeIdx][1] + 40 );\n  }\n}'}</CodeBlock>
        <h3 id='配列を使った粒子状の表現'>配列を使った粒子状の表現</h3>
        <Img src={`${imageFolder}anim_particle.gif`} />
        <CodeBlock>{'float fG = 0.1;	// 重力による加速度\n\n// 100個の粒子の情報\n// [0]:X座標値，[1]:Y座標値，[2]:スピード\nfloat[][] fParticleXYS = new float[100][3];\n\nvoid setup()\n{\n  size( 400, 400 );\n  frameRate( 30 );\n  \n  // 粒子の初期化\n  for( int iParticleIdx=0; iParticleIdx &lt; fParticleXYS.length; iParticleIdx++ )\n  {\n    fParticleXYS[iParticleIdx][0] = random( width );   // X座標値\n    fParticleXYS[iParticleIdx][1] = random( height );  // Y座標値\n    fParticleXYS[iParticleIdx][2] = random( 0, 4 );    // 雨粒のスピード\n  }\n}\n\nvoid draw()\n{\n  background( 204 );\n    \n  // 100個の粒子のアニメーション  \n  for( int iParticleIdx=0; iParticleIdx &lt; fParticleXYS.length; iParticleIdx++ )\n  {   \n    // 速度の更新\n    fParticleXYS[iParticleIdx][2] += fG;\n    \n    // アニメーション後のY座標値．\n    fParticleXYS[iParticleIdx][1] += fParticleXYS[iParticleIdx][2];\n    \n    if( fParticleXYS[iParticleIdx][1] &gt; height )  // リセット\n    {\n      fParticleXYS[iParticleIdx][0] = random( width );     // X座標値\n      fParticleXYS[iParticleIdx][1] = 0;    // Y座標値\n      fParticleXYS[iParticleIdx][2] = random( 0, 2 );\n    }\n    \n    // 雨粒の描画\n    circle( fParticleXYS[iParticleIdx][0], fParticleXYS[iParticleIdx][1], 20 );\n  }\n}'}</CodeBlock>
        <h3 id='親子関係の入れ子アニメーション'>親子関係の入れ子アニメーション</h3>
        <p>惑星の公転のような回転アニメーション</p>
        <Img src={`${imageFolder}anim_wakusei.gif`} />
        <CodeBlock>{'int iRatateDegL;             // 大型の星の現在の回転角\nint iRatateDegM;             // 中型の星の現在の回転角\nint iRatateDegS;             // 小型の星の現在の回転角\n\nvoid setup()\n{\n  size( 300, 300 );\n  frameRate( 20 );\n  iRatateDegL = 0;\n  iRatateDegM = 0;\n  iRatateDegS = 0;\n}\nvoid draw()\n{\n  // 画面のリフレッシュ.\n  fill( 10, 10, 10 );\n  noStroke();\n  rect( 0, 0, width, height );\n  \n  // 大型の星回転\n  iRatateDegL += 2;                 // 毎フレーム2°回転\n  if( iRatateDegL &gt; 360 )\n  {\n    iRatateDegL -= 360;             // 360°以上回転したら戻す.\n  }\n\n  // 中型の星回転\n  iRatateDegM += 2;                 // 毎フレーム2°回転\n  if( iRatateDegS &gt; 360 )\n  {\n    iRatateDegM -= 360;             // 360°以上回転したら戻す.\n  }\n\n  // 小型の星回転\n  iRatateDegS += 2;                 // 毎フレーム2°回転\n  if( iRatateDegS &gt; 360 )\n  {\n    iRatateDegS -= 360;             // 360°以上回転したら戻す.\n  }\n  \n  translate( width/2, height/2 );   // 座標原点をスクリーン中心へ.\n\n  // 大型の星描画\n  rotate( radians(iRatateDegL) );   // 座標系回転(大型の星)\n  translate( width/8, 0 );          // 座標原点を大型の星の中心へ\n  fill( 255, 0, 0 );\n  ellipse( 0, 0, 50, 50 );\n    \n  // 中型の星描画\n  rotate( radians(iRatateDegM) );   // 座標系回転(中型の星)\n  translate( width/4, 0 );          // 座標原点を中型の星の中心へ\n  fill( 0, 255, 0 );\n  ellipse( 0, 0, 30, 30 );\n  \n  // 小さい星描画\n  rotate( radians(iRatateDegS) );   // 座標系回転(小さい星)\n  translate( width/10, 0 );         // 座標原点を小さい星の中心へ\n  fill( 0, 0, 255 );\n  ellipse( 0, 0, 16, 16 );\n}'}</CodeBlock>
        <h3 id='波形の移動位相変化）アニメーション'>波形の移動（位相変化）アニメーション</h3>
        <Img src={`${imageFolder}pulse_translate.gif`} />
        <CodeBlock>{'int iAmplitudeL = 80;  // 振幅（大きな波の高さ）\nint iAnimPulseDeg = 0; // 波の角度\n\nvoid setup()\n{\n  size( 400,200 );\n  colorMode( HSB, 90, 1.0, 1.0, 1.0 );\n}\n\nvoid draw()\n{\n  noStroke();\n  fill( 0, 0, 0.4 );\n  rect( 0, 0, width, height );						// スクリーンリフレッシュ\n  \n  iAnimPulseDeg += 2; 										// アニメーション用の角度を2°増やす.\n  if( iAnimPulseDeg &gt; 360 )               // 360°を超えたらリセット.\n  {\n    iAnimPulseDeg -= 360;\n  }\n  \n  // コサイン波形の描画\n  // X座標値を1増やしながらlineを繰り返し描画する．\n  strokeWeight( 8 );\n  for( int iX=0; iX &lt; width; iX++ )\n  {\n    int iAngle0 = iX*4+iAnimPulseDeg;        // アニメーション用の角度を加算\n    int iAngle1 = (iX+1)*4+iAnimPulseDeg;    // アニメーション用の角度を加算\n\n    stroke( iX%90, 1.0, 1.0 );\n    line( iX, height/2 + iAmplitudeL*cos( radians(iAngle0) ),\n          iX, height/2 + iAmplitudeL*cos( radians(iAngle1+1) ) );\n  }\n}'}</CodeBlock>
        <h3 id='円弧による沈む夕日状の表現'>円弧による沈む夕日状の表現</h3>
        <Img src={`${imageFolder}anim_arc_yuuhi.gif`} />
        <CodeBlock>{'int iArcAnimDeg = 0;        // アニメーション用円弧の角度\n\nvoid setup()\n{\n  size( 200, 200 );\n  frameRate( 12 );\n}\nvoid draw()\n{\n  background( 204 );\n  \n  if( iArcAnimDeg &lt; 180 )  // 180°以上変化したらリセット.\n  {\n    iArcAnimDeg += 4;    // 毎フレームの変化角度\n  }\n  else\n  {\n    iArcAnimDeg = 0;    // リセット.\n  }\n  \n  int iArcAnimDegBegin = iArcAnimDeg + 90;     // 円弧の始まりの角度を計算.\n  int iArcAnimDegEnd   = 450 - iArcAnimDeg;    // 円弧の終わりの角度を計算.\n  \n  // 円弧描画\n  float fY = 180 - 100 * sin( radians( iArcAnimDegBegin ) )/2;\n  fill( 255 );\n  arc( width/2, fY, 100, 100, radians(iArcAnimDegBegin), radians(iArcAnimDegEnd), CHORD );\n}'}</CodeBlock>
        <h3 id='時間帯に応じて背景色を変更する'>時間帯に応じて背景色を変更する．</h3>
        <Img src={`${imageFolder}anim_bg_color_hour.jpg`} />
        <CodeBlock>{'int iBackGroundRGB[][] = { { 255, 255, 204 },    // 朝\n                          { 204, 255, 255 },    // 昼\n                          { 255, 204, 102 },    // 夕\n                          {  51,   0, 255 } };  // 夜\n\nvoid setup()\n{\n}\n\nvoid draw()\n{\n  int iHour = hour();\n  int iHourElem = 0;\n  if( iHour &gt;= 5 &amp;&amp; iHour &lt;= 10 )  // 朝\n  {\n    iHourElem = 0;\n  }\n  else if( iHour &gt;= 11 &amp;&amp; iHour &lt;= 16 )  // 昼\n  {\n    iHourElem = 1;\n  }\n  else if( iHour &gt;= 17 &amp;&amp; iHour &lt;= 19 )  // 夕\n  {\n    iHourElem = 2;\n  }\n  else  // 夜\n  {\n    iHourElem = 3;\n  }\n  background( iBackGroundRGB[iHourElem][0], iBackGroundRGB[iHourElem][1], iBackGroundRGB[iHourElem][2] );\n}'}</CodeBlock>
        <h3 id='内側から多角形を増やして描画する年輪のような表現'>内側から多角形を増やして描画する年輪のような表現</h3>
        <Img src={`${imageFolder}anim_nenrin.gif`} />
        <CodeBlock>{'int iNenrinMaxWidth = 500;  // 年輪の最大幅\nint iNenrinMaxHeight = 300; // 年輪の最大高さ\n\nfloat[][] fNenrinRGB = new float[60][3];            // 年輪の色\nfloat[][][] fNenrinVertexXY = new float[60][12][2]; // 年輪の頂点座標\n\nvoid setup()\n{\n  size( 600, 300 );\n  \n  // 年輪の色初期化\n  for ( int iNenrin = 0; iNenrin &lt; fNenrinRGB.length; iNenrin ++ )\n  {\n    fNenrinRGB[iNenrin][0] = random( 255 );  // R\n    fNenrinRGB[iNenrin][1] = random( 255 );  // G\n    fNenrinRGB[iNenrin][2] = random( 255 );  // B\n  }\n  \n  // 年輪の形の初期化\n  for ( int iNenrin = 0; iNenrin &lt; fNenrinVertexXY.length; iNenrin++ )\n  {\n    float fSizeRate = ( iNenrin + 1 ) / float( fNenrinVertexXY.length );\n    float fNenrinRadiusW = iNenrinMaxWidth * fSizeRate / 2.0;\n    float fNenrinRadiusH = iNenrinMaxHeight * fSizeRate / 2.0;\n    \n    for ( int iVertex = 0; iVertex &lt; fNenrinVertexXY[iNenrin].length; iVertex++ )\n    {\n      fNenrinVertexXY[iNenrin][iVertex][0] = fNenrinRadiusW * cos( radians( 30 * iVertex ) ) * random( 1, 1.1 );\n      fNenrinVertexXY[iNenrin][iVertex][1] = fNenrinRadiusH * sin( radians( 30 * iVertex ) ) * random( 1, 1.1 );\n    }\n  }\n}\n\nvoid draw()\n{\n  background( 204 );\n  noStroke();\n  \n  translate( width/2, height/2 );\n  \n  int iNenrinOutLine = second();  // 0~59\n  \n  // 描画時は逆に外側から内側へ描画していく．\n  for ( int iNenrin = iNenrinOutLine; iNenrin &gt;= 0; iNenrin-- )\n  {    \n    beginShape();\n    for ( int iVertex = 0; iVertex &lt; fNenrinVertexXY[iNenrin].length; iVertex++ )\n    {\n      fill( fNenrinRGB[iNenrin][0], fNenrinRGB[iNenrin][1], fNenrinRGB[iNenrin][2] );\n      vertex( fNenrinVertexXY[iNenrin][iVertex][0], fNenrinVertexXY[iNenrin][iVertex][1] );\n    }\n    endShape( CLOSE );\n  }\n}'}</CodeBlock>
        <h3 id='決まった位置の星が明滅する表現'>決まった位置の星が明滅する表現</h3>
        <Img src={`${imageFolder}anim_fixed_stars.gif`} />
        <CodeBlock>{'float[][] fStarsProp = new float[20][5];\nint iColor = 0;\nvoid setup()\n{\n  size( 400, 400 ); // 配列の初期化\n  for( int fStar = 0; fStar &lt; fStarsProp.length; fStar++ ) \n  {\n    fStarsProp[fStar][0] = random( width );    // X座標\n    fStarsProp[fStar][1] = random( height );   // Y座標\n    fStarsProp[fStar][2] = random( 8, 16 );    // 大きさ\n    fStarsProp[fStar][3] = random( 255 );      // 明るさ\n    fStarsProp[fStar][4] = 1;                  // 1:明るくなる．-1:暗くなる．\n  }\n}\n\nvoid draw()\n{\n  background( 0 ); // 描画\n  for( int fStar=0; fStar &lt; fStarsProp.length; fStar++ )\n  {\n    fill( fStarsProp[fStar][3] );\n    circle( fStarsProp[fStar][0], fStarsProp[fStar][1], fStarsProp[fStar][2] );\n  }\n  // 明るさ更新\n  for( int fStar=0; fStar &lt; fStarsProp.length; fStar++ )\n  {\n    fStarsProp[fStar][3] += fStarsProp[fStar][4] * 255 / 60.0;\n    \n    // 明るくなるか暗くなるか切り替える．\n    if( fStarsProp[fStar][3] &lt; 0 || fStarsProp[fStar][3] &gt; 255 )\n    {\n      fStarsProp[fStar][4] *= -1;\n    }\n  }\n}'}</CodeBlock>
    </div>
  );
};

export default Programming06;
