const sketch = (p:any) => {
    p.setup = () => {
p.createCanvas( 400, 400 );

/*
PImage img;
img = loadImage("dinfo_logo.jpg");

p.background(img);
*/
p.background(204);

p.noStroke();  // fuchi nashi

// finger
p.fill( 0 );
p.ellipse( 300, 151, 104, 136 );

// finger mask
p.fill( 204 );
p.ellipse( 390, 130, 200, 200 );

// daen
p.fill( 0 );
p.ellipse( 200, 230, 260, 200 );

// mouth mask
p.fill( 204 );
p.ellipse( 198, 204, 116, 186 );

// ue daen mask
p.fill( 0 );
p.ellipse( 198, 196, 200, 134 );

// daen mask
p.fill( 204 );
p.ellipse( 198, 204, 90, 60 );

  };
};

export default sketch;
