interface SidebarItem {
  label: string;
  key: number;
  nestedList?: SidebarItem[];
}

const Programming: FC = () => {
  const { param } = useParams();
  const [paramCur, setParamCur] = useState<string>("");
  const navigate = useNavigate(); // useNavigateフックを利用
  const [sidebarElem,setSidebarElem] = useState<number[]>([-1,-1,-1]);


  // リンクがクリックされたときの処理
  const handleLinkClick = (selectIdx: number, selectedLayer: number) => {
    
    // URL更新
    let updatedSidebarElem = [...sidebarElem];

    updatedSidebarElem[selectedLayer] = selectIdx;
    // 選択レイヤー未満は0
    for( let iLayer = selectedLayer + 1; iLayer < updatedSidebarElem.length; iLayer++ )
    {
      updatedSidebarElem[iLayer] = 0;
    }

    setSidebarElem(updatedSidebarElem);

    navigate(`/programming/${updatedSidebarElem[0]}-${updatedSidebarElem[1]}-${updatedSidebarElem[2]}`);
  };

  // ホームがクリックされたときの処理
  const handleHomeClick = (lesson: SidebarItem) => {

    if( lesson.nestedList !== undefined )
    {
      console.log(`Home clicked in Programming: ${lesson.label}`);
      //navigate(`/programming/${lesson.link}`);
    }  
  };

  useEffect(() => {

    //if( param && param !== paramCur )
    if( param )
    {
      // URL
      const parts = param.split('-'); // 文字列を分割して配列に格納する
      const numbers = parts.map(part => parseInt(part, 10)); // 各部分を数値に変換する
      setSidebarElem( numbers);
      setParamCur(param);

      // レイヤー更新
      for( let iLayer=sidebarElem.length-1; iLayer >= 0; iLayer-- )
      {
        if( numbers[iLayer] !== 0 )
        {
          //setSidebarLauer( iLayer );
          break;
        }
      }
      console.log(`numbers in Programming: ${numbers[0]},${numbers[1]},${numbers[2]}`);
      console.log(`sidebarElem in Programming: ${sidebarElem[0]},${sidebarElem[1]},${sidebarElem[2]}`);
    }
    
  }, [param]);

  return (
    <div className="Programming">

      {/* コンテンツ */}
      <div className="main-content">
        {paramCur !== "" && <ContentFrame LessonPrefix={LessonPrefix} param={paramCur} />}
      </div>
      {/* Navigatorコンポーネント */}
      {sidebarElem[0] >= 0 && <Navigator onLinkClicked={handleLinkClick} onHomeClicked={handleHomeClick} sidebarElem={sidebarElem} sidebarItems={sidebarData} />}
    </div>
  );
};

export default Programming;
