// Navigator.tsx

import React, { FC, useState, useEffect } from 'react';
import SideBar from './SideBar';
import './Navigator.css';

interface NavigatorProps  {
  onLinkClicked: (selectIdx: number, layer: number) => void;
  onHomeClicked: (lesson: SidebarItem) => void;
  sidebarElem: number[];
  sidebarItems: SidebarItem[];
}

interface SidebarItem {
  label: string;
  key: number;
  nestedList?: SidebarItem[];
}

export const Navigator: FC<NavigatorProps > = ({ onLinkClicked, onHomeClicked, sidebarElem, sidebarItems }) => {
  const [isSideBarOpen, setIsSideBarOpen] = useState<boolean>(false); // サイドバーの開閉状態を管理する状態
  const [sidebarList, setSidebarList] = useState<SidebarItem[]>();
  const [dispSidebarLayer,setDispSidebarLayer] = useState<number>( ( sidebarElem[0] ===  0 && sidebarElem[1] ===  0 ) ? 0 : 1 );
  const [ LessonText, setLessonText ] = useState<string>();
  const [showSidebar, setShowSidebar] = useState<boolean>(false);
  
  // リンクがクリックされたときの処理
  const handleLinkClick = (selectIdx: number) => {
    //console.log(`Link clicked in Navigator: ${item.label}`);

    onLinkClicked(selectIdx,dispSidebarLayer);  // 親へ通知

    // レイヤー更新
    let nextLayer = dispSidebarLayer;
    sidebarElem[dispSidebarLayer] = selectIdx;
    if( dispSidebarLayer < sidebarElem.length - 2 )        // まだ下に階層がある
    //    !( ( sidebarElem[0] === 0 ) && selectIdx === 0 ) )  // トップレイヤーの最初の項目はもぐらない
    {

      setShowSidebar(false);
      nextLayer = dispSidebarLayer+1; 
    }

    setDispSidebarLayer(nextLayer);
  };

  // 戻るがクリックされた時
  const handleReturnClick = () => {
    setShowSidebar(false);
    setDispSidebarLayer(dispSidebarLayer-1);
  };
    
  // ホームボタンがクリックされたときの処理
  const handleHomeClick = (item: SidebarItem) => {
    onHomeClicked(item);  // 親へ通知
    console.log(`handleHomeClick in Navigator:`);
    setIsSideBarOpen(true);
  };

  // トグルがクリックされたときの処理
  const handleToggleClick = () => {
    setIsSideBarOpen(!isSideBarOpen);
  };

  useEffect(() => {

    // サイドバー項目
    let sidebarList: SidebarItem[] = sidebarItems;
    for( let iLayer = 1; iLayer < (dispSidebarLayer+1); iLayer++ )
    {
      let elem = sidebarElem[iLayer-1];
      let sidebarCurrent: SidebarItem = sidebarList[elem];
      if (sidebarCurrent.nestedList) {
        sidebarList = sidebarCurrent.nestedList;
        console.log(`nested in Navigator`);
      }

      // 授業回名
      setLessonText(sidebarCurrent.label);
    }

    console.log(`dispSidebarLayer in Navigator : ${dispSidebarLayer}`);
    console.log(`sidebarElem in Navigator: ${sidebarElem[0]},${sidebarElem[1]},${sidebarElem[2]}`);
    setSidebarList(sidebarList);

  }, [sidebarItems, dispSidebarLayer]);

  // サイドバー表示リストが準備完了
  useEffect(() => {
    setShowSidebar(true);
  }, [sidebarList])

  return (
    <div className="navigator-style">

      {/* 半透明のボックス */}
      {/*<div className="overlay-box"></div>*/}

      {/* トグルボタン */}
      <button className="toggle-button" onClick={() => handleToggleClick()} >
          {isSideBarOpen ? '×' : '□'}
      </button>
      
      {/* サイドバーコンポーネント */}
      { showSidebar && <SideBar onLinkClicked={handleLinkClick} isOpen={isSideBarOpen} onReturnClick={handleReturnClick} isReturn={(dispSidebarLayer>0)} dispList={sidebarList} LessonText={LessonText} />}
      
      {/* ホームボタン */}
      {/*<button className="button-home" onClick={() => handleHomeClick(selectedLesson)}>🏠</button>*/}

    </div>
  );
};
