// Programming02.tsx

import React, { useEffect} from 'react';
import { useLocation } from 'react-router-dom';
import { LinkP, JumpToDefaultScroll, SketchCode, CodeBlock } from '../ContentCommon';
import { MathJaxProvider, MathJaxFormula } from 'mathjax3-react';
import '../ContentCommon.css';

const imageFolder: string = "/musabi-react/images/programming/";

interface ContentProps {
  practiceId: string;
}

const Programming02: React.FC<ContentProps> = ({ practiceId }) => {

  /* URLから初期スクロール位置 */
  const location = useLocation();
  useEffect(() => {
    JumpToDefaultScroll(practiceId, location as any );    
  }, [practiceId,location]);
  return (
    <div className="common-style">
      <h1 id='h0'>第２回：図形描画</h1>
      <hr color='#8dacf9' />
        <h2 id='processing再入門'>Processing再入門</h2>
        <h3 id='自分のpcにおけるprocessingの起動'>自分のPCにおけるProcessingの起動</h3>
        <p>以下のリンクよりProcessingをダウンロードする．</p>
        <p><a href="http://processing.org/" target='_blank'>http://processing.org/</a></p>
        <h4 id='mac'>Mac</h4>
        <p>「アプリケーション」から実行</p>
        <img src={`${imageFolder}exe_mac.png`} alt="exe_mac" />
        <h4 id='windows'>Windows</h4>
        <p>Zipファイルを解凍し，フォルダ内の「processing.exe」 を実行</p>
        <img src={`${imageFolder}exe_windows.png`} alt="exe_windows" />
        <h3 id='フォントエディタ・コンソール）の変更任意）'>フォント（エディタ・コンソール）の変更（任意）</h3>
        <ul>
        <li><p>変更方法：ファイル &gt; 設定 &gt; エディタとコンソールのフォント</p>
        </li>
        <li><p>デフォルトのフォント Source Code Pro は以下のことができない．</p>
          <ul>
            <li><p>プログラム及びコメントへの日本語（全角）入力</p></li>
            <li><p>コンソールへの日本語（全角）出力</p></li>
            <li><p>これらを有効にするには，日本語対応のフォントに変更してください．
            （MS ゴシック など）</p></li>
          </ul>
        </li>
        </ul>
        <img src={`${imageFolder}font_change_02.png`} alt="font_change_02" />
        <h3 id='プログラムを書く際に忘れがちなこと'>プログラムを書く際に忘れがちなこと</h3>
        <ul>
          <li><p>半角英数と数字を用いる（コメントは除く）</p>
          </li>
          <li><p>文末にはセミコロン<code>;</code>を入れる（忘れがち！）</p>
          <img src={`${imageFolder}first_sample_statement.jpg`} alt="first_sample_statement" />
          </li>
          <li><p>大文字と小文字の区別があるので注意</p>
          </li>
        </ul>
        <h3 id='コメント'>コメント</h3>
        <ul>
        <li><p>プログラムに影響を与えない影響を与えない文字列のこと</p></li>
        <li><p>技術的には，コンパイル時に無視される部分</p></li>
        <li><p>以下の２種類を使い分ける．</p></li>

        </ul>
        <h4 id='スラッシュを２つ使ったもの'>スラッシュ<code>/</code>を２つ使ったもの．</h4>
        <CodeBlock>{'//　← 以降のこの部分（１行）がコメントとしてプログラムから無視される'}</CodeBlock>
        <h4 id='スラッシュとアスタリスクを組み合わせたもの'>スラッシュとアスタリスク<code>*</code>を組み合わせたもの．</h4>
        <CodeBlock>{'/**\n  挟まれている複数の行が\n  プログラムから無視される\n*/'}</CodeBlock>
        <h4 id='用途'>用途</h4>
        <ul>
        <li><p>プログラムを（人が）読みやすくするためのメモとして用いる．</p></li>
        <li><p>プログラムの一部を残したまま一時的に実行しないようにする．</p></li>

        </ul>
        <h4 id='例1-1'>例1</h4>
        <p>命令のメモとして用いる．</p>
        <img src={`${imageFolder}divide_window.png`} alt="divide_window" />
        <CodeBlock>{'// ウィンドウを分割する\nline(0, 50, 100, 50); // yoko\nline(50, 0, 50, 100); // tate\n\n// 円を描画する\ncircle(25, 25, 50);'}</CodeBlock>
        <h4 id='例2-1'>例2</h4>
        <p>プログラムの一部を無効にする．</p>
        <p>分かりやすく一時的にグリッドを引き，提出版では非表示にする，という使い方ができる．</p>
        <img src={`${imageFolder}divide_window_noline.png`} alt="divide_window_noline" />
        <CodeBlock>{'/**\n\n// ウィンドウを分割する\nline(0, 50, 100, 50); // yoko\nline(50, 0, 50, 100); // tate\n\n*/\n\n// 円を描画する\ncircle(25, 25, 50);'}</CodeBlock>
        <h4 id='補足'>補足</h4>
        <p><strong>課題提出においてコメントは重要です．</strong></p>
        <ul>
        <li><p>プログラムの実行に影響を与えないと軽視しがちですが</p>
        </li>
        <li><p><u>コメントが含まれた状態のスケッチを提出</u>してもらい，それも加味して評価を行います．</p>
        <ul>
        <li><p><u>コメントを十分に含んだ分かりやすいスケッチ</u>が高く評価されます．</p></li>
        <li><p>コメントがまったく含まれていない場合，作品がどれほど良質であっても評価が下方修正されます．</p></li>

        </ul>
        </li>

        </ul>
        <p><a href="https://processing.org/reference/comment.html" target='_blank'>https://processing.org/reference/comment.html</a></p>
        <h3 id='コーディングに便利なキーボードの使い方win）'>コーディングに便利なキーボードの使い方（Win）</h3>
        <ul>
        <li><p>エディタウィンドウでプログラムを記述するにあたり，以下のキーの使い分けを行えると便利です．</p></li>
        <li><p>コーディングの効率が上がります．</p></li>
        <li><p>できるだけ意識して使ってみましょう</p></li>

        </ul>
        <h4 id='文字消去'>文字消去</h4>
        <ul>
        <li><p>１文字先を消去： <strong>Delete</strong></p></li>
        <li><p>１文字前を消去：<strong>BackSpace</strong></p></li>

        </ul>
        <h4 id='カーソル移動'>カーソル移動</h4>
        <ul>
        <li><p>行の先頭：<strong>Home</strong></p></li>
        <li><p>行の末尾： <strong>End</strong></p></li>
        <li><p>前のワードの先頭： <strong>Ctrl  ←</strong></p></li>
        <li><p>次の単語の末尾：<strong>Ctrl  →</strong></p></li>
        <li><p>ページ送り：<strong>PgUp</strong></p></li>
        <li><p>ページ戻り：<strong>PgDn</strong></p></li>

        </ul>
        <h4 id='選択'>選択</h4>
        <ul>
        <li><p>カーソル位置から前後に選択範囲を延ばす：<strong>Shift  ← or →</strong></p></li>
        <li><p>カーソル位置から行単位で選択範囲を延ばす：<strong>Shift  ↑ or ↓</strong></p></li>
        <li><p>カーソル位置から前後の１単語を選択：<strong>Shift Ctrl  ← or →</strong></p></li>

        </ul>
        <h2 id='h1'>連続した線による表現</h2>
        <img src={`${imageFolder}draw_line_practice02.png`} alt="draw_line_practice02" />
        <h3 id='線を描画する'>線を描画する</h3>
        <p><strong><code>line()</code></strong></p>
        <h4 id='構文-10'>構文</h4>
        <p><code>line( 始点X座標値, 始点Y座標値, 終点X座標値, 終点Y座標値 )</code></p>
        <h4 id='説明-10'>説明</h4>
        <p>２点間に１本の線を描画する．</p>
        <h4 id='引数-10'>引数</h4>
        <p>始点と終点のX,Y座標値を記述する．</p>
        <h4 id='例1-2'>例1</h4>
        <p>始点X,Y座標値:（30, 20）から終点X,Y座標値: ( 85, 75)に線を描画する．</p>
        <img src={`${imageFolder}line_0.png`} alt="line_0" />
        <CodeBlock>{'line(30, 20, 85, 75);'}</CodeBlock>
        <h4 id='例2-2'>例2 </h4>
        <img src={`${imageFolder}line_three.png`} alt="line_three" />
        <CodeBlock>{'line( 20, 20, 40, 40 );\nline( 40, 40, 50, 70 );  // 一つ目の線の終わりの点からスタート\nline( 50, 70, 80, 80 );  // 二つ目の線の終わりの点からスタート'}</CodeBlock>
        <p><a href="https://processing.org/reference/line_.html" target='_blank'>https://processing.org/reference/line_.html</a></p>
        <h3 id='線の太さを設定する'>線の太さを設定する</h3>
        <p><strong><code>strokeWeight()</code></strong></p>
        <h4 id='構文-11'>構文</h4>
        <p><code>strokeWeight( 太さピクセル数 )</code></p>
        <h4 id='説明-11'>説明</h4>
        <p>線の太さをピクセル単位で設定する．</p>
        <h4 id='引数-11'>引数</h4>
        <p>太さのピクセル数を記述する．</p>
        <h4 id='例-10'>例</h4>
        <img src={`${imageFolder}strokeWeight_.png`} alt="strokeWeight_" />
        <CodeBlock>{'size(400, 400);\nstrokeWeight( 4 );   // light\nline(80, 80, 320, 80);\nstrokeWeight( 16 );  // middle\nline(80, 160, 320, 160);\nstrokeWeight( 40 );  // bold\nline(80, 280, 320, 280);'}</CodeBlock>
        <p><a href="https://processing.org/reference/strokeWeight_.html" target='_blank'>https://processing.org/reference/strokeWeight_.html</a></p>
        <h3 id='線の端の形状を変更する'>線の端の形状を変更する．</h3>
        <p><strong><code>strokeCap()</code></strong></p>
        <ul>
        <li><p>線の終端の形状スタイルを変更する．</p>
        </li>
        <li><p>３種類のスタイルを，対応した引数で切り替えることができる．</p>
        <ul>
        <li><p>ROUND（丸）</p></li>
        <li><p>SQUARE（四角形）</p></li>
        <li><p>PROJECT（突き出し）</p></li>

        </ul>
        </li>

        </ul>
        <h4 id='構文-12'>構文</h4>
        <p><code>strokeCap( ROUND or SQUARE or PROJECT )</code></p>
        <h4 id='例1-3'>例1</h4>
        <p>線の端の形状を丸くする．</p>
        <img src={`${imageFolder}draw_stroke_cap_round.png`} alt="draw_stroke_cap_round" />
        <CodeBlock>{'strokeWeight( 12 );\nstrokeCap( ROUND );\nline( 20, 50, 50, 50 );\nline( 50, 50, 60, 80 );'}</CodeBlock>
        <h4 id='例2-3'>例2</h4>
        <p>終端を四角形にする．</p>
        <img src={`${imageFolder}draw_stroke_cap_square.png`} alt="draw_stroke_cap_square" />
        <CodeBlock>{'strokeWeight( 12 );\nstrokeCap( SQUARE );\nline( 20, 50, 50, 50 );\nline( 50, 50, 60, 80 );'}</CodeBlock>
        <h4 id='例3-1'>例3</h4>
        <p>終端を突き出し形状にする．</p>
        <img src={`${imageFolder}draw_stroke_cap_project.png`} alt="draw_stroke_cap_project" />
        <CodeBlock>{'strokeWeight( 12 );\nstrokeCap( PROJECT );\nline( 20, 50, 50, 50 );\nline( 50, 50, 60, 80 );'}</CodeBlock>
        <p><a href="https://processing.org/reference/strokeCap_.html" target='_blank'>https://processing.org/reference/strokeCap_.html</a></p>
        <h3 id='塗りつぶしを消去する'>塗りつぶしを消去する</h3>
        <p><strong><code>noFill()</code></strong></p>
        <h4 id='構文-13'>構文</h4>
        <p><code>noFill()</code></p>
        <h4 id='説明-12'>説明</h4>
        <p>塗りつぶしを無効にする．</p>
        <h4 id='例-11'>例</h4>
        <img src={`${imageFolder}noFill_.png`} alt="noFill_" />
        <CodeBlock>{'square( 15, 10, 55 );\n\nnoFill();\nsquare( 30, 20, 55 );'}</CodeBlock>
        <p><a href="https://processing.org/reference/noFill_.html" target='_blank'>https://processing.org/reference/noFill_.html</a></p>
        <h2 id='h2'>演習１</h2>
        <ol >
        <li><p><code>line()</code>を複数使用し，画面<u>左端から始まり右端で終わる連続した線</u>を自由に描画する．</p>
        <p>条件</p>
        <ul>
        <li><p>スクリーンサイズ: 400, 400</p></li>
        <li><p>線の数や長さは自由</p></li>

        </ul>
        <img src={`${imageFolder}draw_line_practice01.png`} alt="draw_line_practice01" />
        </li>
        <li><p>各線の太さや終端の形状を変化させ，より動きのある画面を作ってみる．</p>
        </li>

        </ol>
        <img src={`${imageFolder}draw_line_practice02.png`} alt="draw_line_practice02" />
        <ol >
        <li><p>作成したプログラムは後の演習で再利用するので，分かりやすい場所に保存しておく．</p></li>
        <li><p>作例のプログラム</p></li>

        </ol>
        <CodeBlock>{'size( 400, 400 );\n\nstrokeCap( PROJECT );\nstrokeWeight( 4 );\nline( 0, 40, 50, 39 );\n\nstrokeWeight( 12 );\nline( 50, 39, 100, 129 );\n\nstrokeWeight( 2 );\nline( 100, 129, 150, 12 );\n\nstrokeWeight( 6 );\nline( 150, 12, 200, 348 );\n\nstrokeCap( ROUND );\nstrokeWeight( 24 );\nline( 200, 348, 250, 170 );\n\nstrokeCap( PROJECT );\nstrokeWeight( 18 );\nline( 250, 170, 300, 48 );\nstrokeWeight( 2 );\nline( 300, 48, 350, 178 );\n\nstrokeWeight( 1 );\nline( 350, 178, 400, 189 );'}</CodeBlock>
        <h2 id='h3'>色の書式HSBによる表現</h2>
        <img src={`${imageFolder}lines_hsb.png`} alt="lines_hsb" />
        <h3 id='線の色を変える'>線の色を変える</h3>
        <p><strong><code>stroke()</code></strong></p>
        <h4 id='構文-14'>構文</h4>
        <p><code>stroke(  R値, G値, B値  )</code></p>
        <h4 id='説明-13'>説明</h4>
        <ul>
        <li><p>点，線，図形の枠線の色の変更を行う．</p>
        <ul>
        <li><p>デフォルトはBlack(黒)色</p></li>

        </ul>
        </li>
        <li><p>図形描画を行う命令の前に記述する必要がある．</p>
        <ul>
        <li><p>直前の行である必要はない．</p></li>

        </ul>
        </li>
        <li><p>もう一度<code>stroke()</code>が行われるまで，ここで指定した色が線色として使われ続ける．</p>
        <ul>
        <li><p>色を変更したい場合，別の色を<code>stroke()</code>を用いて再度指定する必要がある．</p></li>

        </ul>
        </li>

        </ul>
        <h4 id='引数-12'>引数</h4>
        <p><strong>RGB値</strong>それぞれを<strong>0~255</strong>の間で決定する．</p>
        <h4 id='例1-4'>例1</h4>
        <p>色を指定して線を描画する．</p>
        <img src={`${imageFolder}stroke_line.png`} alt="stroke_line" />
        <CodeBlock>{'strokeWeight( 2 );\nstroke( 0, 0, 255 );\nline( 20, 20, 85, 75 );'}</CodeBlock>
        <h4 id='例2-4'>例2</h4>
        <p>色を指定して図形の（枠）線を描画する．</p>
        <img src={`${imageFolder}stroke_square.png`} alt="stroke_square" />
        <CodeBlock>{'strokeWeight( 2 );\nstroke( 0, 0, 255 );\nsquare( 25, 25, 50 );'}</CodeBlock>
        <h4 id='例3-2'>例3</h4>
        <img src={`${imageFolder}line_three_color.png`} alt="line_three_color" />
        <CodeBlock>{'strokeWeight( 2 );\nstroke(255,0,0);        // R\nline( 20, 20, 40, 40 );\n\nstroke(0,255,0);        // G\nline( 40, 40, 50, 70 );\n\nstroke(0,0,255);        // B\nline( 50, 70, 80, 80 );'}</CodeBlock>
        <p><a href="https://processing.org/reference/stroke_.html" target='_blank'>https://processing.org/reference/stroke_.html</a></p>
        <h3 id='カラーモード'>カラーモード</h3>
        <p><strong><code>colorMode()</code></strong></p>
        <p>色の書式を変える．</p>
        <h4 id='構文-15'>構文</h4>
        <p>いずれかを用いる．</p>
        <h5 id='rgbモードを指定'>RGBモードを指定</h5>
        <p><code>colorMode( RGB, 最大値 )</code></p>
        <h5 id='rgbモードを指定各チャンネルの最大値を指定）'>RGBモードを指定（各チャンネルの最大値を指定）</h5>
        <p><code>colorMode( RGB, Rの最大値, Gの最大値, Bの最大値 )</code></p>
        <h5 id='rgbモードを指定各チャンネルとアルファの最大値を指定）'>RGBモードを指定（各チャンネルとアルファの最大値を指定）</h5>
        <p><code>colorMode( RGB, Rの最大値, Gの最大値, Bの最大値, アルファの最大値 )</code></p>
        <h5 id='hsbモードを指定'>HSBモードを指定</h5>
        <p><code>colorMode( HSB, 最大値 )</code></p>
        <h5 id='hsbモードを指定各チャンネルの最大値を指定）'>HSBモードを指定（各チャンネルの最大値を指定）</h5>
        <p><code>colorMode( HSB, 色相の最大値, 彩度の最大値, 輝度の最大値 )</code></p>
        <h5 id='hsbモードを指定各チャンネルとアルファの最大値を指定）'>HSBモードを指定（各チャンネルとアルファの最大値を指定）</h5>
        <p><code>colorMode( HSB, 色相の最大値, 彩度の最大値, 輝度の最大値, アルファの最大値 )</code></p>
        <h4 id='説明-14'>説明</h4>
        <ul>
        <li><p><code>fill()</code>, <code>stroke()</code>, <code>background()</code>における引数の解釈を変更する．</p>
        <ul>
        <li><p>上記の３つの命令はデフォルトでは引数をRGB値0~255の間で指定する．</p></li>

        </ul>
        </li>
        <li><p>例えば<code>colorMode( RGB, 1.0 )</code>を記述すると，以降の色の指定はRGB値0.0~1.0の間で指定する．</p>
        </li>
        <li><p>また，<code>colorMode( HSB, 360, 100, 100 )</code>を記述すると，以降の色の指定はHSB値0.0~1.0の間で指定する．</p>
        <ul>
        <li><p>HSBは色相（Hue），彩度（Saturation），明度（Brightness）</p></li>

        </ul>
        </li>
        <li><p>難しいと感じたら，<u>通常はこの命令は使わず，HSBを使いたい時だけ以下をつかう</u>とよい．</p>
        <p><code>colorMode( HSB, 360, 100, 100 );</code></p>
        </li>
        <li><p>基本的には<strong>プログラム冒頭で一度だけ</strong>行えばよい．</p>
        </li>

        </ul>
        <h4 id='引数-13'>引数</h4>
        <ul>
        <li><p>RGB or HSB （カラーモデル）</p></li>
        <li><p>各チャンネルの最大値，<strong>アルファ</strong>（透明度）の最大値</p></li>

        </ul>
        <h4 id='例1-5'>例1</h4>
        <p>RGBモード最大値1.0で指定した例</p>
        <img src={`${imageFolder}colormode_rgb.png`} alt="colormode_rgb" />
        <CodeBlock>{'colorMode( RGB, 1.0 );\nfill( 0.1, 0.5, 0.7 );\nsquare( 25, 25, 50 );'}</CodeBlock>
        <h4 id='例2-5'>例2</h4>
        <p>HSBモードで指定した例</p>
        <img src={`${imageFolder}colormode_hsb.png`} alt="colormode_hsb" />
        <CodeBlock>{'colorMode( HSB, 360, 100, 100 );\nfill( 20, 90, 100 );\nsquare( 25, 25, 50 );'}</CodeBlock>
        <h4 id='例3-3'>例3</h4>
        <p>HSBモードでアルファを指定した例</p>
        <img src={`${imageFolder}colormode_hsb_alpha.png`} alt="colormode_hsb_alpha" />
        <CodeBlock>{'colorMode( HSB, 360, 100, 100, 100 );\nfill( 20, 90, 100 );\nsquare( 25, 25, 50 );\n\nfill( 220, 90, 100, 50 );\nsquare( 35, 35, 50 );'}</CodeBlock>
        <p><a href="https://processing.org/reference/colorMode_.html" target='_blank'>https://processing.org/reference/colorMode_.html</a></p>
        <h2 id='h4'>演習２</h2>
        <p>前回の演習で作成したプログラムに色を付け，線に色相のグラデーションをつける．</p>
        <ul>
        <li><p>条件</p>
        <ul>
        <li><p><code>colorMode()</code>を用いてHSBモードで色を設定すること．</p></li>
        <li><p>色相の違いのみでグラデーションをつけること．</p></li>

        </ul>
        </li>

        </ul>
        <img src={`${imageFolder}lines_hsb.png`} alt="lines_hsb" />
        <p>作例のプログラム</p>
        <CodeBlock>{'size( 400, 400 );\ncolorMode( HSB, 360, 100, 100 );\nstrokeCap( PROJECT );\nstrokeWeight( 4 );\nstroke( 200, 90, 80 );\nline( 0, 40, 50, 39 );\n\nstrokeWeight( 12 );\nstroke( 210, 90, 80 );\nline( 50, 39, 100, 129 );\n\nstrokeWeight( 2 );\nstroke( 220, 90, 80 );\nline( 100, 129, 150, 12 );\n\nstrokeWeight( 6 );\nstroke( 230, 90, 80 );\nline( 150, 12, 200, 348 );\n\nstrokeCap( ROUND );\nstrokeWeight( 24 );\nstroke( 240, 90, 80 );\nline( 200, 348, 250, 170 );\n\nstrokeCap( PROJECT );\nstrokeWeight( 18 );\nstroke( 250, 90, 80 );\nline( 250, 170, 300, 48 );\n\nstrokeWeight( 2 );\nstroke( 260, 90, 80 );\nline( 300, 48, 350, 178 );\n\nstrokeWeight( 1 );\nstroke( 270, 90, 80 );\nline( 350, 178, 400, 189 );'}</CodeBlock>
        <p>blendmode</p>
        <h2 id='h5'>ブレンドモードによる表現</h2>
        <img src={`${imageFolder}sippou.png`} alt="sippou" />
        <h3 id='ブレンドモードを設定する'>ブレンドモードを設定する．</h3>
        <p><strong><code>blendMode()</code></strong></p>
        <h4 id='構文-16'>構文</h4>
        <p><code>blendMode( モード )</code></p>
        <h4 id='説明-15'>説明</h4>
        <p>図形描画時に，下に描かれた内容に対する効果を変更する．</p>
        <h4 id='引数-14'>引数</h4>
        <p>以下のモードからいずれかを記述する．</p>
        <ul>
        <li><p><code>BLEND</code>：通常（デフォルト）</p></li>
        <li><p><code>ADD</code>：加算</p></li>
        <li><p><code>SUBTRACT</code>：減算</p></li>
        <li><p><code>DARKEST</code>：比較（暗）</p></li>
        <li><p><code>LIGHTEST</code>：比較（明）</p></li>
        <li><p><code>DIFFERENCE</code>：差の絶対値</p></li>
        <li><p><code>EXCLUSION</code>：除外（マイルド）</p></li>
        <li><p><code>MULTIPLY</code>：乗算</p></li>
        <li><p><code>SCREEN</code>：スクリーン</p></li>
        <li><p><code>REPLACE</code>：置換（透明度無効化）</p></li>

        </ul>
        <p>Photoshop等の描画モードと大まかに対応している．</p>
        <h4 id='例1加算'>例1：加算</h4>
        <p>図形を重ねるほど白に近づく．
        白の上に重ねると何も変化が起きない．</p>
        <img src={`${imageFolder}blendmode_add.png`} alt="blendmode_add" />
        <CodeBlock>{'size( 200, 200 );\nnoStroke();\n\nblendMode( ADD );\n\nbackground( 0 );\n\nfill( 255, 50, 50 );\ncircle( 100, 85, 100 );\n\nfill( 50, 255, 50 );\ncircle( 80, 120, 100 );\n\nfill( 50, 50, 255 );\ncircle( 120, 120, 100 );'}</CodeBlock>
        <h4 id='例2減算'>例2：減算</h4>
        <p>図形を重ねるほど黒に近づく．
        白の上に重ねると色が補色になるので使いづらい．</p>
        <img src={`${imageFolder}blendmode_subtract.png`} alt="blendmode_subtract" />
        <CodeBlock>{'size( 200, 200 );\nnoStroke();\n\nblendMode( SUBTRACT );\n\nbackground( 255 );\n\nfill( 255, 50, 50 );\ncircle( 100, 85, 100 );\n\nfill( 50, 255, 50 );\ncircle( 80, 120, 100 );\n\nfill( 50, 50, 255 );\ncircle( 120, 120, 100 );'}</CodeBlock>
        <h4 id='例3比較暗）'>例3：比較（暗）</h4>
        <p>図形を重ねるとRGB各値の小さい値が選ばれる．
        黒の上に重ねると何も変化が起きない．</p>
        <img src={`${imageFolder}blendmode_darkest.png`} alt="blendmode_darkest" />
        <CodeBlock>{'size( 200, 200 );\nnoStroke();\n\nblendMode( DARKEST );\n\nbackground( 255 );\n\nfill( 255, 50, 50 );\ncircle( 100, 85, 100 );\n\nfill( 50, 255, 50 );\ncircle( 80, 120, 100 );\n\nfill( 50, 50, 255 );\ncircle( 120, 120, 100 );'}</CodeBlock>
        <h4 id='例4比較明）'>例4：比較（明）</h4>
        <p>図形を重ねるとRGB各値の大きい値が選ばれる．
        白の上に重ねると何も変化が起きない．</p>
        <img src={`${imageFolder}blendmode_lightest.png`} alt="blendmode_lightest" />
        <CodeBlock>{'size( 200, 200 );\nnoStroke();\n\nblendMode( LIGHTEST );\n\nbackground( 0 );\n\nfill( 255, 50, 50 );\ncircle( 100, 85, 100 );\n\nfill( 50, 255, 50 );\ncircle( 80, 120, 100 );\n\nfill( 50, 50, 255 );\ncircle( 120, 120, 100 );'}</CodeBlock>
        <h4 id='例5差の絶対値'>例5：差の絶対値</h4>
        <p>図形を重ねると，下の色とのRGB各値の差が選ばれる．
        同色の図形を重ね合わせ，重なった部分を抜く表現に有用．</p>
        <img src={`${imageFolder}blendmode_difference.png`} alt="blendmode_difference" />
        <CodeBlock>{'size( 200, 200 );\nnoStroke();\nblendMode( DIFFERENCE );\n\nbackground( 0 );\n\nfill( 255, 50, 50 );\ncircle( 100, 85, 100 );\n\nfill( 255, 50, 50 );\ncircle( 80, 120, 100 );\n\nfill( 255, 50, 50 );\ncircle( 120, 120, 100 );'}</CodeBlock>
        <h4 id='例6除外'>例6：除外</h4>
        <p>差分と同じだが，RGBの差をマイルドにしたもの．
        重なった部分が完全には抜かれない．</p>
        <img src={`${imageFolder}blendmode_exclusion.png`} alt="blendmode_exclusion" />
        <CodeBlock>{'size( 200, 200 );\nnoStroke();\n\nblendMode( EXCLUSION );\n\nbackground( 0 );\n\nfill( 255, 50, 50 );\ncircle( 100, 85, 100 );\n\nfill( 255, 50, 50 );\ncircle( 80, 120, 100 );\n\nfill( 255, 50, 50 );\ncircle( 120, 120, 100 );'}</CodeBlock>
        <h4 id='例7乗算'>例7：乗算</h4>
        <p>図形を重ねるほど黒に近づく．
        減算と近いが，白の上に重ねても色が補色にならないのでこちらが使いやすい．</p>
        <img src={`${imageFolder}blendmode_multiply.png`} alt="blendmode_multiply" />
        <CodeBlock>{'size( 200, 200 );\nnoStroke();\n\nblendMode( MULTIPLY );\n\nbackground( 255 );\n\nfill( 255, 0, 0 );\ncircle( 100, 85, 100 );\n\nfill( 0, 255, 0 );\ncircle( 80, 120, 100 );\n\nfill( 0, 0, 255 );\ncircle( 120, 120, 100 );'}</CodeBlock>
        <h4 id='例8スクリーン'>例8：スクリーン</h4>
        <p>図形を重ねるほど白に近づく．
        加算と近いが，若干結果が異なるので好みで使い分ける．</p>
        <img src={`${imageFolder}blendmode_screen.png`} alt="blendmode_screen" />
        <CodeBlock>{'size( 200, 200 );\nnoStroke();\n\nblendMode( SCREEN );\n\nbackground( 0 );\n\nfill( 255, 50, 50 );\ncircle( 100, 85, 100 );\n\nfill( 50, 255, 50 );\ncircle( 80, 120, 100 );\n\nfill( 50, 50, 255 );\ncircle( 120, 120, 100 );'}</CodeBlock>
        <h2 id='h6'>演習３</h2>
        <p>七宝（しっぽう）模様を作る．</p>
        <img src={`${imageFolder}sippou.png`} alt="sippou" />
        <p>条件</p>
        <ul>
        <li><p>スクリーンサイズ：300 × 300</p></li>

        </ul>
        <ol>
        <li><p>９つの正円を描画する．</p>
        <img src={`${imageFolder}sippou_6circles.png`} alt="sippou_6circles" />
        </li>
        <li><p>ステップ1の上に16個の正円を描画する．</p>
        <img src={`${imageFolder}sippou_16circles.png`} alt="sippou_16circles" />
        </li>
        <li><p>ブレンドモード（差の絶対値）命令文を記述する．</p>
        <img src={`${imageFolder}sippou_blendmode.png`} alt="sippou_blendmode" />
        </li>
        <li><p>以下の色指定を記述する．</p>
        <ul>
        <li><p><code>background( 255, 0, 0 );</code></p></li>
        <li><p><code>fill( 0, 255, 255 );</code></p></li>

        </ul>
        <img src={`${imageFolder}sippou.png`} alt="sippou" />
        </li>
        <li><p>以下，完成プログラム</p>
        <CodeBlock>{'size( 300, 300 );\n\nblendMode( DIFFERENCE );\n\nbackground( 255, 0, 0 );\n\nfill( 0, 255, 255 );\n\ncircle( 50, 50, 100 );\ncircle( 150, 50, 100 );\ncircle( 250, 50, 100 );\n\ncircle( 50, 150, 100 );\ncircle( 150, 150, 100 );\ncircle( 250, 150, 100 );\n\ncircle( 50, 250, 100 );\ncircle( 150, 250, 100 );\ncircle( 250, 250, 100 );\n\n\ncircle( 0, 0, 100 );\ncircle( 100, 0, 100 );\ncircle( 200, 0, 100 );\ncircle( 300, 0, 100 );\n\ncircle( 0, 100, 100 );\ncircle( 100, 100, 100 );\ncircle( 200, 100, 100 );\ncircle( 300, 100, 100 );\n\ncircle( 0, 200, 100 );\ncircle( 100, 200, 100 );\ncircle( 200, 200, 100 );\ncircle( 300, 200, 100 );\n\ncircle( 0, 300, 100 );\ncircle( 100, 300, 100 );\ncircle( 200, 300, 100 );\ncircle( 300, 300, 100 );'}</CodeBlock>
        </li>

        </ol>
        <h2 id='h7'>円弧による表現</h2>
        <img src={`${imageFolder}ougi.png`} alt="ougi" />
        <h3 id='度度数法）をラジアン弧度法）に変換する'>度（度数法）をラジアン（弧度法）に変換する．</h3>
        <p><strong><code>radians()</code></strong></p>
        <h4 id='構文-17'>構文</h4>
        <p><code>radians( 角度 )</code></p>
        <h4 id='説明-16'>説明</h4>
        <ul>
          <li><p>角度（°）をラジアン（rad）単位に変換する．</p>
          </li>
          <li><p>簡単な数式で書くと以下のようになる</p>
            <div>
              <MathJaxProvider>
                <MathJaxFormula formula="$$ラジアン=\frac{角度\times\pi}{180}$$" />
              </MathJaxProvider>
            </div>
          </li>
          <li><p>角度とラジアンの対応表</p>
          <figure><table width="50%">
          <thead>
          <tr><th>度（°）</th><th>ラジアン（rad）</th></tr></thead>
          <tbody><tr><td>0</td><td>0</td></tr><tr><td>45</td><td>0.7853…</td></tr><tr><td>90</td><td>1.5708…</td></tr><tr><td>180</td><td>3.1415…（π）</td></tr><tr><td>270</td><td>4.7123…</td></tr><tr><td>360</td><td>6.2831…（2π）</td></tr></tbody>
          </table></figure>
        </li>
        <li><p>角度を指定する必要がある場合に便利な数値変換命令．</p>
        <ul>
        <li><p>円弧を描画したり，図形の回転を行う際に．</p></li>

        </ul>
        </li>
        <li><p>我々が日常的に親しみのある単位は度数法「°」表記であるが，
        Processingではをラジアン（弧度法）単位へ変換する必要がある．</p>
        <ul>
        <li><p>Processing で角度を用いるにあたり，おおくは角度をラジアン単位で指定する必要がある．</p></li>

        </ul>
        </li>
        <li><p>このような数値の変換を行う命令は，<strong>引数として数値に置き換えて命令へ組み込むことができる</strong>．
        下の例では<code>print()</code>に組み込み，変換後の数値をコンソールに表示している．</p>
        </li>

        </ul>
        <h4 id='例1-6'>例1</h4>
        <p>45°をラジアンへ変換した値をコンソールに表示する．</p>
        <CodeBlock>{'print( radians( 45 ) );'}</CodeBlock>
        <img src={`${imageFolder}radians_45.png`} alt="radians_45" />
        <h4 id='例2-6'>例2</h4>
        <p>180°をラジアンへ変換した値をコンソールに表示する．</p>
        <CodeBlock>{'print( radians( 180 ) );'}</CodeBlock>
        <img src={`${imageFolder}radians_180.png`} alt="radians_180" />
        <p>詳しい使用例は次の「円弧の描画」で紹介する．</p>
        <p><a href="https://processing.org/reference/radians_.html" target='_blank'>https://processing.org/reference/radians_.html</a></p>
        <h3 id='円弧を描画する'>円弧を描画する</h3>
        <p><strong><code>arc()</code></strong></p>
        <h4 id='構文-18'>構文</h4>
        <p><code>arc( 中心のX座標, 中心のY座標, 横の直径, 縦の直径, 弧の始まりのラジアン, 弧の終わりのラジアン, モード )</code></p>
        <p>角度をラジアンへ変換する命令を含めると以下のようになる．</p>
        <p><code>arc( 中心のX座標, 中心のY座標, 横の直径, 縦の直径, radians( 弧の始まりの角度 ), radians( 弧の終わりの角度 ), モード );</code></p>
        <img src={`${imageFolder}arc_zukai.jpg`} alt="arc_zukai" />
        <h4 id='説明-17'>説明</h4>
        <h4 id='引数-15'>引数</h4>
        <ul>
        <li><p>弧の元になる楕円の中心のX,Y座標</p>
        </li>
        <li><p>弧の元になる楕円の横と縦の直径</p>
        </li>
        <li><p>弧の始まりのラジアン</p>
        </li>
        <li><p>弧の終わりのラジアン</p>
        </li>
        <li><p>弧のモード</p>
        <ul>
        <li><p><code>OPEN</code>：オープンな弧</p></li>
        <li><p><code>CHORD</code>：閉じた弧</p></li>
        <li><p><code>PIE</code>：パイ状</p></li>

        </ul>
        </li>

        </ul>
        <h4 id='例1-7'>例1</h4>
        <p>45°から始まり，270°で終わるオープンな弧</p>
        <img src={`${imageFolder}arc_open.png`} alt="arc_open" />
        <CodeBlock>{'arc( 50, 50, 80, 80, radians( 45 ), radians( 270 ), OPEN );'}</CodeBlock>
        <h4 id='例2-7'>例2</h4>
        <p>45°から始まり，270°で終わる閉じた弧</p>
        <img src={`${imageFolder}arc_chord.png`} alt="arc_chord" />
        <CodeBlock>{'arc( 50, 50, 80, 80, radians( 45 ), radians( 270 ), CHORD );'}</CodeBlock>
        <h4 id='例3-4'>例3</h4>
        <p>45°から始まり，270°で終わるパイ状の弧</p>
        <img src={`${imageFolder}arc_pie.png`} alt="arc_pie" />
        <CodeBlock>{'arc( 50, 50, 80, 80, radians( 45 ), radians( 270 ), PIE );'}</CodeBlock>
        <h4 id='例4-1'>例4</h4>
        <p>円弧を用いた沈む夕日のような表現</p>
        <img src={`${imageFolder}arc_moon.png`} alt="arc_moon" />
        <CodeBlock>{'size( 400, 200 );\narc( 50,  100, 80, 80, radians( 120 ), radians( 420 ), CHORD );\narc( 150, 100, 80, 80, radians( 135 ), radians( 405 ), CHORD );\narc( 250, 100, 80, 80, radians( 175 ), radians( 365 ), CHORD );\narc( 350, 100, 80, 80, radians( 190 ), radians( 350 ), CHORD );'}</CodeBlock>
        <p><a href="https://processing.org/reference/arc_.html" target='_blank'>https://processing.org/reference/arc_.html</a></p>
        <h2 id='h8'>演習４</h2>
        <p>扇柄を作る．</p>
        <img src={`${imageFolder}ougi.png`} alt="ougi" />
        <p>条件</p>
        <ul>
        <li><p>スクリーンサイズ：200×200</p></li>

        </ul>
        <ol>
        <li><p>一番大きな弧を<code>PIE</code>指定で描画する．</p>
        <img src={`${imageFolder}ougi_Larc.png`} alt="ougi_Larc" />
        </li>
        <li><p>大きさを変えた弧を３つ描画する．</p>
        <img src={`${imageFolder}ougi_arcs.png`} alt="ougi_arcs" />
        </li>
        <li><p>線の太さや色を好みで設定する．</p>
        <img src={`${imageFolder}ougi.png`} alt="ougi" />
        </li>
        <li><p>余裕があれば下部分を削る表現に挑戦する．</p>
        <img src={`${imageFolder}ougi_ad.png`} alt="ougi_ad" />
        </li>
        <li><p>以下，完成プログラム</p>
        <CodeBlock>{'size( 200, 200 );\n\nfill( 0, 204, 255 );\nstroke( 255 );\nstrokeWeight( 4 );\n\narc( 100, 150, 200, 200, radians( 225 ), radians( 315 ), PIE );\n\narc( 100, 150, 160, 160, radians( 225 ), radians( 315 ), PIE );\n\narc( 100, 150, 120, 120, radians( 225 ), radians( 315 ), PIE );\n\narc( 100, 150, 80, 80, radians( 225 ), radians( 315 ), PIE );\n\n// shita wo kezuru\nnoStroke();\nfill( 204 );\narc( 100, 156, 84, 88, radians( 225 ), radians( 315 ), PIE );'}</CodeBlock>
        </li>

        </ol>
        <h2 id='h9'>座標変換による表現</h2>
        <img src={`${imageFolder}5petals_flower.png`} alt="5petals_flower" />
        <ul>
        <li><p>図形描画を用いてレイアウトを行うにあたり，図形を平行移動，回転，または拡大縮小させると，より表現の幅が広がる．
        このような操作をトランスフォーム( Transform )と呼ぶ．</p></li>
        <li><p>Processing においてトランスフォームを実現するには<strong>座標変換</strong>を用いる．</p></li>
        <li><p>座標変換を理解する前に，Processingにおける<strong>座標系</strong>について学習する．</p></li>

        </ul>
        <h3 id='座標系-coordinate-system-'>座標系( Coordinate system )</h3>
        <ul>
        <li><p>空間内の特定の位置を点で示す決まりごとのこと．</p>
        </li>
        <li><p>具体的には以下の要素を内包する</p>
        <ul>
        <li><p>原点</p></li>
        <li><p>単位</p></li>
        <li><p>各方向軸の向き( X軸, Y軸, Z軸 )</p></li>

        </ul>
        </li>

        </ul>
        <h4 id='processing-の座標系'>Processing の座標系</h4>
        <p>Processing のようなスクリーン（二次平面）上で図形の操作を行うアプリケーションにおいては，<strong>2次元の座標系</strong>が導入されている．</p>
        <ul>
        <li><p>描画を行うための基準．</p></li>
        <li><p>X軸とY軸が互いに直交し，交わるところに原点がある．</p></li>
        <li><p><u>プログラム開始時点における</u>座標系を，シンプルな矩形を描画するプログラムと共に以下に示す．</p></li>

        </ul>
        <CodeBlock>{'rect( 20, 20, 40, 40 );'}</CodeBlock>
        <img src={`${imageFolder}transform_origin.png`} alt="transform_origin" />
        <p>&nbsp;</p>
        <h3 id='座標変換とは'>座標変換とは</h3>
        <p>この座標系（X軸,Y軸,原点）を並行移動，回転，拡大縮小することを<strong>座標変換</strong>という．
        <u>図形が変換されるのではない</u>ことに注意</p>
        <h4 id='平行移動-1'>平行移動</h4>
        <img src={`${imageFolder}window_transform_translation_.png`} alt="window_transform_translation_" />
        <hr />
        <h4 id='回転-1'>回転</h4>
        <img src={`${imageFolder}window_transform_rotate_.png`} alt="window_transform_rotate_" />
        <hr />
        <h4 id='拡大縮小-1'>拡大縮小</h4>
        <img src={`${imageFolder}window_transform_scale_.png`} alt="window_transform_scale_" />
        <p><u>座標変換を実行した後に</u>図形描画を行うことにより，ウィンドウ上では図形が平行移動，回転または拡大縮小したように描画される．</p>
        <h3 id='平行移動-2'>平行移動</h3>
        <p><strong><code>translate()</code></strong></p>
        <h4 id='構文-19'>構文</h4>
        <p><code>translate( X移動量, Y移動量 )</code></p>
        <h4 id='説明-18'>説明</h4>
        <ul>
        <li><p>座標系をX,Y軸方向へ平行移動させる．</p></li>
        <li><p>同様の操作を<code>translate()</code>を使わずに行えるが，こちらを使った方が簡単な場合が多い．</p></li>

        </ul>
        <h5 id='平行移動を行わず描画を行った場合'>平行移動を<u>行わず</u>描画を行った場合</h5>
        <img src={`${imageFolder}transform_origin02_draw.png`} alt="transform_origin02_draw" />
        <h5 id='平行移動を行った後に描画を行った場合'>平行移動を行った後に描画を行った場合</h5>
        <img src={`${imageFolder}transform_translate.png`} alt="transform_translate" />
        <p>&nbsp;</p>
        <img src={`${imageFolder}transform_translate_draw.png`} alt="transform_translate_draw" />
        <h4 id='引数-16'>引数</h4>
        <p>移動させるX,Y値を記述する．</p>
        <h4 id='例-12'>例</h4>
        <p>シンプルな家の模様を複数描画する．
        同じ構造の図形を繰り返し描画する際，よりシンプルにできる．</p>
        <img src={`${imageFolder}house_02.png`} alt="house_02" />
        <CodeBlock>{'size(400, 100);\nbackground(255);\n\n// 一つ目\ntriangle(15, 0, 0, 15, 30, 15);\nrect(0, 15, 30, 30);\nrect(12, 30, 10, 15);\n\ntranslate(40, 0); // X軸方向に40移動 \n\n// 二つ目\ntriangle(15, 0, 0, 15, 30, 15);\nrect(0, 15, 30, 30);\nrect(12, 30, 10, 15);\n\ntranslate(40, 0); // X軸方向にさらに40移動\n\n// 三つ目\ntriangle(15, 0, 0, 15, 30, 15);\nrect(0, 15, 30, 30);\nrect(12, 30, 10, 15);'}</CodeBlock>
        <p><a href="https://processing.org/reference/translate_.html" target='_blank'>https://processing.org/reference/translate_.html</a></p>
        <h3 id='回転-2'>回転</h3>
        <p><strong><code>rotate()</code></strong></p>
        <h4 id='構文-20'>構文</h4>
        <p><code>rotate( ラジアン )</code></p>
        <p>角度（°）を使いたい場合はこう書くとよい．</p>
        <p><code>rotate( radians( 角度 ) )</code></p>
        <h4 id='説明-19'>説明	</h4>
        <p>座標を<u>原点を中心に</u>時計回りに回転させる．</p>
        <h5 id='座標系の回転のイメージ'>座標系の回転のイメージ</h5>
        <img src={`${imageFolder}transform_rotation.png`} alt="transform_rotation" />
        <p>&nbsp;</p>
        <h5 id='描画'>描画</h5>
        <img src={`${imageFolder}transform_rotation_draw.png`} alt="transform_rotation_draw" />
        <h4 id='引数-17'>引数</h4>
        <p>回転するラジアンを記述する．
        マイナス「-」の値を指定すると反時計回りに回転する．</p>
        <h4 id='例1-8'>例1</h4>
        <p>原点を中心に15°回転させ，矩形描画を行う．</p>
        <img src={`${imageFolder}transform_rotate_simple_rect.png`} alt="transform_rotate_simple_rect" />
        <CodeBlock>{'size( 400, 400 );\nbackground( 255 );\n\nfill( 0, 0, 255 );\nnoStroke();\n\n// 以下，回転と矩形描画の繰り返し．\nrotate( radians(5) );\nrect( 300, 0, 40, 20 );\n\nrotate( radians(15) );\nrect( 300, 0, 40, 20 );\n\nrotate( radians(15) );\nrect( 300, 0, 40, 20 );\n\nrotate( radians(15) );\nrect( 300, 0, 40, 20 );\n\nrotate( radians(15) );\nrect( 300, 0, 40, 20 );\n\nrotate( radians(15) );\nrect( 300, 0, 40, 20 );\n\nrotate( radians(15) );\nrect( 300, 0, 40, 20 );'}</CodeBlock>
        <h4 id='例2-8'>例2</h4>
        <p><u>スクリーン中央を中心に回転</u>させたい場合</p>
        <img src={`${imageFolder}transform_rotate_center_simple_rect.png`} alt="transform_rotate_center_simple_rect" />
        <ol>
        <li><p>スクリーン中心へ平行移動を行う．</p></li>

        </ol>
        <img src={`${imageFolder}transform_translate_center.png`} alt="transform_translate_center" />
        <ol>
        <li><p>座標を回転させる．</p></li>

        </ol>
        <img src={`${imageFolder}transform_translate_center_rotate.png`} alt="transform_translate_center_rotate" />
        <ol>
        <li><p>図形の描画を行う．
        手順2に戻る．</p></li>

        </ol>
        <img src={`${imageFolder}transform_translate_center_rotate_draw.png`} alt="transform_translate_center_rotate_draw" />
        <CodeBlock>{'size( 400, 400 );\nbackground( 255 );\n\nfill( 0, 0, 255 );\nnoStroke();\n\n// 原点をスクリーン中心へ移動\ntranslate( width/2, height/2 );\n\n// 以下，回転と矩形描画の繰り返し．\n\nrect( 100, 0, 40, 20 );\n\nrotate( radians(45) );\nrect( 100, 0, 40, 20 );\n\nrotate( radians(45) );\nrect( 100, 0, 40, 20 );\n\nrotate( radians(45) );\nrect( 100, 0, 40, 20 );\n\nrotate( radians(45) );\nrect( 100, 0, 40, 20 );\n\nrotate( radians(45) );\nrect( 100, 0, 40, 20 );\n\nrotate( radians(45) );\nrect( 100, 0, 40, 20 );\n\nrotate( radians(45) );\nrect( 100, 0, 40, 20 );'}</CodeBlock>
        <h4 id='例3-5'>例3</h4>
        <p>時計の針のような描画</p>
        <img src={`${imageFolder}transform_01.png`} alt="no-referrer" />
        <CodeBlock>{'size( 300, 300 );\n\ntranslate( width/2, height/2 );  // 原点をスクリーン中心に移動. \n\nstrokeWeight(1);\nline( 0, 0, 100, 0 ); \nrotate( radians(30) );    // 30度回転.\n\nstrokeWeight(4);\nline( 0, 0, 100, 0 ); \nrotate( radians(50) );    // 50度回転.\n\nstrokeWeight(6);\nline( 0, 0, 100, 0 ); \nrotate( radians(70) );    // 80度回転.\n\nstrokeWeight(8);\nline( 0, 0, 100, 0 ); \nrotate( radians(80) );    // 90度回転.\n\nstrokeWeight(10);\nline( 0, 0, 100, 0 );'}</CodeBlock>
        <p><a href="https://processing.org/reference/rotate_.html" target='_blank'>https://processing.org/reference/rotate_.html</a></p>
        <h3 id='拡大縮小-2'>拡大縮小</h3>
        <p><strong><code>scale()</code></strong></p>
        <h4 id='構文-21'>構文</h4>
        <p><code>scale( 拡大率 )</code>
        <code>scale( X拡大率, Y拡大率 )</code></p>
        <h4 id='説明-20'>説明</h4>
        <ul>
        <li><p>原点を基点に空間の大きさをスケーリングする．</p>
        </li>
        <li><p>拡大すると原点から外側へ向けて，見た目の座標や大きさが引き延ばされる．</p>
        <ul>
        <li><p>縮小すると原点へ向けて，見た目の座標や大きさが縮められる．</p></li>

        </ul>
        </li>

        </ul>
        <h5 id='拡大縮小を行わず描画を行った場合'>拡大縮小を<u>行わず</u>描画を行った場合</h5>
        <img src={`${imageFolder}transform_origin02_draw.png`} alt="transform_origin02_draw" />
        <h5 id='拡大縮小を行った後に描画を行った場合'>拡大縮小を行った後に描画を行った場合</h5>
        <p>図形の大きさだけでなく，２つの図形の間隔も大きくなる．</p>
        <ul>
        <li><p>円が原点から遠ざかる方向へ移動している．</p></li>

        </ul>
        <img src={`${imageFolder}transform_scale_gainen02.png`} alt="transform_scale_gainen02" />
        <h4 id='引数-18'>引数</h4>
        <ul>
        <li><p>拡大率は<code>1.0</code>が等倍．</p></li>
        <li><p>例えば<code>2.0</code>を渡すと2倍に拡大される．</p></li>
        <li><p>半分のスケールにしたい場合は<code>0.5</code>を記述する．</p></li>

        </ul>
        <h4 id='例-13'>例</h4>
        <p>見た目の大きさはかわっているが，<code>rect()</code>に<u>渡している値は同じ</u>であることに注目</p>
        <img src={`${imageFolder}transform_scale_simple_rects.png`} alt="transform_scale_simple_rects" />
        <CodeBlock>{'size(200,200);\nbackground(255);\n\nstroke(128);\nrect(20, 20, 40, 40);		// 小さい矩形\n\nscale(2.0);							// 2倍に拡大\n\nstroke(0);\nrect(20, 20, 40, 40);		// 大きな矩形'}</CodeBlock>
        <p><a href="https://processing.org/reference/scale_.html" target='_blank'>https://processing.org/reference/scale_.html</a></p>
        <h2 id='h10'>演習５</h2>
        <p>シンプルな花の模様を作成する．</p>
        <img src={`${imageFolder}5petals_flower.png`} alt="5petals_flower" />
        <p>条件</p>
        <ul>
        <li><p>スクリーンサイズ： 400×400</p></li>

        </ul>
        <ol>
        <li><p>座標系をスクリーン中心へ平行移動し，花弁用の楕円を１つ描画する．
        楕円は少しX軸方向（右方向）へずらしておく．（これには平行移動は使わないこと）</p>
        <img src={`${imageFolder}5petals_flower_firstellipse.png`} alt="5petals_flower_firstellipse" />
        </li>
        <li><p>座標系を72°回転させて同じ楕円を１つ描画する．</p>
        <img src={`${imageFolder}5petals_flower_secondellipse.png`} alt="5petals_flower_secondellipse" />
        </li>
        <li><p>ステップ２を繰り返し，計５個の楕円を描画する．</p>
        <img src={`${imageFolder}5petals_flower_5ellipses.png`} alt="5petals_flower_5ellipses" />
        </li>
        <li><p>中心に楕円を一つ描画する．</p>
        <img src={`${imageFolder}5petals_flower_base.png`} alt="5petals_flower_base" />
        </li>
        <li><p>回転（<code>-18°</code>）命令文を１つ目の楕円描画直前に挿入する．</p>
        <img src={`${imageFolder}5petals_flower_rot_fix.png`} alt="5petals_flower_rot_fix" />
        </li>
        <li><p>線の太さや各種色を設定する．</p>
        <img src={`${imageFolder}5petals_flower.png`} alt="5petals_flower" />
        </li>
        <li><p>以下，完成プログラム</p>
        <CodeBlock>{'size( 400, 400 );\n\nstroke( 204 );\nstrokeWeight( 4 );\n\ntranslate( 200, 200 );\nrotate( radians( -18 ) );\nellipse( 95, 0, 160, 50 );\nrotate( radians( 72 ) );\nellipse( 95, 0, 160, 50 );\nrotate( radians( 72 ) );\nellipse( 95, 0, 160, 50 );\nrotate( radians( 72 ) );\nellipse( 95, 0, 160, 50 );\nrotate( radians( 72 ) );\nellipse( 95, 0, 160, 50 );\nellipse( 0, 0, 40, 40 );\n'}</CodeBlock>
        </li>

        </ol>
        <h2 id='h11'>座標変換の組み合わせ</h2>
        <p>復習を兼ねて座標変換の組み合わせを解説する．</p>
        <h3 id='平行移動と回転の組み合わせ'>平行移動と回転の組み合わせ</h3>
        <ul>
        <li><p>平行移動と回転を組み合わせると難解になる．</p></li>
        <li><p>スケッチブックや方眼紙に座標系の変換を<strong>メモ</strong>するなど，工夫してみましょう．</p></li>

        </ul>
        <h4 id='平行移動回転'>平行移動+回転</h4>
        <ol>
          <li><p>平行移動</p></li>
          <img src={`${imageFolder}transform_translate.png`} alt="transform_translate" />
          <li><p>回転</p></li>
          <img src={`${imageFolder}transform_translate_rotation.png`} alt="transform_translate_rotation" />
          <li><p>図形描画</p></li>
          <img src={`${imageFolder}transform_translate_rotation_draw.png`} alt="transform_translate_rotation_draw" />
        </ol>
        <h4 id='回転平行移動'>回転+平行移動</h4>
        <p>あまり推奨しない</p>
        <ul>
        <li><p>初めに回転を行うとその後の平行移動は感覚的に理解し難い．</p></li>
        <li><p>できるだけ先の「平行移動→回転」を用いることをすすめる．</p></li>

        </ul>
        <h5 id='1-回転'>1. 回転</h5>
        <img src={`${imageFolder}transform_rotation.png`} alt="transform_rotation" />
        <h5 id='回転後の平行移動の考え方'>回転後の平行移動の考え方</h5>
        <ul>
        <li><p>回転後の平行移動は特に注意</p>
        <ul>
        <li><p><strong>回転後のX,Y軸を元に</strong>移動する．</p></li>

        </ul>
        </li>

        </ul>
        <img src={`${imageFolder}transform_rotation_translate01.png`} alt="transform_rotation_translate01" />
        <p>&nbsp;</p>
        <hr />
        <h5 id='2-平行移動'>2. 平行移動</h5>
        <img src={`${imageFolder}transform_rotation_translate02.png`} alt="transform_rotation_translate02" />
        <h2 id='h12'>多角形による表現</h2>
        <img src={`${imageFolder}asanoha_elem.png`} alt="asanoha_elem" />
        <h3>多角形を描画する</h3>
        <p><strong><code>beginShape()</code></strong></p>
        <h4 id='構文-22'>構文</h4>
        <p><code>beginShape( 図形の種類 )</code></p>
        <h4 id='説明-21'>説明</h4>
        <p>多角形描画は複数の文を記述する必要がある．</p>
        <ol>
        <li><p>はじめに<code>beginShape();</code>と記述．</p>
        <ul>
        <li><p>引数として後述の図形の種類を記述する．</p></li>

        </ul>
        </li>
        <li><p>必要な頂点の数だけ <code>vertex( X座標, Y座標 );</code> を記述する．</p>
        </li>
        <li><p>最後に <code>endShape();</code> を記述し，多角形描画を終了する．
        ※なお、<code>endShape(CLOSE);</code>と記述することで閉じた形状を描画できる．</p>
        </li>

        </ol>
        <CodeBlock>{'beginShape( 種類 );					// 形状の開始宣言\n\n// 頂点座標（頂点の数だけ複数記述する必要がある）\nvertex( 頂点1のX値, 頂点1のY値 );\nvertex( 頂点2のX値, 頂点2のY値 );\nvertex( 頂点3のX値, 頂点3のY値 );\nvertex( 頂点4のX値, 頂点4のY値 );\nvertex( 頂点5のX値, 頂点5のY値 );\nvertex( 頂点6のX値, 頂点6のY値 );\nvertex( 頂点7のX値, 頂点7のY値 );\nvertex( 頂点8のX値, 頂点8のY値 );\n\nendShape(CLOSE);					// 形状の終了宣言'}</CodeBlock>
        <h4 id='引数-19'>引数</h4>
        <p>図形の種類</p>
        <ul>
        <li><p>通常（引数無し）</p></li>
        <li><p><code>POINTS</code></p></li>
        <li><p><code>LINES</code></p></li>
        <li><p><code>TRIANGLES</code></p></li>
        <li><p><code>TRIANGLE_STRIP</code></p></li>
        <li><p><code>TRIANGLE_FAN</code></p></li>
        <li><p><code>QUADS</code></p></li>
        <li><p><code>QUAD_STRIP</code></p></li>

        </ul>
        <h4 id='例1-9'>例1</h4>
        <p>矢印状の図形
        複雑な形状は事前に方眼紙等に下書きをし，下図のような計画を立てるとよい．</p>
        <img src={`${imageFolder}draw_arrow_model.png`} alt="draw_arrow_model" />
        <img src={`${imageFolder}draw_arrow.png`} alt="draw_arrow" />
        <CodeBlock>{'beginShape();\nvertex( 0, 20 );		// A\nvertex( 60, 20 );		// B\nvertex( 60, 0 );		// C\nvertex( 100, 30 );	// D\nvertex( 60, 60 );		// E\nvertex( 60, 40 );		// F\nvertex( 0, 40 );		// G\nendShape(CLOSE);'}</CodeBlock>
        <h4 id='例2-9'>例2</h4>
        <p>トライアングルストリップ<code>TRIANGLE_STRIP</code>の例
        ストリップ（strip ）は「細長い一片」の意味</p>
        <img src={`${imageFolder}beginshape_triangle_strip.png`} alt="beginshape_triangle_strip" />
        <CodeBlock>{'size( 200, 200 );\n\nbeginShape( TRIANGLE_STRIP );\nvertex( 20, 20);\nvertex( 60, 180 );\nvertex( 100, 20);\nvertex( 140, 180);\nvertex( 180, 20);\nendShape( CLOSE );'}</CodeBlock>
        <h4 id='例3-6'>例3</h4>
        <p>トライアングルファン<code>TRIANGLE_FAN</code>の例
        最初の点を中心に連続した三角形を描画する．</p>
        <img src={`${imageFolder}beginshape_triangle_fan.png`} alt="beginshape_triangle_fan" />
        <CodeBlock>{'size( 200, 200 );\n\ntranslate( 100, 50 );\n\nbeginShape( TRIANGLE_FAN );\nvertex(  0,  0 );\nvertex( 80, 35 );\nvertex( 55, 70 );\nvertex( 20, 90 );\nvertex( -20, 90 );\nvertex( -55, 70 );\nvertex( -80, 35 );\nendShape( CLOSE );'}</CodeBlock>
        <h4 id='例4-2'>例4</h4>
        <p>クアッドストリップ<code>QUAD_STRIP</code>の例</p>
        <img src={`${imageFolder}beginshape_quad_strip.png`} alt="beginshape_quad_strip" />
        <CodeBlock>{'size( 200, 200 );\n\nbeginShape( QUAD_STRIP );\nvertex( 20, 20);\nvertex( 50, 180 );\nvertex( 80, 20);\nvertex( 110, 180);\nvertex( 140, 20);\nvertex( 170, 180);\nendShape( CLOSE );'}</CodeBlock>
        <p><a href="https://processing.org/reference/beginShape_.html" target='_blank'>https://processing.org/reference/beginShape_.html</a></p>
        <p><a href="https://processing.org/reference/vertex_.html" target='_blank'>https://processing.org/reference/vertex_.html</a></p>
        <p><a href="https://processing.org/reference/endShape_.html" target='_blank'>https://processing.org/reference/endShape_.html</a></p>
        <h3 id='線のつなぎ目の形状を変更する'>線のつなぎ目の形状を変更する．</h3>
        <p><strong><code>strokeJoin()</code></strong></p>
        <h4 id='構文-23'>構文</h4>
        <p><code>strokeJoin( 種類  )</code></p>
        <h4 id='説明-22'>説明</h4>
        <p>引数に応じて線のつなぎ目の形状を変更する．</p>
        <h4 id='引数-20'>引数</h4>
        <p>種類</p>
        <ul>
        <li><p><code>MITER</code>（デフォルト）</p></li>
        <li><p><code>BEVEL</code></p></li>
        <li><p><code>ROUND</code></p></li>

        </ul>
        <h4 id='例1-10'>例1</h4>
        <p><code>MITER</code>（デフォルト）</p>
        <img src={`${imageFolder}strokejoint_mitter.png`} alt="strokejoint_mitter" />
        <CodeBlock>{'size( 200, 200 );\nnoFill();\nstrokeWeight( 20 );\n\nstrokeJoin( MITER );\n\nbeginShape();\nvertex( 70, 40 );\nvertex( 130, 100 );\nvertex( 70, 150 );\nendShape();'}</CodeBlock>
        <h4 id='例2-10'>例2</h4>
        <p><code>BEVEL</code></p>
        <img src={`${imageFolder}strokejoint_bevel.png`} alt="strokejoint_bevel" />
        <CodeBlock>{'size( 200, 200 );\nnoFill();\nstrokeWeight( 20 );\n\nstrokeJoin( BEVEL );\n\nbeginShape();\nvertex( 70, 40 );\nvertex( 130, 100 );\nvertex( 70, 150 );\nendShape();'}</CodeBlock>
        <h4 id='例3-7'>例3</h4>
        <p><code>ROUND</code></p>
        <img src={`${imageFolder}strokejoint_round.png`} alt="strokejoint_round" />
        <CodeBlock>{'size( 200, 200 );\nnoFill();\nstrokeWeight( 20 );\n\nstrokeJoin( ROUND );\n\nbeginShape();\nvertex( 70, 40 );\nvertex( 130, 100 );\nvertex( 70, 150 );\nendShape();'}</CodeBlock>
        <h2 id='h13'>演習６</h2>
        <p>麻の葉模様（の一部）を作成する．</p>
        <img src={`${imageFolder}asanoha_elem.png`} alt="asanoha_elem" />
        <p>麻の葉模様は三分割された正方形の組み合わせでできている．</p>
        <p>条件</p>
        <ul>
        <li><p>スクリーンサイズ：300×300</p></li>

        </ul>
        <ol>
        <li><p>座標系をスクリーン中心へ平行移動し，トライアングルストリップを１つ描画する．
        ２つ目の図のように５つの頂点を設定する．</p>
        <img src={`${imageFolder}asanoha_elem_trianglestrip.png`} alt="asanoha_elem_trianglestrip" />
        <img src={`${imageFolder}asanoha_elem_trianglestrip_graph.jpg`} alt="asanoha_elem_trianglestrip_graph" />
        </li>
        <li><p>座標系を60°ずつ回転させてトライアングルストリップを６つ描画する．</p>
        <img src={`${imageFolder}asanoha_elem_6trianglestrips.png`} alt="asanoha_elem_6trianglestrips" />
        </li>
        <li><p>線の太さや各種色を設定する．</p>
        <img src={`${imageFolder}asanoha_elem_colored.png`} alt="asanoha_elem_colored" />
        </li>
        <li><p>線のつなぎ目を<code>BEVEL</code>にする．</p>
        <img src={`${imageFolder}asanoha_elem.png`} alt="asanoha_elem" />
        </li>
        <li><p>以下，完成プログラム</p>
        </li>

        </ol>
        <CodeBlock>{'size( 300, 300 );\n\ntranslate( 150, 150 );\n\nfill( 235,184,184 );\nstroke( 255 );\nstrokeWeight( 2 );\nstrokeJoin( BEVEL );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();'}</CodeBlock>
        <h2 id='h14'>座標系の保存と復帰</h2>
        <ul>
        <li><p>座標変換の命令を使用すると，変化した座標系はそのまま残り続ける．</p>
        <ul>
        <li><p>目的によってはこのことが不都合となる場合がある．</p></li>

        </ul>
        </li>
        <li><p>Processingにはある時点の座標系を保存したり，保存した時点の座標系に戻したりする機能が存在する．</p>
        </li>
        <li><p>座標系の詳細は数学における行列が深くかかわっているが，本授業では掘り下げない．</p>
        </li>
        <li><p>以下の二つの命令を任意の場所に記述し，使うことができれば問題ない．</p>
        </li>

        </ul>
        <h4 id='構文-24'>構文</h4>
        <h5 id='座標系を保存する'>座標系を保存する．</h5>
        <p><strong><code>pushMatrix()</code></strong></p>
        <h5 id='座標系を保存した時点に戻す'>座標系を保存した時点に戻す．</h5>
        <p><strong><code>popMatrix()</code></strong></p>
        <h4 id='説明-23'>説明</h4>
        <p>プログラムの処理の順を追って説明する．</p>
        <ol>
        <li><p><strong><code>pushMatrix()</code></strong>で座標系を保存しておく．</p>
        </li>
        <li><p>座標変換と各種描画の命令</p>
        <ul>
        <li><p><strong><code>translate()</code>, <code>rotate()</code>, <code>scale()</code></strong></p></li>
        <li><p>図形描画</p></li>

        </ul>
        </li>
        <li><p><strong><code>popMatrix()</code></strong>で初期の座標系に戻す．</p>
        </li>

        </ol>
        <h4 id='例-14'>例</h4>
        <p>座標変換を行った後に白の矩形を描画し，
        座標系を初期に戻した後に黒の矩形を描画する．</p>
        <img src={`${imageFolder}transform_pushpopMatrix_simple.png`} alt="transform_pushpopMatrix_simple" />
        <CodeBlock>{'size(500, 500);\n\npushMatrix();          // 座標系の記憶 \n\ntranslate(250, 250);\nrotate( radians(30) );\nscale(2.0);\n\nrect(0, 0, 50, 50);\n\npopMatrix();           // 座標系を元に戻す\n\nfill(0);\nrect(0, 0, 50, 50);'}</CodeBlock>
        <h3 id='座標系保存のメカニズム'>座標系保存のメカニズム</h3>
        <p><code>pushMatrix()</code>と<code>popMatrix()</code>は，座標系データをStack形式で出し入れしている．</p>
        <h4 id='stackスタック）とは'>Stack（スタック）とは</h4>
        <ul>
        <li><p><strong>先に入れたデータを先に取り出す</strong>．FIFO( First In First Out )</p></li>
        <li><p>追加(<strong>push</strong>)したデータをあたらしい順に取り出す(<strong>pop</strong>)仕組み．</p></li>

        </ul>
        <p>したがって，pushMatrix()とpopMatrix()を入れ子構造で使用すると，新しく保存した座標系から古いものへ，
        という順に取り出される．</p>
        <img src={`${imageFolder}transform_stack.jpg`} alt="transform_stack" />
        <h4 id='具体的なプログラム記述'>具体的なプログラム記述</h4>
        <p>Queueの仕組みがわかると理解しやすい．</p>
        <img src={`${imageFolder}transform_matrix_nest.png`} alt="transform_matrix_nest" />
        <h4 id='例-15'>例</h4>
        <p>角度と位置を変化させながら楕円を４つ描画する．</p>
        <img src={`${imageFolder}pushpopMatrix_example01.png`} alt="pushpopMatrix_example01" />
        <CodeBlock>{'size( 400, 400 );\n\ncolorMode( RGB, 100, 100, 100 );\n\nbackground( 60, 60, 80 );\n\n// 色\nstroke( 80, 62, 70 );\nfill( 90, 90, 49 );\n\n// 線の太さ\nstrokeWeight( 6 );\n\ntranslate( 100, 60 );\n\npushMatrix();  // 座標系保存\nrotate( radians(20) );\nellipse( 0, 0, 100, 20 );\npopMatrix();   // 座標系を戻す\n\ntranslate( 40, 80 );\n\npushMatrix();  // 座標系保存\nrotate( radians(60) );\nellipse( 0, 0, 100, 20 );\npopMatrix();   // 座標系を戻す\n\ntranslate( 40, 110 );\n\npushMatrix();  // 座標系保存\nrotate( radians(40) );\nellipse( 0, 0, 100, 20 );\npopMatrix();   // 座標系を戻す\n\ntranslate( 80, 80 );\n\npushMatrix();  // 座標系保存\nrotate( radians(4) );\nellipse( 0, 0, 100, 20 );\npopMatrix();   // 座標系を戻す'}</CodeBlock>
        <p><a href="https://processing.org/reference/pushMatrix_.html" target='_blank'>https://processing.org/reference/pushMatrix_.html</a>
        <p><a href="https://processing.org/reference/popMatrix_.html" target='_blank'>https://processing.org/reference/popMatrix_.html</a></p>
        <h2 id='h15'>演習７</h2>
        <p>演習６の麻の葉模様を横に一つ追加し，繋げる．</p>
        <img src={`${imageFolder}asanoha_elem2.png`} alt="asanoha_elem2" />
        <ol>
        <li><p>演習６の完成プログラムを開く．</p>
        </li>
        <li><p>プログラムの最後に平行移動命令文<code>translate( 200, 0 );</code>を記述する．</p>
        </li>
        <li><p>ステップ2の後ろに，模様描画部分をコピー＆ペーストする．
        しかし，うまく描画されないことを確認する．</p>
        <img src={`${imageFolder}asanoha_elem2_sippai.png`} alt="asanoha_elem2_sippai" />
        </li>
        <li><p><code>pushMatrix()</code>と<code>popMatrix()</code>を適切な場所に記述する．</p>
        <img src={`${imageFolder}asanoha_elem2_pushpop.png`} alt="asanoha_elem2_pushpop" />
        </li>
        <li><p>ステップ2で記述した部分を<code>translate( 200, 0 );</code>の<code>200</code>の値を調整し，模様同士を隣接させる．</p>
        <img src={`${imageFolder}asanoha_elem2.png`} alt="asanoha_elem2" />
        </li>
        <li><p>以下，完成プログラム</p>
        </li>

        </ol>
        <CodeBlock>{'size( 450, 300 );\n\ntranslate( 150, 150 );\n\npushMatrix();\n\nfill( 235,184,184 );\nstroke( 255 );\nstrokeWeight( 2 );\nstrokeJoin( BEVEL );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\npopMatrix();\n\ntranslate( 140, 0 );\n\nfill( 235,184,184 );\nstroke( 255 );\nstrokeWeight( 2 );\nstrokeJoin( BEVEL );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();\n\nrotate( radians( 60 ) );\n\nbeginShape( TRIANGLE_STRIP );\n// ippen 80\nvertex( 0, 0 );\nvertex( 69, 40 );\nvertex( 23, 40 );\nvertex( 0, 80 );\nvertex( 0, 0 );\nendShape();'}</CodeBlock>
        <h2 id='h16'>曲線による表現</h2>
        <h3 id='曲線を描画する'>曲線を描画する</h3>
        <p><code>curve()</code></p>
        <h4 id='構文-25'>構文</h4>
        <p><code>curve( 制御点1のX値, 制御点1のY値, 描画点1のX値, 描画点1のY値, 描画点2のX値, 描画点2のY値, 制御点2のX値, 制御点2のY値 )</code></p>
        <img src={`${imageFolder}curve_spline_clipped.png`} alt="curve_spline_clipped" /></p>
        <h4 id='説明-24'>説明</h4>
        <p>4点を結ぶ曲線の中間2点間が描画される．（スプライン曲線）</p>
        <ul>
        <li><p>spline たわみ</p>
        <ul>
        <li><p>4点で囲まれた領域を丸く膨らませた曲線が生成される</p></li>

        </ul>
        </li>
        <li><p>特定のある<u>囲まれた領域を丸く膨らませた図形</u>を生成したい際に有効．</p>
        </li>

        </ul>
        <h4 id='引数-21'>引数</h4>
        <ul>
        <li><p>描画点2点間に曲線が描画される．</p></li>
        <li><p>制御点の座標によって形状が変化する．</p></li>

        </ul>
        <h4 id='例-16'>例</h4>
        <p>4点とを曲線で囲まれた領域（上の図のプログラム）</p>
        <CodeBlock>{'size( 600, 600 );\nbackground(255);\n\nnoFill();\n\n// 濃い黒の曲線\nstroke(0);\nstrokeWeight(4);\ncurve( 100, 100, 450, 200, 500, 350, 60, 500 );\n\n// 薄い曲線\nstroke(200);\nstrokeWeight(4);\ncurve( 450, 200, 500, 350, 60, 500, 100, 100 );\ncurve( 500, 350, 60, 500, 100, 100, 450, 200 );\ncurve( 60, 500, 100, 100, 450, 200, 500, 350 );\n\n// 4つの点を赤く描画．\nstroke(255,0,0);\nstrokeWeight(12);\npoint(100, 100);\npoint(450, 200);\npoint(500, 350);\npoint(60, 500);'}</CodeBlock>
        <p><a href="https://processing.org/reference/curve_.html" target='_blank'>https://processing.org/reference/curve_.html</a></p>
        <h3 id='ベジェ曲線を描画する'>ベジェ曲線を描画する</h3>
        <p><code>bezier()</code></p>
        <h4 id='構文-26'>構文</h4>
        <p><code>curve()</code>の場合と描画点，制御点の記述順が異なることに注意．</p>
        <p><code>bezier( 描画点1のX値, 描画点1のY値, 制御点1のX座標, 制御点1のY座標, 制御点2のX値, 制御点2のY値, 描画点2のX座標, 描画点2のY座標)</code></p>
        <img src={`${imageFolder}bezier_clipped.png`} alt="bezier_clipped"/>
        <h4 id='説明-25'>説明</h4>
        <p>描画点（アンカーポイント）2点と制御点（スムーズポイント）2点から描画される曲線．</p>
        <ul>
        <li><p>実際に描画されるのは描画点2点間のみ</p>
        </li>
        <li><p>制御点1から描画点1へ引いた直線が曲線に接する．
        制御点2から描画点2へ引いた直線が曲線に接する．</p>
        <ul>
        <li><p>以上の条件を満たす曲線が生成される．</p></li>
        <li><p>これらの直線はIllustratorでいうハンドル</p></li>

        </ul>
        </li>
        <li><p>Adobe Illustrator のパスとほぼ同様</p>
        <ul>
        <li><p>アンカーポイント</p></li>
        <li><p>スムーズポイント，コーナーポイント</p></li>
        <li><p>ハンドル</p></li>

        </ul>
        </li>

        </ul>
        <h4 id='引数-22'>引数</h4>
        <ul>
        <li><p>描画点2点間に曲線が描画される．</p></li>
        <li><p>制御点の座標によって形状が変化する．</p></li>

        </ul>
        <h4 id='例-17'>例</h4>
        <p>ハンドルと曲線（上の図のプログラム）</p>
        <CodeBlock>{'size( 600, 600 );\nbackground(255);\n\nnoFill();\n\n// 薄い直線（ハンドル）\nstroke(200);\nstrokeWeight(4);\nline( 100, 100, 450, 200 );\nline( 500, 350, 60, 500 );\n\n// 濃い黒の曲線\nstroke(0);\nstrokeWeight(4);\nbezier( 450, 200, 100, 100, 60, 500, 500, 350 );\n\n// 4つの点を赤く描画．\nstroke(255,0,0);\nstrokeWeight(12);\npoint(100, 100);\npoint(450, 200);\npoint(500, 350);\npoint(60, 500);'}</CodeBlock>
        <p><a href="https://processing.org/reference/bezier_.html" target='_blank'>https://processing.org/reference/bezier_.html</a></p>
        <h2 id='h17'>演習８</h2>
        <p>ベジェ曲線を使って波を作る．</p>
        <img src={`${imageFolder}bezier_wave.png`} alt="bezier_wave" />
        <p>条件</p>
        <ul>
        <li><p>スクリーンサイズ：400×200</p></li>

        </ul>
        <ol>
        <li><p>左半分の曲線を描画する．</p>
        <p>制御点①と②は，波のピークに対して対称の位置にあるのが望ましい．
        ※制御点やハンドルは描画する必要無し．</p>
        <img src={`${imageFolder}bezier_wave_half_l.jpg`} alt="bezier_wave_half_l" />
        </li>
        <li><p>右半分の曲線を描画する．
        ※制御点やハンドルは描画する必要無し．</p>
        <img src={`${imageFolder}bezier_wave_half_r.jpg`} alt="bezier_wave_half_r" />
        </li>
        <li><p>制御点の位置を動かし，波の形を調整する．</p>
        </li>
        <li><p>線の太さや色を調整する．</p>
        </li>
        <li><p>以下，完成プログラム</p>
        </li>

        </ol>
        <CodeBlock>{'size( 400, 200 );\n\nnoFill();\n\n/*\n// handles\nstroke( 160 );\nline( 0, 100, 70, 20 );\nline( 200, 100, 130, 20 );\nline( 200, 100, 270, 180 );\nline( 400, 100, 330, 180 );\n\n// CPoints\nstrokeWeight( 8 );\nstroke( 255, 0, 0 );\npoint( 70, 20 );\npoint( 130, 20 );\npoint( 270, 180 );\npoint( 330, 180 );\n*/\n\nstrokeWeight( 2 );\nstroke( 0 );\n\nbezier( 0, 100, 70, 20, 130, 20, 200, 100 );\nbezier( 200, 100, 270, 180, 330, 180, 400, 100 );'}</CodeBlock>
        <h2 id='h18'>その他の図形描画命令</h2>
        <p>演習は行わないが，今後必要になったら参照すること．</p>
        <h3 id='点を描画する'>点を描画する</h3>
        <p><code>point()</code></p>
        <h4 id='構文-27'>構文</h4>
        <p><code>point( X座標値, Y座標値 )</code></p>
        <h4 id='説明-26'>説明</h4>
        <ul>
        <li><p>１つの点（ドット）を描画する．</p>
        </li>
        <li><p><code>stroke()</code> で指定したカラーが適用される．</p>
        <ul>
        <li><p>色の指定が無ければデフォルトカラーのブラックの色が描画される．</p></li>

        </ul>
        </li>
        <li><p><code>strokeWeight()</code>で点の大きさを指定できる．</p>
        </li>

        </ul>
        <h4 id='引数-23'>引数</h4>
        <p>点を描画する座標のX値, Y値．</p>
        <h4 id='例-18'>例</h4>
        <img src={`${imageFolder}point_4points.png`} alt="point_4points" />
        <CodeBlock>{'point( 30, 30 );\n\nstrokeWeight( 8 );\npoint( 70, 30 );\n\nstroke( 0, 0, 255 );\npoint( 30, 70 );\n\nstrokeWeight( 16 );\npoint( 70, 70 );'}</CodeBlock>
        <p><a href="https://processing.org/reference/point_.html" target='_blank'>https://processing.org/reference/point_.html</a></p>
        <p>"kukei"</p>
        <h3 id='矩形を描画する'>矩形を描画する．</h3>
        <p><strong><code>rect()</code></strong></p>
        <h4 id='構文-28'>構文</h4>
        <p><code>rect( 左上X座標, 左上Y座標, 矩形の幅, 矩形の高さ )</code></p>
        <h4 id='説明-27'>説明</h4>
        <p>矩形（長方形）を描画する．</p>
        <h4 id='引数-24'>引数</h4>
        <ul>
        <li><p>矩形の左上の頂点のX,Y座標値</p></li>
        <li><p>矩形の幅，高さ</p></li>

        </ul>
        <img src={`${imageFolder}rect_corner.png`} alt="rect_corner" />
        <h4 id='例-19'>例</h4>
        <img src={`${imageFolder}rect_default.png`} alt="rect_default" />
        <CodeBlock>{'size(400, 400);\n\nrect( 20, 90, 300, 100 );'}</CodeBlock>
        <p>rectmode</p>
        <h3 id='矩形の描画方法を指定する'>矩形の描画方法を指定する．</h3>
        <p><strong><code>rectMode()</code></strong></p>
        <h4 id='構文-29'>構文</h4>
        <p><code>rectMode( モード )</code></p>
        <h4 id='説明-28'>説明</h4>
        <ul>
        <li><p>描画情報の指定方法は4種類あり，<code>rectMode()</code>で切り替えることができる．
        自分の実現したいことに適した方法を選ぶとよい．</p></li>

        </ul>
        <h4 id='引数-25'>引数</h4>
        <p>モード</p>
        <ul>
        <li><p><code>CORNER</code>（デフォルト）</p></li>
        <li><p><code>CORNERS</code></p></li>
        <li><p><code>CENTER</code></p></li>
        <li><p><code>RADIUS</code></p></li>

        </ul>
        <h4 id='例1-11'>例1</h4>
        <p><code>CORNERS</code></p>
        <h5 id='構文-30'>構文</h5>
        <p><code>rect( 左上X座標, 左上Y座標, 右下X座標, 右下Y座標 )</code></p>
        <img src={`${imageFolder}rect_corners.png`} alt="rect_corners" />
        <img src={`${imageFolder}rect_mode_common.png`} alt="rect_mode_common" />
        <CodeBlock>{'size( 200, 200 );\n\nrectMode( CORNERS );\nrect( 20, 60, 180, 140 );'}</CodeBlock>
        <h4 id='例2-11'>例2</h4>
        <p><code>CENTER</code></p>
        <h5 id='構文-31'>構文</h5>
        <p><code>rect( 中心X座標, 中心Y座標, 矩形の幅, 矩形の高さ )</code></p>
        <img src={`${imageFolder}rect_center.png`} alt="rect_center" />
        <img src={`${imageFolder}rect_mode_common.png`} alt="rect_mode_common" />
        <CodeBlock>{'size( 200, 200 );\n\nrectMode( CENTER );\nrect( 100, 100, 160, 80 );'}</CodeBlock>
        <h4 id='例3-8'>例3</h4>
        <p><code>RADIUS</code></p>
        <h5 id='構文-32'>構文</h5>
        <p><code>rect( 中心X座標, 中心Y座標, 矩形の幅の半分, 矩形の高さの半分 )</code></p>
        <img src={`${imageFolder}rect_radius.png`} alt="rect_radius" />
        <img src={`${imageFolder}rect_mode_common.png`} alt="rect_mode_common" />
        <CodeBlock>{'size( 200, 200 );\n\nrectMode( RADIUS );\nrect( 100, 100, 80, 40 );'}</CodeBlock>
        <p><a href="https://processing.org/reference/rectMode_.html" target='_blank'>https://processing.org/reference/rectMode_.html</a></p>
        <h3 id='楕円の描画方法を指定する'>楕円の描画方法を指定する．</h3>
        <p><strong><code>ellipseMode()</code></strong></p>
        <h4 id='構文-33'>構文</h4>
        <p><code>ellipseMode( モード )</code></p>
        <h4 id='説明-29'>説明</h4>
        <ul>
        <li><p>描画情報の指定方法は4種類あり，<code>ellipseMode()</code>で切り替えることができる．
        自分の実現したいことに適した方法を選ぶとよい．</p>
        <ul>
        <li><p><code>rectMode()</code> とほぼ同様</p></li>

        </ul>
        </li>

        </ul>
        <h4 id='引数-26'>引数</h4>
        <p>モード</p>
        <ul>
        <li><p><code>CENTER</code>（デフォルト）</p></li>
        <li><p><code>RADIUS</code></p></li>
        <li><p><code>CORNER</code></p></li>
        <li><p><code>CORNERS</code></p></li>

        </ul>
        <h4 id='例1-12'>例1</h4>
        <p><code>RADIUS</code></p>
        <h5 id='構文-34'>構文</h5>
        <p><code>ellipse( 中心X座標, 中心Y座標, 楕円の幅の半分, 楕円の高さの半分 )</code></p>
        <img src={`${imageFolder}ellipsemode_radius.jpg`} alt="ellipsemode_radius" />
        <img src={`${imageFolder}ellipse_mode_common.png`} alt="ellipse_mode_common" />
        <CodeBlock>{'size( 200, 200 );\n\nellipseMode( RADIUS );\nellipse( 100, 100, 80, 40 );'}</CodeBlock>
        <h4 id='例2-12'>例2</h4>
        <p><code>CORNER</code></p>
        <h5 id='構文-35'>構文</h5>
        <p><code>ellipse( 左上X座標, 左上Y座標, 楕円の幅, 楕円の高さ )</code></p>
        <img src={`${imageFolder}ellipse_corner.png`} alt="ellipse_corner" />
        <img src={`${imageFolder}ellipse_mode_common.png`} alt="ellipse_mode_common" />
        <CodeBlock>{'size( 200, 200 );\n\nellipseMode( CORNER );\nellipse( 20, 60, 160, 80 );'}</CodeBlock>
        <h4 id='例3-9'>例3</h4>
        <p><code>CORNERS</code></p>
        <h5 id='構文-36'>構文</h5>
        <p><code>rect( 左上X座標, 左上Y座標, 右下X座標, 右下Y座標 )</code></p>
        <img src={`${imageFolder}ellipse_corners.png`} alt="ellipse_corners" />
        <img src={`${imageFolder}ellipse_mode_common.png`} alt="ellipse_mode_common" />
        <CodeBlock>{'size( 200, 200 );\n\nellipseMode( CORNERS );\nellipse( 20, 60, 180, 140 );'}</CodeBlock>
        <p><a href="https://processing.org/reference/ellipseMode_.html" target='_blank'>https://processing.org/reference/ellipseMode_.html</a></p>
        <p>triangle</p>
        <h3 id='三角形を描画する'>三角形を描画する．</h3>
        <p><strong><code>triangle()</code></strong></p>
        <h4 id='構文-37'>構文</h4>
        <p><code>triangle( 頂点1X座標, 頂点1Y座標, 頂点2X座標, 頂点2Y座標, 頂点3X座標, 頂点3Y座標 )</code></p>
        <h4 id='説明-30'>説明</h4>
        <p>三角形を描画する．</p>
        <h4 id='引数-27'>引数</h4>
        <p>描画する三角形の３つの頂点の座標</p>
        <h4 id='例-20'>例</h4>
        <img src={`${imageFolder}shape_triangle.png`} alt="shape_triangle" />
        <CodeBlock>{'size( 200, 200 );\n\ntriangle( 100, 40, 40, 160, 160, 160 );'}</CodeBlock>
        <p><a href="https://processing.org/reference/triangle_.html" target='_blank'>https://processing.org/reference/triangle_.html</a></p>
        <h3 id='四角形を描画する'>四角形を描画する．</h3>
        <p><strong><code>quad()</code></strong></p>
        <h4 id='構文-38'>構文</h4>
        <p><code>quad( 頂点1X座標, 頂点1Y座標, 頂点2X座標, 頂点2Y座標, 頂点3X座標, 頂点3Y座標, 頂点4X座標, 頂点43Y座標 )</code></p>
        <h4 id='説明-31'>説明</h4>
        <p>四角形を描画する．
        矩形と違い，線と線の間の角度が９０°に固定されていない．</p>
        <h4 id='引数-28'>引数</h4>
        <p>描画する四角形の４つの頂点の座標</p>
        <h4 id='例-21'>例</h4>
        <img src={`${imageFolder}quad_example.png`} alt="quad_example" />
        <CodeBlock>{'size( 200, 200 );\n\nquad( 30, 20, 40, 160, 160, 180, 150, 50 );'}</CodeBlock>
        <h2 id='h19'>演習EX</h2>
        <p>本日まで学んだ命令を自由に使い，自由に画像を生成してみる．</p>
        <ul>
        <li><p>ロゴマーク，紋章，シンボルなど，ひと塊の図が好ましい．</p></li>
        <li><p>画面一面を埋め尽くす文様などは第４回目以降が望ましい．</p></li>

        </ul>
    </div>
  );
};

export default Programming02;
