// Programming00.tsx

import React, { useEffect} from 'react';
import { useLocation } from 'react-router-dom';
import { LinkP, JumpToDefaultScroll, SketchCode } from '../ContentCommon';
import '../ContentCommon.css';

const imageFolder: string = "/musabi-react/images/programming/";

interface ContentProps {
  practiceId: string;
}

const Programming00: React.FC<ContentProps> = ({ practiceId }) => {

  /* URLから初期スクロール位置 */
  const location = useLocation();
  useEffect(() => {
    JumpToDefaultScroll(practiceId, location as any );
    console.log(`Programming00, practiceId; ${practiceId}`);
  }, [practiceId,location]);
  
  return (
    <div className="common-style">
      <h1 id='h0'>情報処理Ⅱのページへようこそ</h1>
      <hr color='#8dacf9' />
      <img src={`${imageFolder}test.jpg`} alt="test" />
      <p>ここが授業で主に使うページです．</p>
      <p>画面右上のアイコンをクリックするとサイドバーが開き，授業回別のページを開けます</p>
      <p><LinkP to="#sample">最初のサンプルへスクロール</LinkP></p>
      <h2 id='h1'>授業の目的</h2>
        <ul>
          <li><p>プログラミングの楽しさを実感する．</p></li>
          <li><p><span>論理的思考力に基づいた造形表現ができる能力を養う．</span></p></li>
          <li><p><span>数と画像の関係を理解する．</span></p></li>
        </ul>
      <h2 id='h2' >授業で学ぶ技術</h2>
      <p>Processingを用いて以下のプログラム技術の基礎を学ぶ</p>
        <ul>
          <li><p><span>演算（算術式など）</span></p></li>
          <li><p><span>制御文（for文，if文）</span></p></li>
          <li><p><span>データ構造（変数，配列）</span></p></li>
          <li><p><span>関数</span></p></li>
          <li><p><span>再帰</span></p></li>
          <li><p><span>クラス（導入のみ）</span></p></li>
        </ul>
      <p>また，論理的に画像・映像を設計する手続き的な造形表現を学ぶ．</p>
      <h2 id='h3'>授業の進め方</h2>
        <ol>
          <li><p><span>Processingを用いた作品制作を通し，プログラミングの基本概念を理解する．</span></p></li>
          <li><p><span>一回の授業内で3～5つ程度に分けた演習をこなす．</span></p></li>
        </ol>
        <h3 id="sample">サンプルコード</h3>
        <SketchCode name="sk_test1"></SketchCode>
        <h3>教材について</h3>
        <ul>
          <li><p><span>Teamsホームページ</span></p>
            <ul>
              <p><span>Teams授業チーム内の「ホームページ」から</span></p>
              <p><span>教材Webページや課題ページなどの授業に必要なリンクがまとめられている．</span></p>
              <p><span>講師への質問チャットもこのページの講師アイコンから</span></p>
            </ul>
          </li>
          <li><p><span>情報処理Ⅱ授業Webページ</span></p>
            <ul>
              <p><span>このページのこと</span></p>
              <p><span>授業で学習する内容がまとめられている．</span></p>
            </ul>
          </li>
          <li><p><span>Processing公式リファレンス（英語）</span></p>
            <p><a href="https://processing.org/reference/" target="_blank">Processing公式リファレンス</a></p>
          </li>
        </ul>
        <h3>授業時間外に発生する作業について</h3>
        <ul>
          <li><p><span>課題提出のため，授業時間外の作業時間が発生する．</span></p></li>
          <li><p><span>他の授業の課題や自主制作時間などを考慮し，各自スケジュールをしっかり組むこと．</span></p></li>
        </ul>
        <h3>課題について</h3>
          <p>Teams授業チーム内の「課題」から提出する．<br></br>詳細は出題時に説明する．</p>
        <h3>授業時間外の質問について</h3>
          <ul>
            <li><p><span>基本的にはチャットを送ってください．</span></p></li>
            <li><p><span>できるだけ早急に対応しますが，遅れる場合もあり．</span></p></li>
          </ul>
        <h3>必要機材</h3>
          <p>授業用のデータを持ち運ぶストレージを用意してください．</p>
          <ul>
            <li><p><span>大学のオンライン個人ストレージまたはUSBメモリなど．</span></p></li>
            <li><p><span>プログラムデータはテキストデータであるため，データ容量は小さめ．</span></p></li>
          </ul>
      <h2 id='h4'>評価について</h2>
        <p>出席数と提出された課題を元に，総合的に判断．</p>
    </div>
  );
};

export default Programming00;
